import { ContextListener } from 'xstate/lib/interpreter';
import { setItem } from './dependencies';

export function persistWatcher<Context>(
  itemsToWatch: Array<keyof Context>,
  initialContext?: Context,
): ContextListener<Context> {
  let previousContext = initialContext ? { ...initialContext } : undefined;
  return (context, prevContext) => {
    if (prevContext) {
      previousContext = { ...prevContext };
    }
    Promise.all([
      ...itemsToWatch.map(async (item) => {
        if (previousContext && context[item] !== previousContext[item]) {
          await setItem(item as string, JSON.stringify(context[item]));
        }
      }),
    ]).catch(() => {
      // ignore the error
    });
  };
}
