import React, { forwardRef } from 'react';
import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';
import { InlineSibling } from './Inline';

const StyledSelect = styled.select`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  color: ${({ theme: { color } }) => color.input};
  width: 100%;
  line-height: 1.5;
  &:disabled {
    background-color: transparent;
    border-bottom: 1px solid ${({ theme: { color } }) => color.borderBlue};
    color: ${({ theme: { color } }) => color.superLightGrey};
  }
  -moz-appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
  background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='40px' height='40px' viewBox='0 0 40 40' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: Sketch 64 (93537) - https://sketch.com --%3E%3Ctitle%3EGroup 5%3C/title%3E%3Cdesc%3ECreated with Sketch.%3C/desc%3E%3Cg id='-Full-page-examples' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='3.--Application-Form---About-You' transform='translate(-612.000000, -1220.000000)'%3E%3Cg id='Group-5' transform='translate(612.000000, 1220.000000)'%3E%3Crect id='Rectangle' x='0' y='0' width='39' height='40'%3E%3C/rect%3E%3Cpath d='M19.3076749,24.7230373 C19.6965184,25.1041802 20.3188314,25.1041802 20.7076749,24.7230373 L28.7076749,16.7230373 C28.8969876,16.5352707 29.0034733,16.2796748 29.0034733,16.0130373 C29.0034733,15.7463998 28.8969876,15.4908039 28.7076749,15.3030373 C28.5262083,15.1096828 28.2728468,15 28.0076749,15 C27.7425031,15 27.4891416,15.1096828 27.3076749,15.3030373 L20.0076749,22.6030373 L12.7076749,15.3030373 C12.5243288,15.1127551 12.2719111,15.0045761 12.0076749,15.0030373 C11.7374078,15.0036994 11.4784515,15.1115978 11.2876749,15.3030373 C10.9041084,15.6986646 10.9041084,16.3274099 11.2876749,16.7230373 L19.3076749,24.7230373 Z' id='-Color' fill='%23${({
    theme,
  }) =>
    theme.color.primary.substring(
      1,
    )}'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: right 0px top 50%;
`;

const StyledSelectError = styled(StyledSelect)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
  color: ${({ theme: { color } }) => color.primary};
`;

type Props = Omit<
  React.SelectHTMLAttributes<HTMLSelectElement>,
  'value' | 'placeholder' | 'onChange' | 'disabled' | 'name'
> & {
  options: string[] | Record<string, string>;
  value: string | undefined;
  placeholder?: string;
  onChange: (value: string) => void;
  name: string;
  disabled?: boolean;
  error?: ValidationError;
};

function Selection(
  {
    options,
    value: initialValue,
    placeholder,
    onChange,
    name,
    children,
    error,
    disabled = false,
    ...props
  }: Props,
  ref?: React.Ref<HTMLSelectElement>,
) {
  const values = Array.isArray(options) ? options : Object.keys(options);
  const Select = error ? StyledSelectError : StyledSelect;
  return (
    <>
      <Select
        {...props}
        ref={ref}
        data-testid={name}
        value={initialValue || ''}
        placeholder={placeholder}
        onChange={(event) => onChange(event.target.value)}
        disabled={disabled}>
        {!!placeholder && <option value="">{placeholder}</option>}
        {values.map((value) => (
          <option key={`${name}${value}`} value={value}>
            {Array.isArray(options) ? value : options[value]}
          </option>
        ))}
      </Select>
      <InlineSibling>{children}</InlineSibling>
    </>
  );
}

export default forwardRef(Selection);
