import { isWithinInterval, parse, isValid } from 'date-fns';

export const isValidDate = ({
  year,
  month,
  day,
}: {
  year: string | undefined;
  month: string | undefined;
  day: string | undefined;
}) => {
  const date = parse(`${day}.${month}.${year}`, 'dd.MM.yyyy', new Date());
  return isValid(date);
};

export const alphanumericDashHyphenRegex = new RegExp(/^[a-zA-Z0-9 '-]*$/i);

export const alphanumericRegex = new RegExp(/^[a-zA-Z0-9 '-]*$/i);

export const isDateWitinRange = ({
  startDate,
  endDate,
  dateInQuestion,
}: {
  startDate: Date;
  endDate: Date;
  dateInQuestion: Date;
}) => isWithinInterval(dateInQuestion, { start: startDate, end: endDate });

export const dateStringIsNotInTheFuture = (dateString: string | undefined) => {
  if (!dateString) return true;
  const dateFromString = new Date(dateString);
  return dateFromString < new Date(Date.now());
};
