import cloneDeep from 'lodash.clonedeep';
import {
  AccommodationCircumstance,
  AddressHistoryEntry,
  AddressHistoryEntryHistoryType,
  GetPersonResponse,
} from 'services/customer';
import { GetApplicationResponse } from 'services/application';
import { AddressResponse } from 'services/address';
import {
  addressResponseToAddress,
  emptyUserAddress,
  findAddressResponse,
  insertExtraAddressIfNeeded,
  UserAddress,
} from './address';
import { OnboardingContext } from './context';
import {
  emptyFunds,
  incomeDetailsToFunds,
  fundsToIncomeDetails,
  initialDepositToFunds,
  fundsToInitialDeposit,
} from './funds';
import {
  otherNameIsMaidenName,
  otherNameIsNotMaidenName,
  otherNameString,
} from './otherName';

const differentString = (s: string) => (t: string) => t !== s;

export const personToContext = (
  person: GetPersonResponse,
  addressHistory: AddressResponse[],
  applicationResponse?: GetApplicationResponse,
): Partial<OnboardingContext> => ({
  applicationId: person.application_id || '',
  productCode: applicationResponse?.product_code || undefined,
  aboutYou: {
    salutation: person.salutation || undefined,
    firstName: person.given_name || '',
    middleNames: person.middle_names || '',
    lastName: person.family_name || '',
    gender: person.gender || undefined,
    DOB: person.date_of_birth || '',
    otherNames:
      person.other_names === null || person.salutation === null
        ? undefined
        : [
            ...(person.maiden_name
              ? [{ name: person.maiden_name, isMaidenName: true }]
              : []),
            ...(person.other_names || [])
              ?.filter(differentString(person.maiden_name || ''))
              .map((name) => ({
                name,
                isMaidenName: false,
              })),
          ],
    countryOfBirth: person.country_of_birth || '',
    taxResidenceData: person.tax_residence_data,
    nationalities:
      person.nationalities && person.nationalities.length > 0
        ? person.nationalities
        : [''],
  },
  contactDetails: {
    primaryTelNumber: person.primary_contact_number || '',
    accommodationCircumstances: person.accommodation_circumstances || undefined,
    addresses: (person.address_history || [])
      .map(addressHistoryToContext(addressHistory))
      .reduce(
        insertExtraAddressIfNeeded,
        (person.address_history || []).length
          ? []
          : [cloneDeep(emptyUserAddress)],
      ),
  },
  finances: {
    employmentStatus: person.employment
      ?.employment_type as OnboardingContext['finances']['employmentStatus'],
    occupation:
      person.employment?.occupation_type_name &&
      person.employment?.occupation_type_code
        ? {
            name: person.employment.occupation_type_name,
            code: person.employment.occupation_type_code,
          }
        : '',
    employmentStarted: person.employment?.company_start_date || undefined,
    companyName: person.employment?.company_name || '',
    companyControl: person.employment?.company_control || undefined,
    industrySector:
      person.employment?.industry_sector_name &&
      person.employment?.industry_sector_code
        ? {
            name: person.employment.industry_sector_name,
            code: person.employment.industry_sector_code,
          }
        : '',
    outgoings:
      person.monthly_outgoings === null ? undefined : person.monthly_outgoings,
    dependents:
      person.financial_dependents === null || person.financial_dependents < 0
        ? undefined
        : person.financial_dependents > 3
        ? 3
        : (person.financial_dependents as 0 | 1 | 2 | 3),
    incomeSources:
      !person.income_details || person.income_details.length === 0
        ? [emptyFunds]
        : person.income_details.map(incomeDetailsToFunds),
  },
  account: {
    purposeOfNewAccount: applicationResponse?.purpose_of_account || undefined,
    purposeOfNewAccountDetail: '', // @TODO - not to/from API?
    incoming: applicationResponse?.monthly_deposit || undefined,
    outgoings: applicationResponse?.monthly_outgoings || undefined,
    largeCash: applicationResponse?.monthly_cash_transaction?.type || undefined,
    largeCashAmount:
      applicationResponse?.monthly_cash_transaction?.amount || undefined,
    initialDeposits:
      !applicationResponse?.purpose_of_account &&
      applicationResponse?.initial_deposits &&
      applicationResponse?.initial_deposits.length === 0
        ? undefined
        : applicationResponse?.initial_deposits?.map(initialDepositToFunds),
  },
});

const toNumber = (s: string) => parseInt(s, 10);

const zeroPad = (n: number) => `${n}`.padStart(2, '0');

const formatDateString = (s: string) =>
  s.split('-').map(toNumber).map(zeroPad).join('-');

export const aboutYouToPersonUpdate = (
  aboutYou: OnboardingContext['aboutYou'],
): Partial<GetPersonResponse> => ({
  salutation: aboutYou.salutation || null,
  given_name: aboutYou.firstName || null,
  middle_names: aboutYou.middleNames || null,
  family_name: aboutYou.lastName || null,
  gender: aboutYou.gender || null,
  date_of_birth: aboutYou.DOB ? formatDateString(aboutYou.DOB) : null,
  tax_residence_data: aboutYou.taxResidenceData,
  nationalities:
    aboutYou.nationalities.length === 1 && aboutYou.nationalities[0] === ''
      ? []
      : aboutYou.nationalities,
  maiden_name: aboutYou.otherNames?.find(otherNameIsMaidenName)?.name || null,
  other_names: aboutYou.otherNames
    ?.filter(otherNameIsNotMaidenName)
    .map(otherNameString),
  country_of_birth: aboutYou.countryOfBirth || null,
});

const addressHistoryToContext = (addressHistory: AddressResponse[]) => (
  address: AddressHistoryEntry,
): UserAddress => {
  const response = addressHistory.find(findAddressResponse(address));
  return {
    selectedAddress: response ? addressResponseToAddress(response) : '',
    movingInDate:
      address.address_history_type ===
      AddressHistoryEntryHistoryType.moreThanThreeYears
        ? 'moreThan3Years'
        : address.move_on
        ? {
            month: parseInt(address.move_on.split('-')[1] || '0', 10) || 0,
            year: parseInt(address.move_on.split('-')[0] || '0', 10) || 0,
          }
        : undefined,
  };
};

const contextToAddressHistory = (
  address: UserAddress,
): AddressHistoryEntry => ({
  address_id:
    typeof address.selectedAddress === 'string'
      ? ''
      : address.selectedAddress.id,
  address_history_type:
    address.movingInDate === 'moreThan3Years'
      ? AddressHistoryEntryHistoryType.moreThanThreeYears
      : AddressHistoryEntryHistoryType.lessThanThreeYears,
  move_on:
    address.movingInDate === 'moreThan3Years' || !address.movingInDate
      ? null
      : `${address.movingInDate.year}-${zeroPad(
          address.movingInDate.month,
        )}-01`,
});

export const contactDetailsToPersonUpdate = (
  contactDetails: OnboardingContext['contactDetails'],
): Partial<GetPersonResponse> => ({
  primary_contact_number: contactDetails.primaryTelNumber,
  accommodation_circumstances: contactDetails.accommodationCircumstances as AccommodationCircumstance,
  address_history: contactDetails.addresses.map(contextToAddressHistory),
});

export const financesToPersonUpdate = (
  finances: OnboardingContext['finances'],
): Partial<GetPersonResponse> => ({
  monthly_outgoings:
    finances.outgoings === undefined ? null : finances.outgoings,
  financial_dependents:
    finances.dependents === undefined ? null : finances.dependents,
  income_details: finances.incomeSources.map(fundsToIncomeDetails),
  employment: {
    employment_type: finances.employmentStatus,
    occupation_type_name:
      typeof finances.occupation === 'string'
        ? undefined
        : finances.occupation.name,
    occupation_type_code:
      typeof finances.occupation === 'string'
        ? undefined
        : finances.occupation.code,
    company_start_date: finances.employmentStarted
      ? formatDateString(finances.employmentStarted)
      : undefined,
    industry_sector_name:
      typeof finances.industrySector === 'string'
        ? undefined
        : finances.industrySector.name,
    industry_sector_code:
      typeof finances.industrySector === 'string'
        ? undefined
        : finances.industrySector.code,
    company_control: finances.companyControl,
    company_name: finances.companyName || undefined,
  },
});

export const accountToApplicationUpdate = (
  productCode: OnboardingContext['productCode'],
  account: OnboardingContext['account'],
): Partial<GetApplicationResponse> => ({
  product_code: productCode === 'SELECT_LATER' ? null : productCode || null,
  purpose_of_account: account.purposeOfNewAccount || null,
  monthly_deposit: account.incoming === undefined ? null : account.incoming,
  monthly_outgoings: account.outgoings === undefined ? null : account.outgoings,
  monthly_cash_transaction: {
    type:
      account.largeCash !== 'YES' &&
      account.largeCash !== 'NO' &&
      account.largeCash !== undefined
        ? account.largeCash
        : null,
    amount: account.largeCashAmount || null,
  },
  initial_deposits: account.initialDeposits?.map(fundsToInitialDeposit),
});

