import styled from 'styled-components';
import { ReactComponent as Arrow } from 'assets/back-arr.svg';
import { useState } from 'react';

const StyledButton = styled.button`
  border: none;
  background: transparent;
  cursor: pointer;
  align-self: flex-start;
  margin: 0;
  padding: 0;
`;

type Props = {
  send: (event: { type: 'BACK' }) => void;
  canTransition: (event: { type: 'BACK' }) => boolean;
};

export default function BackArrow({ send, canTransition }: Props) {
  const [visible] = useState(canTransition({ type: 'BACK' }));
  const onClick = () => send({ type: 'BACK' });
  if (!visible) return <></>;
  const disabled = !canTransition({ type: 'BACK' });
  return (
    <StyledButton type="button" disabled={disabled} onClick={onClick}>
      <Arrow />
    </StyledButton>
  );
}
