import BodyList from 'components/Text/BodyList';
import BodyItem from 'components/Text/BodyItem';
import { Errors } from 'sanstatecharts-onboarding';
import styled from 'styled-components';
import { errorList } from './errorList';
import React, { forwardRef } from 'react';

const StyledBlock = styled.div`
  margin: ${({ theme: { spacing } }) => spacing.s4} -${({ theme: { spacing } }) => spacing.s4half};
  padding: ${({ theme: { spacing } }) => spacing.s4}
    ${({ theme: { spacing } }) => spacing.s4half};
  background: ${({ theme: { color } }) => color.superLightRed};
  color: ${({ theme: { color } }) => color.content};
`;

const PlainButton = styled.button`
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  border: none;
  background: transparent;
  padding: 0;
  text-align: left;
  cursor: pointer;
`;

type Props<Values extends object> = {
  validationErrors: Errors<Values>;
  onScrollToError?: (
    field: keyof Values,
    index?: number,
    itemField?: string,
  ) => void;
};

const ClickError = ({
  children,
  onClick,
}: {
  children: React.ReactNode;
  onClick?: () => void;
}) =>
  onClick ? (
    <PlainButton onClick={onClick}>{children}</PlainButton>
  ) : (
    <>{children}</>
  );

function ValidationErrors<Values extends object>(
  { validationErrors, onScrollToError }: Props<Values>,
  ref?: React.Ref<HTMLDivElement>,
) {
  const errors = errorList(validationErrors);
  if (errors.length === 0) return <></>;
  return (
    <StyledBlock ref={ref} data-testid="validationErrors">
      <p>Please correct the following error{errors.length === 1 ? '' : 's'}:</p>
      <BodyList>
        {errorList(validationErrors).map(
          ({ message, field, index, itemField }) => (
            <BodyItem key={message}>
              <ClickError
                onClick={
                  onScrollToError
                    ? () =>
                        onScrollToError(field as keyof Values, index, itemField)
                    : undefined
                }>
                {message}
              </ClickError>
            </BodyItem>
          ),
        )}
      </BodyList>
    </StyledBlock>
  );
}

export default forwardRef(ValidationErrors);
