import { StrictMode, useMemo } from 'react';
import { Interpreter, State } from 'xstate';
import { ThemeProvider } from 'styled-components';
import { Themes } from 'assets/styles/theme';
import GlobalStyle from 'assets/styles/main';
import {
  OnboardingContext,
  OnboardingEvent,
  OnboardingSchema,
  startOnboardingService,
  stateMatches,
  pcaOnboardingMachine,
  defaultPCAOnboardingContext,
  bcaOnboardingMachine,
  defaultBCAOnboardingContext,
  getItem,
  removeItem,
  PCAOnboardingContext,
  BCAOnboardingEvent,
  BCAOnboardingContext,
  PCAOnboardingEvent,
} from 'sanstatecharts-onboarding';
import StateMachines from 'state-machines/StateMachines';
import PCARouter from './PCARouter';
import BCARouter from './BCARouter';
import { getTokens } from 'services/auth';
import { setAuthHeader } from 'services/client';
import EnvironmentCheck from './EnvironmentCheck';
import PasswordProtect from './PasswordProtect';
import { sendSessionIdToSdk } from 'lib/sdkBridge';

type PCAProps = {
  webview: 'PCA';
  state?: State<PCAOnboardingContext, PCAOnboardingEvent>;
};

type BCAProps = {
  webview: 'BCA';
  state?: State<BCAOnboardingContext, BCAOnboardingEvent>;
};

export default function App({ webview, state }: PCAProps | BCAProps) {
  const service = useMemo(() => {
    const { accessToken } = getTokens();
    if (accessToken) {
      setAuthHeader(accessToken);
    }
    const auth = { initialUrl: window.location.search };
    const emailAddress = getItem('emailAddress') || '';
    const service = (webview === 'PCA'
      ? startOnboardingService(
          pcaOnboardingMachine,
          defaultPCAOnboardingContext,
          { auth, emailAddress },
          ['auth', 'emailAddress'],
          state as State<PCAOnboardingContext, PCAOnboardingEvent>,
        )
      : startOnboardingService(
          bcaOnboardingMachine,
          defaultBCAOnboardingContext,
          { auth, emailAddress },
          ['auth', 'businessType', 'emailAddress'],
          state as State<BCAOnboardingContext, BCAOnboardingEvent>,
        )) as Interpreter<OnboardingContext, OnboardingSchema, OnboardingEvent>;
    service.subscribe((state) => {
      if (stateMatches(state, 'magicLinkIsValid')) {
        window.history.replaceState({}, document.title, '/');
        removeItem('emailAddress');
      }
      if (state.done) {
        sendSessionIdToSdk(state.context.sessionId);
      }
    });
    return service;
  }, [webview, state]);
  const Router = webview === 'PCA' ? PCARouter : BCARouter;
  return (
    <EnvironmentCheck>
      <ThemeProvider theme={Themes.SANUK}>
        <GlobalStyle />
        <PasswordProtect>
          <StateMachines rootService={service}>
            <StrictMode>
              <Router />
            </StrictMode>
          </StateMachines>
        </PasswordProtect>
      </ThemeProvider>
    </EnvironmentCheck>
  );
}
