import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import CountryInput from 'components/Form/CountryInput';
import { findError } from 'components/ValidationErrors/findError';
import FormLink from 'components/Form/FormLink';
import { countriesData } from 'lib/countries';

type Values = Pick<OnboardingContext['aboutYou'], 'nationalities'>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  aboutYou: Values;
};

export default function NationalityFields({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_NATIONALITY',
    index: 0,
    value: 'NATIONALITY',
  });
  return (
    <>
      <Label
        error={shownErrors['nationalities']}
        ref={refFor('nationalities', 0)}>
        Nationality
        {aboutYou.nationalities.map((nationality, index) => (
          <CountryInput
            key={`nationality${index}`}
            name={`nationality${index}`}
            field="nationality"
            placeholder={
              index === aboutYou.nationalities.length - 1
                ? 'Please select...'
                : undefined
            }
            showFlags={true}
            dataSource={countriesData}
            onChange={(value: string) =>
              send({
                type: 'UPDATE_NATIONALITY',
                index,
                value,
              })
            }
            disabled={disabled}
            value={nationality}
            error={findError(shownErrors, 'nationalities', index)}
            ref={index !== 0 ? refFor('nationalities', index) : undefined}
            filterOut={aboutYou.nationalities}>
            {canTransition({ type: 'REMOVE_NATIONALITY', index }) && (
              <FormLink
                id={`removeNationality${index}`}
                inline
                onClick={() => send({ type: 'REMOVE_NATIONALITY', index })}>
                Remove
              </FormLink>
            )}
          </CountryInput>
        ))}
      </Label>
      {canTransition({ type: 'ADD_NATIONALITY' }) && (
        <FormLink
          id="addNationality"
          onClick={() => send({ type: 'ADD_NATIONALITY' })}>
          Add another nationality
        </FormLink>
      )}
    </>
  );
}
