/* eslint-disable camelcase */
import { AxiosResponse } from 'axios';
import { withRefresh } from 'services/auth';
import {
  IncomeDetailsSourceCodes,
  MonthlyDepositSum,
  PCAProductCode,
  PurposeOfAccount,
} from 'services/customer';

import { client } from '../client';

export interface UpdateApplicationItem {
  op: 'replace';
  path: string;
  value: keyof GetApplicationResponse;
}
export type UpdateApplicationPayload = UpdateApplicationItem[];

export type UpdateApplicationResponse = Record<string, never>;

export type MonthlyCashTransaction = {
  type: MonthlyDepositSum | null;
  amount: number | null;
};

export type InitialDepositItem = {
  source: IncomeDetailsSourceCodes | null;
  amount: number | null;
  origin: string | null;
};

export type GetApplicationResponse = {
  application_id: string | null;
  product_code: PCAProductCode | null;
  purpose_of_account: PurposeOfAccount | null;
  monthly_deposit: number | null;
  monthly_outgoings: number | null;
  monthly_cash_transaction: MonthlyCashTransaction | null;
  initial_deposits: InitialDepositItem[];
};

export type GetApplicationTransformed = {
  applicationId: string | '';
  productCode: PCAProductCode | '';
  purposeOfAccount: PurposeOfAccount | null;
  monthlyDeposit: number | null;
  monthlyOutgoings: number | null;
  monthlyCashTransaction: MonthlyCashTransaction | null;
  initialDeposits: InitialDepositItem[];
};

// casting to Formik-safe values if falsy
// ' ' === value displayed in form, but not mandatory
// '' === value displayed in form, and mandatory
export const camelCaseApplicationDataForStore = (
  data: GetApplicationResponse,
): GetApplicationTransformed => ({
  applicationId: data.application_id || '',
  productCode: data.product_code || '',
  purposeOfAccount: data.purpose_of_account || null,
  monthlyDeposit: data.monthly_deposit || null,
  monthlyOutgoings: data.monthly_outgoings || null,
  monthlyCashTransaction: data.monthly_cash_transaction || null,
  initialDeposits: data.initial_deposits || [],
});

export const getApplication = withRefresh(
  (applicationId: string): Promise<AxiosResponse<GetApplicationResponse>> =>
    client.get(`applications/${applicationId}`),
);

export const updateApplication = withRefresh(
  ({
    applicationId,
    updatePayload,
  }: {
    applicationId: string;
    updatePayload: Partial<GetApplicationResponse>;
  }): Promise<AxiosResponse<UpdateApplicationResponse>> => {
    // https://tools.ietf.org/html/rfc6902#section-4.3
    const patchDataSet = Object.keys(updatePayload).reduce(
      (patchDataSetAcc, patchDataSetProp) => {
        patchDataSetAcc.push({
          op: 'replace',
          path: `/${[patchDataSetProp]}`,
          value: updatePayload[
            patchDataSetProp as keyof typeof updatePayload
          ] as keyof GetApplicationResponse,
        });
        return patchDataSetAcc;
      },
      [] as UpdateApplicationPayload,
    );

    return client.patch(`applications/${applicationId}`, patchDataSet);
  },
);
