import React from 'react';
import styled from 'styled-components';

type Props = Omit<
  React.ButtonHTMLAttributes<HTMLButtonElement>,
  'onClick' | 'children' | 'id'
> & {
  children: string;
  inline?: boolean;
  onClick: () => void;
  id: string;
};

const StyledButton = styled.button`
  border: none;
  background: transparent;
  cursor: pointer;
  margin: 0;
  padding: 0;
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s3};
  text-align: left;
  color: ${({ theme: { color } }) => color.teal};
  text-decoration: underline;
  margin-top: -${({ theme: { spacing } }) => spacing.s3};
  &:last-child {
    margin-bottom: 0;
  }
`;

const StyledButtonInline = styled(StyledButton)`
  color: ${({ theme: { color } }) => color.primary};
  margin: 0;
  display: block;
`;

const StyledContainer = styled.div`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
`;

export default function FormLink({
  children: title,
  id,
  onClick,
  inline,
  ...props
}: Props) {
  const Container = inline ? React.Fragment : StyledContainer;
  const Component = inline ? StyledButtonInline : StyledButton;
  return (
    <Container>
      <Component
        type="button"
        onClick={onClick}
        id={id}
        data-testid={id}
        {...props}>
        {title}
      </Component>
    </Container>
  );
}
