import { array, object, string, boolean } from 'yup';
import { OptionalObjectSchema, TypeOfShape } from 'yup/lib/object';
import { subYears } from 'date-fns';
import { validate } from 'statelib/validate';
import {
  alphanumericRegex,
  alphanumericDashHyphenRegex,
  isDateWitinRange,
  isValidDate,
} from './validationHelpers';

const countryOfBirthValidationSchema = object({
  countryOfBirth: string()
    .required('country of birth should be specified')
    .label('countryOfBirth'),
});

const nationalityValidationSchema = string()
  .trim()
  .required('nationality should be specified')
  .label('nationality');

export const otherNameValidationSchema = object({
  name: string().trim().min(2).max(50).label('other name'),
  isMaidenName: boolean().defined(),
});

const optionalTaxIdentificationNumberCountries = [
  'AU', 'JP', 'AF', 'AX', 'DZ', 'AO', 'AQ', 'AM', 'AZ', 'BD', 'BY', 'BJ', 'BT', 
  'BO', 'BQ', 'BA', 'BW', 'BV', 'XD', 'IO', 'BF', 'BI', 'KH', 'CM', 'XE', 'CV', 'CF', 
  'TD', 'CX', 'CC', 'KM', 'CG', 'CD', 'CI', 'CU', 'DJ', 'DO', 'XC', 'TL', 'EC', 'EG', 
  'SV', 'GQ', 'ER', 'ET', 'FK', 'FJ', 'GF', 'PF', 'TF', 'GA', 'GM', 'GE', 'GP', 
  'GT', 'GN', 'GW', 'GY', 'HT', 'HM', 'VA', 'HN', 'IR', 'IQ', 'JM', 'JO', 'KZ', 'KE', 
  'KI', 'KP', 'QZ', 'KG', 'LA', 'LS', 'LR', 'LY', 'MK', 'MG', 'MW', 'MV', 'ML', 'MQ', 
  'MR', 'YT', 'FM', 'MD', 'MN', 'ME', 'MA', 'MZ', 'MM', 'NA', 'NP', 'AN', 'NC', 'NI', 
  'NE', 'NG', 'NF', 'OM', 'PK', 'PW', 'PS', 'PG', 'PY', 'PE', 'PH', 'PN', 'RS', 
  'RE', 'RW', 'SH', 'PM', 'ST', 'SN', 'CS', 'SL', 'SB', 'SO', 'SS', 'LK', 'GS', 'SD', 
  'SR', 'SJ', 'SZ', 'SY', 'TW', 'TJ', 'TZ', 'TH', 'TG', 'TK', 'TO', 'TN', 'TM', 'TV', 
  'UG', 'UA', 'UZ', 'VE', 'VN', 'WF', 'EH', 'YE', 'ZR', 'ZM', 'ZW', 'BL', 'MF'
]

const uniqueEntries = <T>(value?: T[]) =>
  !value || value.every((v, i) => value.indexOf(v) === i);

const personalValidationSchema = object({
  salutation: string()
    .defined()
    .trim()
    .required('title must be specified')
    .label('title'),
  firstName: string()
    .trim()
    .matches(
      alphanumericDashHyphenRegex,
      "first name can only contain the special characters ' and -",
    )
    .min(2, 'first name must be at least 2 characters')
    .max(50, 'first name cannot be more than 50 characters')
    .required('first name must be specified')
    .label('first name'),
  middleNames: string()
    .trim()
    .test(
      'must-be-more-than-2',
      'if entered, middle names must be more than 2 characters',
      (middleNames) => !middleNames || middleNames.length >= 2,
    )
    .matches(
      alphanumericDashHyphenRegex,
      "middle name can only contain the special characters ' and -",
    )
    .max(100, 'middle name cannot be more than 100 characters')
    .label('middle name'),
  lastName: string()
    .trim()
    .matches(
      alphanumericDashHyphenRegex,
      "last name can only contain the special characters ' and -",
    )
    .min(2, 'last name must be at least 2 characters')
    .max(50, 'last name cannot be more than 50 characters')
    .required('last name must be specified')
    .label('last name'),
});

type ShapeFromSchema<T> = T extends OptionalObjectSchema<infer Shape>
  ? TypeOfShape<Shape>
  : never;

type OtherNames = undefined | ShapeFromSchema<typeof otherNameValidationSchema>;

const otherNamesNotMaidenName = (o: OtherNames) => o && !o.isMaidenName;

const fiveOtherNamesMaximum = (otherNames?: Array<OtherNames>) =>
  !otherNames || otherNames.filter(otherNamesNotMaidenName).length <= 5;

const additionalValidationSchema = object({
  otherNames: array()
    .defined()
    .max(6)
    .of(otherNameValidationSchema)
    .test(fiveOtherNamesMaximum)
    .label('other names'),
  DOB: string()
    .trim()
    .required()
    .test('Invalid date', 'You must enter a valid date', (s) => {
      const [yyyy, mm, dd] = s?.split('-') || [''];

      return isValidDate({ year: yyyy, month: mm, day: dd });
    })
    .test('Outside year params', 'You must be between 13 and 120', (s) => {
      const [yyyy, mm, dd] = s?.split('-') || [''];

      const dob = new Date(Number(yyyy), Number(mm), Number(dd));

      const date120yearsInPast = subYears(new Date(), 120);
      const date13yearsInPast = subYears(new Date(), 13);

      return isDateWitinRange({
        startDate: date120yearsInPast,
        dateInQuestion: dob,
        endDate: date13yearsInPast,
      });
    })
    .label('date of birh'),
});

const nationalitiesValidationSchema = object({
  nationalities: array()
    .min(1)
    .max(3)
    .of(nationalityValidationSchema)
    .test('unique', 'You must select unique nationalities', uniqueEntries)
    .label('nationalities'),
});

export const taxResidenceEntryValidationSchema = object({
  country_of_tax_residence: string().required('tax residency country should be specified or removed').label('country of tax residence'),
  tax_identification_number: string().nullable().when('country_of_tax_residence', {
    is: (countryCode: string) => optionalTaxIdentificationNumberCountries.includes(countryCode) || countryCode === 'GB',
    then: string().nullable().trim().min(1).max(20).matches(
      alphanumericRegex,
      'tax identifiaction number can only contain alphanumeric characters',
    ).label('tax ID Number'),
    otherwise: string().nullable().required().trim().min(1).max(20).matches(
      alphanumericRegex,
      'tax identifiaction number can only contain alphanumeric characters',
    ).label('tax ID Number')
  }),
});

const taxResidenceDataSchema = object({
  taxResidenceData: array()
    .min(1, 'you must select a tax status option')
    .max(5)
    .of(taxResidenceEntryValidationSchema)
});

export const aboutYouPersonalValidation = validate(personalValidationSchema);

export const aboutYouAdditionalValidation = validate(
  additionalValidationSchema,
);

export const nationalitiesValidation = validate(nationalitiesValidationSchema);

export const taxStatusValidation = validate(taxResidenceDataSchema);

export const aboutYouValidation = validate(
  taxResidenceDataSchema
    .concat(countryOfBirthValidationSchema)
    .concat(nationalitiesValidationSchema)
    .concat(
      object({
        gender: string().trim().defined(),
      }),
    )
    .concat(additionalValidationSchema)
    .concat(personalValidationSchema),
);
