import { useCallback, useState } from 'react';

export function useUncontrolledInput<T>(
  value: T,
  parentChange: (s: T) => void,
  controlled: boolean = false,
) {
  const [localValue, setLocalValue] = useState(value);
  const onBlur = useCallback(() => {
    localValue !== value && !controlled && parentChange(localValue);
  }, [localValue, parentChange, value, controlled]);
  const onChange = useCallback(
    (event: React.ChangeEvent<any>) =>
      controlled
        ? parentChange(event.target.value)
        : setLocalValue(event.target.value),
    [controlled, parentChange],
  );
  return {
    onBlur,
    onChange,
    value: controlled ? value : undefined,
    defaultValue: controlled ? undefined : value,
  };
}
