import { Subscription, State, Interpreter } from 'xstate';
import { isService } from './service';

type Interpreters = { [serviceID: string]: Interpreter<any> };

function allInterpreters(service: Interpreter<any>): Interpreters {
  return Array.from(service.children.values()).reduce(
    (all, childService) => {
      if (!isService(childService)) {
        return all;
      }
      return {
        ...all,
        [childService.id]: childService,
        ...allInterpreters(childService),
      };
    },
    {
      [service.id]: service,
    } as Interpreters,
  );
}

type InterpreterSubscriptions = {
  [id: string]: {
    sessionId: string;
    subscription: Subscription;
  };
};

const addIfNotAPartOf = (
  groupA: Interpreters | InterpreterSubscriptions,
  groupB: Interpreters | InterpreterSubscriptions,
) => (a: string[], id: string) => {
  if (
    !groupA[id] ||
    !groupB[id] ||
    groupA[id]!.sessionId !== groupB[id]!.sessionId
  ) {
    a.push(id);
  }
  return a;
};

export type LogFunction = (serviceID: string, state: State<any>) => void;

export function xstateLog(
  service: Interpreter<any, any, any, any>,
  loggingFunction: LogFunction,
) {
  const subscriptions: InterpreterSubscriptions = {};

  function unsubscribe(id: string) {
    if (subscriptions[id]) {
      subscriptions[id]!.subscription.unsubscribe();
      delete subscriptions[id];
    }
  }

  function addSubscriptions() {
    const interpreters = allInterpreters(service);
    Object.keys(interpreters)
      .reduce(addIfNotAPartOf(subscriptions, interpreters), [])
      .map((id) => interpreters[id]!)
      .forEach((interpreter) => {
        const { id } = interpreter;
        subscriptions[id] = {
          sessionId: interpreter.sessionId,
          subscription: interpreter.subscribe((state) => {
            loggingFunction(id, state);
            if (subscriptions[id]) {
              addSubscriptions();
            }
          }),
        };
      });
    Object.keys(subscriptions)
      .reduce(addIfNotAPartOf(interpreters, subscriptions), [])
      .forEach(unsubscribe);
  }

  addSubscriptions();
}

const stateToConsole = (excludeEvents: string[]) => (
  logging: 'log' | 'warn',
): LogFunction => (serviceID, state) =>
  !excludeEvents.includes(state.event.type) &&
  // eslint-disable-next-line no-console
  console[logging](serviceID, state.value, state.event, state.context);

export function logToConsole(service: Interpreter<any, any, any, any>) {
  xstateLog(service, stateToConsole(['ACTIVE'])('log'));
}
