import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';
import { InlineSibling } from './Inline';

const StyledList = styled.ul`
  margin: 0;
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  line-height: 1.5;
  width: 100%;
  padding: 0;
  background-color: ${({ theme: { color } }) => color.white};
`;

const StyledItem = styled.li`
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
`;

const InlineItem = styled.div`
  padding: ${({ theme: { spacing } }) => `${spacing.s3} ${spacing.s4}`};
`;

const StyledButton = styled.button`
  border: none;
  background: transparent;
  cursor: pointer;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: left;
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  padding: ${({ theme: { spacing } }) => `${spacing.s3} ${spacing.s4}`};
  height: auto;
  transition: background-color 0.15s ease-in-out; border-color 0.15s ease-in-out; border-width 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
`;

type Props = {
  options: string[] | Record<string, string>;
  onChange: (value: string) => void;
  onCancel: () => void;
  name: string;
  disabled?: boolean;
  children?: React.ReactNode;
  error?: ValidationError;
};

export default function SelectionList({
  options,
  onChange,
  onCancel,
  name,
  children,
  error,
  disabled = false,
}: Props) {
  const values = Array.isArray(options) ? options : Object.keys(options);
  const [visible, setVisible] = useState(!disabled);
  useEffect(() => {
    function escapeListener(event: KeyboardEvent) {
      if (event.key === 'Escape') {
        setVisible(false);
        onCancel();
      }
    }
    document.addEventListener('keydown', escapeListener, false);
    return () => document.removeEventListener('keydown', escapeListener);
  }, [onCancel]);
  return visible ? (
    <StyledList>
      {values.map((value) => (
        <StyledItem key={`${name}${value}`}>
          <StyledButton
            type="button"
            data-testid={`${name}${value}`}
            onClick={(event: React.MouseEvent) => {
              event.preventDefault();
              setVisible(false);
              onChange(value);
            }}
            onKeyUp={({ key }) => {
              if (key === 'Enter' || key === 'Space') {
                setVisible(false);
                onChange(value);
              }
            }}>
            {Array.isArray(options) ? value : options[value]}
          </StyledButton>
        </StyledItem>
      ))}
      {!!children && <InlineItem>{children}</InlineItem>}
    </StyledList>
  ) : !!children ? (
    <InlineSibling>{children}</InlineSibling>
  ) : (
    <React.Fragment />
  );
}
