import BodyText from 'components/Text/BodyText';
import React from 'react';
import styled from 'styled-components';

const StyledInput = styled.input`
  display: none;
  + span + span {
    display: none;
  }
  + span + span + p {
    display: none;
  }
  &:checked + span {
    display: none;
  }
  &:checked + span + span {
    display: block;
  }
  &:checked + span + span + p {
    display: block;
    margin-top: ${({ theme: { spacing } }) => spacing.s4};
  }
`;
const StyledLabel = styled.label`
  cursor: pointer;
  display: block;
  padding-bottom: ${({ theme: { spacing } }) => spacing.s4};
`;

const StyledSpan = styled.span`
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  bottom: ${({ theme: { spacing } }) => spacing.s4};
  color: ${({ theme: { color } }) => color.primary};
  &:before {
    content: '+';
    margin-right: ${({ theme: { spacing } }) => spacing.s3};
    width: ${({ theme: { spacing } }) => spacing.s4};
    height: ${({ theme: { spacing } }) => spacing.s4};
    line-height: 0.675rem;
    display: inline-block;
    text-align: center;
    border-radius: ${({ theme: { spacing } }) => spacing.s3};
    border: 1px solid ${({ theme: { color } }) => color.primary};
  }
`;

const StyledSpanMinus = styled(StyledSpan)`
  &:before {
    content: '-';
  }
`;

export default function MoreInfo({ children }: { children: React.ReactNode }) {
  return (
    <StyledLabel>
      <StyledInput type="checkbox" />
      <StyledSpan>View more</StyledSpan>
      <StyledSpanMinus>View less</StyledSpanMinus>
      <BodyText>{children}</BodyText>
    </StyledLabel>
  );
}
