import {
  BCAOnboardingContext,
  businessTypeValidation,
} from 'sanstatecharts-onboarding';
import BusinessLogo from 'components/BusinessLogo';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import Page from 'components/Page/Page';
import { ReactComponent as Icon } from 'assets/walking-figure.svg';
import { useBCAOnboarding } from 'lib/useBCAOnboarding';
import Form from 'components/Form/Form';
import Label from 'components/Form/Label';
import ContinueButton from 'components/Form/ContinueButton';
import { useValidation } from 'lib/useValidation';
import Selection from 'components/Form/Selection';

export default function SelectBusinessType() {
  const [state, send, canTransition] = useBCAOnboarding();
  const { businessType } = state.context;
  const validationErrors = businessTypeValidation({ businessType });
  const { shownErrors, refFor, ...validation } = useValidation(
    validationErrors,
  );
  return (
    <Page>
      <BusinessLogo />
      <CenterBlockSVG>
        <Icon />
      </CenterBlockSVG>
      <Form>
        <Label>
          Business Type
          <Selection
            name="businessType"
            placeholder="Please select..."
            options={{
              CORPORATION: 'Corporation',
              PARTNERSHIP: 'Partnership',
              SOLE_PROPRIETORSHIP: 'Sole proprietorship',
              LIMITED_LIABILITY_COMPANY: 'Limited liability company',
            }}
            onChange={(value) =>
              send({
                type: 'UPDATE_BUSINESS_TYPE',
                businessType:
                  (value as BCAOnboardingContext['businessType']) || undefined,
              })
            }
            disabled={
              !canTransition({
                type: 'UPDATE_BUSINESS_TYPE',
                businessType: 'CORPORATION',
              })
            }
            value={businessType}
            error={shownErrors['businessType']}
          />
        </Label>
        <ContinueButton
          validationErrors={validationErrors}
          {...validation}
          status="editing"
          hideErrorBlock
        />
      </Form>
    </Page>
  );
}
