import { Machine } from 'xstate';
import { PurposeOfAccount, updatePerson } from 'services/customer';
import { createAddress } from 'services/address';
import { updateApplication } from 'services/application';
import { noErrors } from 'statelib/validate';
import { getAccessToken, startIdAndV } from 'services/idandv';
import { addressResponseToAddress, UserAddress } from './address';
import {
  aboutYouToPersonUpdate,
  accountToApplicationUpdate,
  contactDetailsToPersonUpdate,
  financesToPersonUpdate,
  overdraftToPersonUpdate,
} from './person';
import { OnboardingContext } from './context';
import { magicLinkMachine } from './magicLink.machine';
import { userAddressValidationSchema } from './validation/contactDetailsValidation';

const addressWithIdPromise = (userId: string) => (
  address: UserAddress,
): Promise<UserAddress> => {
  if (
    typeof address.selectedAddress === 'string' ||
    address.selectedAddress.id ||
    !noErrors(userAddressValidationSchema, address)
  ) {
    return Promise.resolve(address);
  }
  return createAddress({
    address: {
      address_line_1: address.selectedAddress.addressLineOne,
      address_line_2: address.selectedAddress.addressLineTwo,
      address_line_3: address.selectedAddress.addressLineThree,
      city: address.selectedAddress.city,
      postal_code: address.selectedAddress.postcode,
      country_code: address.selectedAddress.country,
      district_or_county: '',
      state_or_region: '',
    },
    addressableId: userId,
    userId,
    addressableType: 'CUSTOMER',
    type: 'RESIDENTIAL',
  }).then(({ data }) => ({
    ...address,
    selectedAddress: addressResponseToAddress(data),
  }));
};

async function refreshAfterUpdate<Update, Context extends OnboardingContext>(
  retrieveUser: (auth: Pick<Context, 'auth'>) => Promise<Partial<Context>>,
  auth: Pick<Context, 'auth'>['auth'],
  update: Promise<Update>,
) {
  const result = await update;
  const refreshed = await retrieveUser({ auth });
  return [refreshed, result] as [Partial<Context>, Update];
}

async function updateContactDetails({
  auth,
  contactDetails,
}: Pick<OnboardingContext, 'auth' | 'contactDetails'>) {
  const updatedAddresses = await Promise.all(
    contactDetails.addresses.map(addressWithIdPromise(auth.userId)),
  );
  const { status } = await updatePerson({
    userId: auth.userId,
    updatePayload: contactDetailsToPersonUpdate({
      ...contactDetails,
      addresses: updatedAddresses,
    }),
  });
  if (status !== 204) {
    throw new Error(`person update response status ${status}`);
  }
  return updatedAddresses;
}

export const services = <Context extends OnboardingContext>(
  retrieveUser: (auth: Pick<Context, 'auth'>) => Promise<Partial<Context>>,
) => ({
  magicLink: ({ auth, emailAddress }: Context) =>
    Machine(magicLinkMachine).withContext({
      auth,
      emailAddress,
    }),
  updatePersonAboutYou: async ({ auth, aboutYou }: Context) =>
    refreshAfterUpdate(
      retrieveUser,
      auth,
      updatePerson({
        userId: auth.userId,
        updatePayload: aboutYouToPersonUpdate(aboutYou),
      }).then(({ status }) => {
        if (status !== 204) {
          throw new Error(`person update response status ${status}`);
        }
      }),
    ),
  updatePersonContactDetails: async ({ auth, contactDetails }: Context) =>
    refreshAfterUpdate(
      retrieveUser,
      auth,
      updateContactDetails({ auth, contactDetails }),
    ),
  updatePersonFinances: async ({ auth, finances }: Context) =>
    refreshAfterUpdate(
      retrieveUser,
      auth,
      updatePerson({
        userId: auth.userId,
        updatePayload: financesToPersonUpdate(finances),
      }).then(({ status }) => {
        if (status !== 204) {
          throw new Error(`person update response status ${status}`);
        }
      }),
    ),
  updatePersonAccount: async ({
    auth,
    applicationId,
    productCode,
    account,
    finances,
  }: Context) => {
    let accountPayload = account;

    if (account.purposeOfNewAccount === PurposeOfAccount.mainBankAccount) {
      accountPayload = {
        // standard account context props
        ...account,
        // modified
        outgoings: finances.outgoings,
        incoming: finances.incomeSources.reduce(
          (prevVal, currVal) => prevVal + (currVal?.amount || 0),
          0,
        ),
      };
    }

    return refreshAfterUpdate(
      retrieveUser,
      auth,
      updateApplication({
        applicationId,
        updatePayload: accountToApplicationUpdate(productCode, accountPayload),
      }).then(({ status }) => {
        if (status !== 204) {
          throw new Error(`person update response status ${status}`);
        }
      }),
    );
  },
  updatePersonOverdraft: async ({ auth, overdraft }: Context) =>
    refreshAfterUpdate(
      retrieveUser,
      auth,
      updatePerson({
        userId: auth.userId,
        updatePayload: overdraftToPersonUpdate(overdraft),
      }).then(({ status }) => {
        if (status !== 204) {
          throw new Error(`person update response status ${status}`);
        }
      }),
    ),
  fetchSessionId: async (_context: Context) =>
    getAccessToken().then((res1) => {
      if (res1.status !== 200) {
        throw new Error(
          `Get access token for ID&V response status ${res1.status}`,
        );
      }
      return startIdAndV(res1.data).then((res2) => {
        if (res2.status !== 201) {
          throw new Error(
            `Get sessionId for ID&V response status ${res2.status}`,
          );
        }
        return res2.data.sessionId;
      });
    }),
});
