import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';
import { useCallback } from 'react';
import { useUncontrolledInput } from 'lib/useUncontrolledInput';

type InputProps = Omit<
  React.InputHTMLAttributes<HTMLInputElement>,
  'onChange' | 'value' | 'id'
> & {
  onChange: (s: string) => void;
  id: string;
  value: string;
  error?: ValidationError;
};

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
`;

const StyledLabel = styled.label`
  display: flex;
  flex-direction: column;
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  text-align: center;
  + label {
    margin-left: ${({ theme: { spacing } }) => spacing.s4};
  }
`;

const StyledInput = styled.input`
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  color: ${({ theme: { color } }) => color.input};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s2};
  padding: 0.5rem 0;
  text-align: center;
  &:disabled {
    background-color: transparent;
    border-bottom: 1px solid ${({ theme: { color } }) => color.borderBlue};
    color: ${({ theme: { color } }) => color.superLightGrey};
  }

  &::-webkit-outer-spin-button,
  ::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
`;

const StyledInput2 = styled(StyledInput)`
  width: 3rem;
`;

const StyledInput4 = styled(StyledInput)`
  width: 6rem;
`;

const StyledInput2Error = styled(StyledInput2)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
`;

const StyledInput4Error = styled(StyledInput4)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
`;

export default function DateInput({
  onChange,
  value,
  error,
  id,
  ...props
}: InputProps) {
  const values = value.split('-');
  const Input2 = error ? StyledInput2Error : StyledInput2;
  const Input4 = error ? StyledInput4Error : StyledInput4;
  const dayChange = useCallback(
    (value: string) => onChange([values[0], values[1], value].join('-')),
    [values, onChange],
  );
  const monthChange = useCallback(
    (value: string) => onChange([values[0], value, values[2]].join('-')),
    [values, onChange],
  );
  const yearChange = useCallback(
    (value: string) => onChange([value, values[1], values[2]].join('-')),
    [values, onChange],
  );
  const inputPropsDay = useUncontrolledInput(values[2] || '', dayChange);
  const inputPropsMonth = useUncontrolledInput(values[1] || '', monthChange);
  const inputPropsYear = useUncontrolledInput(values[0] || '', yearChange);

  const onInput = useCallback((event: React.FormEvent<HTMLInputElement>) => {
    const target = event.target as HTMLInputElement;
    target.value = target.value.slice(0, target.maxLength);
  }, []);

  return (
    <Container>
      <StyledLabel>
        <Input2
          {...props}
          {...inputPropsDay}
          onInput={onInput}
          min={1}
          max={31}
          maxLength={2}
          id={`${id}DD`}
          data-testid={`${id}DD`}
          type="number"
          placeholder="DD"
          inputMode="numeric"
          pattern="[0-9]*"
        />
        DD
      </StyledLabel>
      <StyledLabel>
        <Input2
          {...props}
          {...inputPropsMonth}
          onInput={onInput}
          maxLength={2}
          min={1}
          max={12}
          id={`${id}MM`}
          data-testid={`${id}MM`}
          type="number"
          placeholder="MM"
          inputMode="numeric"
          pattern="[0-9]*"
        />
        MM
      </StyledLabel>
      <StyledLabel>
        <Input4
          {...props}
          {...inputPropsYear}
          onInput={onInput}
          maxLength={4}
          type="number"
          id={`${id}YYYY`}
          data-testid={`${id}YYYY`}
          placeholder="YYYY"
          inputMode="numeric"
          pattern="[0-9]*"
        />
        YYYY
      </StyledLabel>
    </Container>
  );
}
