import { useEffect, useCallback } from 'react';
import { EventObject, Interpreter } from 'xstate';
import useRerender from './useRerender';

export default function useXstate<Context, Schema, Event extends EventObject>(
  service: Interpreter<Context, Schema, Event>,
) {
  const rerender = useRerender();
  useEffect(
    () =>
      service.subscribe(() => {
        if (service.state.changed) {
          rerender();
        }
      }).unsubscribe,
    [rerender, service],
  );
  const { state, send } = service;
  const canTransition = useCallback(
    (event: Event) => !!service.nextState(event).changed,
    [service],
  );
  const nextState = useCallback((event: Event) => service.nextState(event), [
    service,
  ]);
  return [state, send, canTransition, nextState] as [
    typeof state,
    typeof send,
    typeof canTransition,
    typeof nextState,
  ];
}
