import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import DateInput from 'components/Form/DateInput';

type Values = Pick<OnboardingContext['aboutYou'], 'DOB'>;
type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  aboutYou: Values;
};

export default function DOBField({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_ABOUT_YOU',
    field: 'DOB',
    value: '2000-01-01',
  });
  return (
    <Label error={shownErrors['DOB']} ref={refFor('DOB')}>
      Date of birth
      <DateInput
        id="DOB"
        onChange={(value) =>
          send({ type: 'UPDATE_ABOUT_YOU', field: 'DOB', value })
        }
        disabled={disabled}
        value={aboutYou.DOB}
        error={shownErrors['DOB']}
      />
    </Label>
  );
}
