import React, { MutableRefObject } from 'react';
import { Funds, ValidationError } from 'sanstatecharts-onboarding';
import Label from './Label';
import Selection from './Selection';
import MoneyInput from './MoneyInput';
import Radios from './Radios';
import { countriesData, countriesOfBirthAndIncome } from 'lib/countries';
import CountryInput from './CountryInput';
import StandardInput from './StandardInput';

type Props = {
  onChange: (a: Funds) => void;
  name: string;
  value: Funds;
  disabled?: boolean;
  children?: React.ReactNode;
  error: (key: keyof Funds) => ValidationError | undefined;
  refFor: (key?: keyof Funds) => MutableRefObject<any>;
  title: string;
  amountTitle: string;
  originTitle: string;
};

type NonObject<T> = T extends object ? never : T;

const typeOptions: {
  [key in NonNullable<NonObject<Funds['type']>>]: string;
} = {
  EMPLOYMENT: 'Employment or Benefits',
  RETIREMENT: 'Retirement',
  PROPERTY: 'Property',
  INHERITENCE: 'Inheritence',
  STOCKS_ETC: 'Stocks, Shares, Bonds, Managed Investments or Debentures',
  SALE_DIVESTMENT_ASSETS: 'Sale/Divestment of assets',
  LEGAL_SETTLEMENT: 'Legal Settlement',
  VIRTUAL_CURRENCIES: 'Ownership/sale virtual currencies',
  GIFT: 'Gift',
  ANOTHER_PERSON_ENTITY: 'Another person/entity',
  SAVINGS: 'Savings',
  STUDENT: 'Student',
};

export default function FundsInput({
  onChange,
  name,
  value,
  children,
  error,
  refFor,
  title,
  amountTitle,
  originTitle,
  disabled = false,
}: Props) {
  return (
    <>
      <Label error={error('type')} ref={refFor('type')}>
        {title}
        <Selection
          options={{ ...typeOptions, OTHER: 'Other income' }}
          placeholder="Please select..."
          name={`${name}type`}
          onChange={(incomeType) =>
            onChange({
              ...value,
              type:
                incomeType === ''
                  ? undefined
                  : incomeType === 'OTHER'
                  ? { otherName: '' }
                  : (incomeType as Funds['type']),
            })
          }
          disabled={disabled}
          value={typeof value.type === 'object' ? 'OTHER' : value.type}
          error={error('type')}
        />
        {typeof value.type === 'object' && (
          <Label error={error('type')} ref={refFor('type')}>
            Please describe the source of your funds
          <StandardInput
            id={`${name}otherType`}
            placeholder={`Enter ${title.toLowerCase()}`}
            onChange={(otherName: string) =>
              onChange({
                ...value,
                type: { otherName },
              })
            }
            disabled={disabled}
            value={value.type.otherName}
            error={error('type')}
          />
          </Label>
        )}
      </Label>
      <Label error={error('amount')} ref={refFor('amount')}>
        {amountTitle}
        <MoneyInput
          id={`${name}amount`}
          onChange={(amount) =>
            onChange({
              ...value,
              amount,
            })
          }
          disabled={disabled}
          value={value.amount}
          error={error('amount')}
        />
      </Label>
      {children}
      <Label error={error('origin')} ref={refFor('origin')}>
        {originTitle}
        <Radios
          name={`${name}originUK`}
          options={['Yes', 'No']}
          onChange={(originUK) =>
            onChange({
              ...value,
              origin: originUK === 'Yes' ? countriesData[0]!.ISOcode : '',
            })
          }
          disabled={disabled}
          value={
            value.origin === undefined
              ? undefined
              : value.origin !== countriesData[0]!.ISOcode
              ? 'No'
              : 'Yes'
          }
          error={error('origin')}
        />
      </Label>
      {value.origin !== undefined &&
        value.origin !== countriesData[0]!.ISOcode && (
          <Label error={error('origin')}>
            Where is it from?
            <CountryInput
              name={`${name}country`}
              value={value.origin}
              disabled={disabled}
              onChange={(origin: string) => onChange({ ...value, origin })}
              error={error('origin')}
              filterOut={[countriesData[0]!.ISOcode]}
              dataSource={countriesOfBirthAndIncome}
            />
          </Label>
        )}
    </>
  );
}
