/* eslint-disable camelcase */
import { AxiosResponse } from 'axios';

import { client, setAuthHeader } from '../client';
import * as dependencies from '../../dependencies';

interface GetTermsAndConditionsVersionResponse {
  data: {
    id: {
      type: string;
      key: string;
    };
    format: string | null;
    version: number;
    content: string;
    hash: string;
  };
}

type Attribution = {
  product_key: string;
  channel_key: string;
};

export interface AuthSessionResponse {
  message_id: string;
  session_id: string;
}

export interface AuthTokenResponse {
  access_token: string;
  access_token_expiry: number;
  customer_id: string;
  id_token: string | null;
  refresh_token: string;
  refresh_token_expiry: number;
  scope: string;
}

export const registerUserRequest = ({
  email,
  termsVersion,
  attribution,
}: {
  email: string;
  termsVersion: GetTermsAndConditionsVersionResponse['data']['version'];
  attribution: Attribution;
}): Promise<AxiosResponse<AuthSessionResponse>> =>
  client.post('auth/users', {
    email,
    accepted_content: [
      {
        id: {
          type: 'json',
          key: 'terms_and_conditions',
        },
        version: termsVersion,
      },
    ],
    source: 'WEB',
    attribution,
  });

export const authenticateUserRequest = ({
  responseToken,
  sessionId,
}: {
  responseToken: string;
  sessionId: Tokens['sessionId'];
}): Promise<AxiosResponse<AuthTokenResponse>> =>
  client.post('auth/tokens', {
    session_id: sessionId,
    challenge_response_token: responseToken,
  });

export const refreshAccessTokenRequest = ({
  refreshToken,
  userId,
}: {
  refreshToken: Tokens['refreshToken'];
  userId: Tokens['userId'];
}): Promise<AxiosResponse<AuthTokenResponse>> =>
  client.post('auth/tokens/refresh', {
    refresh_token: refreshToken,
    customer_id: userId,
  });

interface Tokens {
  accessToken?: string | null;
  refreshToken?: string | null;
  sessionId?: string | null;
  accessTokenExpiry?: Date | null;
  userId: string | null;
}

export const getTokens = (): Tokens => {
  const parsed = JSON.parse(dependencies.getItem('tokens') || '{}');
  return {
    ...parsed,
    accessTokenExpiry: parsed.accessTokenExpiry
      ? new Date(parsed.accessTokenExpiry)
      : null,
  };
};

export const setTokens = (update: Partial<Tokens>): void => {
  const updatedTokens = {
    ...getTokens(),
    ...update,
  };
  dependencies.setItem(
    'tokens',
    JSON.stringify({
      ...updatedTokens,
      accessTokenExpiry: updatedTokens.accessTokenExpiry?.getTime() || null,
    }),
  );
  if (updatedTokens.accessToken) {
    setAuthHeader(updatedTokens.accessToken);
  }
};

export const clearTokens = (): void => {
  dependencies.removeItem('tokens');
};

type AxiosMethod<Request, Response> = (
  payload: Request,
) => Promise<AxiosResponse<Response>>;

export const withRefresh = <Request, Response>(
  method: AxiosMethod<Request, Response>,
): AxiosMethod<Request, Response> => {
  const refresh = async (): Promise<void> => {
    const { refreshToken, userId } = getTokens();
    const { data } = await refreshAccessTokenRequest({
      refreshToken,
      userId: userId || null,
    });
    const accessTokenExpiry = new Date(Date.now());
    accessTokenExpiry.setSeconds(
      accessTokenExpiry.getSeconds() + data.access_token_expiry,
    );
    setTokens({
      accessToken: data.access_token,
      refreshToken: data.refresh_token,
      accessTokenExpiry,
      userId: data.customer_id,
    });
  };
  return async (payload: Request) => {
    const now = new Date(Date.now());
    const { accessTokenExpiry } = getTokens();
    if (accessTokenExpiry && accessTokenExpiry <= now) {
      await refresh();
      return method(payload);
    }
    let response = await method(payload);
    if (response.status === 401 || response.status === 403) {
      await refresh();
      response = await method(payload);
    }
    return response;
  };
};
