import { useOnboarding } from 'lib/useOnboarding';
import Page from 'components/Page/Page';
import Heading from 'components/Text/Heading';
import Subheading from 'components/Text/Subheading';
import TopBar from 'components/TopBar/TopBar';
import Centred from 'components/Text/Centred';
import BodyTextBold from 'components/Text/BodyTextBold';
import CardTextBold from 'components/Text/CardTextBold';
import CardText from 'components/Text/CardText';
import CardTextContainer from 'components/Card/CardTextContainer';
import PrimaryButton from 'components/Button/PrimaryButton';
import SecondaryButton from 'components/Button/SecondaryButton';
import BasicCard from 'components/Card/BasicCard';
import CardItem from 'components/Card/CardItem';
import { stateMatches } from 'sanstatecharts-onboarding';
import { ReactComponent as Document } from 'assets/document_scan.svg';
import { ReactComponent as Face } from 'assets/face_scan.svg';
import CardIcon from 'components/Card/CardIcon';

export default function IdandV() {
  const [state, send, canTransition] = useOnboarding();

  return (
    <Page>
      <TopBar title="Preparation"></TopBar>
      <Centred>
        <Heading>Checking your identity</Heading>
        <Subheading>
          We need some evidence to make sure you are who you say you are
        </Subheading>
        <BodyTextBold>Be prepared to:</BodyTextBold>
      </Centred>
      <BasicCard>
        <CardItem>
          <CardIcon>
            <Document />
          </CardIcon>
          <CardTextContainer>
            <CardTextBold>
              Provide a photo of your original in-date:
            </CardTextBold>
            <CardText>UK driving licence or passport (photo page)</CardText>
          </CardTextContainer>
        </CardItem>
        <CardItem>
          <CardIcon>
            <Face />
          </CardIcon>
          <CardTextContainer>
            <CardTextBold>Scan your face:</CardTextBold>
            <CardText>
              This is to make sure it’s you and you’re a real person
            </CardText>
          </CardTextContainer>
        </CardItem>
      </BasicCard>
      <PrimaryButton
        onClick={() => send({ type: 'CONTINUE' })}
        status={
          stateMatches(state, 'idandV', 'submitting')
            ? 'SUBMITTING'
            : canTransition({ type: 'CONTINUE' })
            ? 'ACTIVE'
            : 'DISABLED'
        }>
        I'm ready
      </PrimaryButton>
      <SecondaryButton>Cancel</SecondaryButton>
    </Page>
  );
}
