import { noErrors } from 'statelib/validate';
import { assign, DoneInvokeEvent } from 'xstate';
import merge from 'lodash.merge';
import cloneDeep from 'lodash.clonedeep';
import { AccommodationCircumstance } from 'services/customer';
import {
  SelectProductCode,
  UpdateAboutYou,
  AddAnotherName,
  RemoveOtherName,
  ClearOtherNames,
  MaidenNameYes,
  MaidenNameNo,
  UpdateOtherName,
  AddNationality,
  RemoveNationality,
  UpdateNationality,
  AddTaxResidenceData,
  RemoveTaxResidenceData,
  UpdateTaxResidenceData,
  UpdatePrimaryTelNumber,
  UpdateAccommodationCircumstances,
  AddAddress,
  RemoveAddress,
  UpdateAddress,
  UpdateFinances,
  AddIncomeSource,
  RemoveIncomeSource,
  UpdateIncomeSource,
  UpdateAccount,
  Continue,
  UpdateInitialDeposit,
  AddInitialDeposit,
  RemoveInitialDeposit,
  ClearInitialDeposits,
  Restart,
} from './events';
import {
  emptyUserAddress,
  UserAddress,
  insertExtraAddressIfNeeded,
  pruneIrrelevantAddresses,
} from './address';
import { emptyFunds } from './funds';
import { OnboardingContext } from './context';
import { maidenNameIsSet, OtherName } from './otherName';
import { movingInDateValidationSchema } from './validation/contactDetailsValidation';
import {
  Auth,
  defaultAuth,
  SetAuth,
  SetEmailAddress,
} from './magicLink.machine';
import { emptyTaxResidenceEntry } from 'services/customer';
import { purposesRequiringIncomingsOutgoings } from './validation/accountValidation';

const replaceOtherNameAtIndex = (name: string, index: number) => (
  s: OtherName,
  i: number,
) => (i !== index ? s : { ...s, name });

const replaceAtIndex = <T>(item: T, index: number) => (s: T, i: number) =>
  i !== index ? s : item;

const setMaidenNameFalseAtIndex = (index: number) => (
  otherName: OtherName,
  i: number,
) => (i !== index ? otherName : { ...otherName, isMaidenName: false });

const setMaidenNameTrueAtIndex = (index: number) => (
  otherName: OtherName,
  i: number,
) => ({ ...otherName, isMaidenName: i === index });

const moreThan3YearsAgo = (d: Date) => {
  const threeYearsAgo = new Date();
  threeYearsAgo.setFullYear(threeYearsAgo.getFullYear() - 3);
  return threeYearsAgo > d;
};

const correctedMovingInDate = (value: UserAddress): UserAddress =>
  typeof value.movingInDate === 'object' &&
  noErrors(movingInDateValidationSchema, value.movingInDate) &&
  moreThan3YearsAgo(
    new Date(value.movingInDate.year, value.movingInDate.month - 1),
  )
    ? {
        ...value,
        movingInDate: 'moreThan3Years',
      }
    : value;

const replaceAddressAtIndex = (
  addresses: UserAddress[],
  value: UserAddress,
  index: number,
) =>
  addresses
    .map(replaceAtIndex(correctedMovingInDate(value), index))
    .reduce(pruneIrrelevantAddresses, [])
    .reduce(insertExtraAddressIfNeeded, []);

const notAtIndex = <T>(index: number) => (_item: T, i: number) => i !== index;

const removeAddressAtIndex = (addresses: UserAddress[], index: number) =>
  addresses.filter(notAtIndex(index)).reduce(insertExtraAddressIfNeeded, []);

export const actions = {
  setAuth: assign((_context: OnboardingContext, { auth }: SetAuth) => ({
    auth,
  })),
  assignSessionId: assign(
    (
      _context: OnboardingContext,
      { data: sessionId }: DoneInvokeEvent<string>,
    ) => ({
      sessionId,
    }),
  ),
  setEmailAddress: assign(
    (_context: OnboardingContext, { emailAddress }: SetEmailAddress) => ({
      emailAddress,
    }),
  ),
  assignAuth: assign(
    (_context: OnboardingContext, { data: auth }: DoneInvokeEvent<Auth>) => ({
      auth,
    }),
  ),
  clearAuth: assign((_context: OnboardingContext, _event: Restart) => ({
    auth: {
      ...defaultAuth,
    },
  })),
  clearMagicLink: assign(({ auth }: OnboardingContext, _event: Continue) => ({
    auth: {
      ...auth,
      initialUrl: '',
    },
  })),
  addAnotherName: assign(
    ({ aboutYou }: OnboardingContext, _event: AddAnotherName) => ({
      aboutYou: {
        ...aboutYou,
        otherNames: [
          ...(aboutYou.otherNames || []),
          {
            name: '',
            isMaidenName: maidenNameIsSet(aboutYou.otherNames || [])
              ? false
              : undefined,
          },
        ],
      },
    }),
  ),
  clearOtherNames: assign(
    ({ aboutYou }: OnboardingContext, _event: ClearOtherNames) => ({
      aboutYou: {
        ...aboutYou,
        otherNames: [],
      },
    }),
  ),
  removeAnotherName: assign(
    ({ aboutYou }: OnboardingContext, { index }: RemoveOtherName) => ({
      aboutYou: {
        ...aboutYou,
        otherNames:
          aboutYou.otherNames === undefined
            ? []
            : aboutYou.otherNames.filter(notAtIndex(index)),
      },
    }),
  ),
  addMaidenName: assign(
    ({ aboutYou }: OnboardingContext, { index }: MaidenNameYes) => ({
      aboutYou: {
        ...aboutYou,
        otherNames: aboutYou.otherNames?.map(setMaidenNameTrueAtIndex(index)),
      },
    }),
  ),
  removeMaidenName: assign(
    ({ aboutYou }: OnboardingContext, { index }: MaidenNameNo) => ({
      aboutYou: {
        ...aboutYou,
        otherNames: aboutYou.otherNames?.map(setMaidenNameFalseAtIndex(index)),
      },
    }),
  ),
  updateOtherName: assign(
    ({ aboutYou }: OnboardingContext, { value, index }: UpdateOtherName) => ({
      aboutYou: {
        ...aboutYou,
        otherNames: aboutYou.otherNames?.map(
          replaceOtherNameAtIndex(value, index),
        ),
      },
    }),
  ),
  updateNationality: assign(
    ({ aboutYou }: OnboardingContext, { value, index }: UpdateNationality) => ({
      aboutYou: {
        ...aboutYou,
        nationalities: aboutYou.nationalities.map(replaceAtIndex(value, index)),
      },
    }),
  ),
  addNationality: assign(
    ({ aboutYou }: OnboardingContext, _event: AddNationality) => ({
      aboutYou: {
        ...aboutYou,
        nationalities: [...aboutYou.nationalities, ''],
      },
    }),
  ),
  removeNationality: assign(
    ({ aboutYou }: OnboardingContext, { index }: RemoveNationality) => ({
      aboutYou: {
        ...aboutYou,
        nationalities: aboutYou.nationalities.filter(notAtIndex(index)),
      },
    }),
  ),
  updateTaxResidenceData: assign(
    ({ aboutYou }: OnboardingContext, { value, index }: UpdateTaxResidenceData) =>
      ({
        aboutYou: {
          ...aboutYou,
          taxResidenceData: aboutYou.taxResidenceData.map(replaceAtIndex(value, index)),
        },
      }),
  ),
  addTaxResidenceData: assign(
    ({ aboutYou }: OnboardingContext, _event: AddTaxResidenceData) => ({
      aboutYou: {
        ...aboutYou,
        taxResidenceData: [...aboutYou.taxResidenceData, cloneDeep(emptyTaxResidenceEntry)],
      },
    }),
  ),
  removeTaxResidenceData: assign(
    ({ aboutYou }: OnboardingContext, { index }: RemoveTaxResidenceData) =>
      ({
        aboutYou: {
          ...aboutYou,
          taxResidenceData: aboutYou.taxResidenceData.filter(notAtIndex(index)),
        },
      }),
  ),
  updateAddress: assign(
    (
      { contactDetails }: OnboardingContext,
      { value, index }: UpdateAddress,
    ) => ({
      contactDetails: {
        ...contactDetails,
        addresses: replaceAddressAtIndex(
          contactDetails.addresses,
          value,
          index,
        ),
      },
    }),
  ),
  addAddress: assign(
    ({ contactDetails }: OnboardingContext, _event: AddAddress) => ({
      contactDetails: {
        ...contactDetails,
        addresses: [...contactDetails.addresses, cloneDeep(emptyUserAddress)],
      },
    }),
  ),
  removeAddress: assign(
    ({ contactDetails }: OnboardingContext, { index }: RemoveAddress) => ({
      contactDetails: {
        ...contactDetails,
        addresses:
          contactDetails.addresses.length === 1 && index === 0
            ? [cloneDeep(emptyUserAddress)]
            : removeAddressAtIndex(contactDetails.addresses, index),
      },
    }),
  ),
  assignUpdatedAddresses: assign(
    (
      { contactDetails }: OnboardingContext,
      {
        data: [, addresses],
      }: DoneInvokeEvent<[Partial<OnboardingContext>, UserAddress[]]>,
    ) => ({
      contactDetails: {
        ...contactDetails,
        addresses,
      },
    }),
  ),
  assignRefreshedDetails: assign(
    (
      context: OnboardingContext,
      {
        data: [updatedContext],
      }: DoneInvokeEvent<[Partial<OnboardingContext>, unknown]>,
    ) => merge(context, updatedContext),
  ),
  assignProductCode: assign(
    (_context: OnboardingContext, { account }: SelectProductCode) => ({
      productCode: account,
    }),
  ),
  assignAboutYou: assign(
    (
      { aboutYou }: OnboardingContext,
      { field, value }: UpdateAboutYou<any>,
    ) => ({
      aboutYou: {
        ...aboutYou,
        [field]: value,
      },
    }),
  ),
  assignPrimaryTelNumber: assign(
    (
      { contactDetails }: OnboardingContext,
      { value: primaryTelNumber }: UpdatePrimaryTelNumber,
    ) => ({
      contactDetails: {
        ...contactDetails,
        primaryTelNumber,
      },
    }),
  ),
  updateAccommodationCircumstances: assign(
    (
      { contactDetails }: OnboardingContext,
      { value }: UpdateAccommodationCircumstances,
    ) => ({
      contactDetails: {
        ...contactDetails,
        accommodationCircumstances: value as AccommodationCircumstance,
      },
    }),
  ),
  assignFinances: assign(
    (
      { finances }: OnboardingContext,
      { field, value }: UpdateFinances<any>,
    ) => ({
      finances: {
        ...finances,
        companyControl:
          field === 'employmentStatus' && value !== 'EMPLOYED'
            ? undefined
            : finances.companyControl,
        occupation:
          field === 'employmentStatus' &&
          value !== 'EMPLOYED' &&
          value !== 'SELF_EMPLOYED'
            ? ''
            : finances.occupation,
        industrySector:
          field === 'employmentStatus' &&
          value !== 'SELF_EMPLOYED' &&
          (value !== 'EMPLOYED' || !finances.companyControl)
            ? ''
            : finances.industrySector,
        companyName:
          field === 'employmentStatus' &&
          value !== 'EMPLOYED' &&
          value !== 'SELF_EMPLOYED'
            ? ''
            : finances.companyName,
        employmentStarted:
          field === 'employmentStatus' &&
          value !== 'EMPLOYED' &&
          value !== 'SELF_EMPLOYED'
            ? undefined
            : finances.employmentStarted,
        [field]: value,
      },
    }),
  ),
  updateIncomeSource: assign(
    (
      { finances }: OnboardingContext,
      { value, index }: UpdateIncomeSource,
    ) => ({
      finances: {
        ...finances,
        incomeSources: finances.incomeSources.map(replaceAtIndex(value, index)),
      },
    }),
  ),
  addIncomeSource: assign(
    ({ finances }: OnboardingContext, _event: AddIncomeSource) => ({
      finances: {
        ...finances,
        incomeSources: [...finances.incomeSources, emptyFunds],
      },
    }),
  ),
  removeIncomeSource: assign(
    ({ finances }: OnboardingContext, { index }: RemoveIncomeSource) => ({
      finances: {
        ...finances,
        incomeSources:
          index === 0 && finances.incomeSources.length === 1
            ? [emptyFunds]
            : finances.incomeSources.filter(notAtIndex(index)),
      },
    }),
  ),
  assignAccount: assign(
    ({ account }: OnboardingContext, { field, value }: UpdateAccount<any>) => ({
      account: {
        ...account,
        incoming:
          field === 'purposeOfNewAccount' &&
          !purposesRequiringIncomingsOutgoings.includes(value)
            ? undefined
            : account.incoming,
        outgoings:
          field === 'purposeOfNewAccount' &&
          !purposesRequiringIncomingsOutgoings.includes(value)
            ? undefined
            : account.outgoings,
        [field]: value,
      },
    }),
  ),
  updateInitialDeposit: assign(
    (
      { account }: OnboardingContext,
      { value, index }: UpdateInitialDeposit,
    ) => ({
      account: {
        ...account,
        initialDeposits: account.initialDeposits?.map(
          replaceAtIndex(value, index),
        ),
      },
    }),
  ),
  addInitialDeposit: assign(
    ({ account }: OnboardingContext, _event: AddInitialDeposit) => ({
      account: {
        ...account,
        initialDeposits: [...(account.initialDeposits || []), emptyFunds],
      },
    }),
  ),
  removeInitialDeposit: assign(
    ({ account }: OnboardingContext, { index }: RemoveInitialDeposit) => ({
      account: {
        ...account,
        initialDeposits: account.initialDeposits?.filter(notAtIndex(index)),
      },
    }),
  ),
  clearInitialDeposits: assign(
    ({ account }: OnboardingContext, _event: ClearInitialDeposits) => ({
      account: {
        ...account,
        initialDeposits: [],
      },
    }),
  ),
};
