const requiredEnvironmentVariables = {
  REACT_APP_API_DOMAIN: (
    <span>
      Hostname of the API e.g.&nbsp;
      <code>private-api-pre.santanderuk.pre.corp</code>
    </span>
  ),
  REACT_APP_API_ENDPOINT: (
    <span>
      API endpoint base e.g.&nbsp;
      <code>https://private-api-pre.santanderuk.pre.corp/sanuk/internal</code>
    </span>
  ),
  REACT_APP_WEBVIEW: (
    <span>
      either&nbsp;
      <code>BCA</code> or <code>PCA</code>
    </span>
  ),
};

const isJest = process.env.NODE_ENV === 'test';

const processNotSet = (s: string) => !isJest && !process.env[s];

export default function EnvironmentCheck({
  children,
}: {
  children: JSX.Element;
}) {
  const missing = Object.keys(requiredEnvironmentVariables).filter(
    processNotSet,
  );
  if (missing.length) {
    return (
      <>
        <p>
          The following environment variables need adding to your{' '}
          <code>.env</code> file:
        </p>
        <ul>
          {missing.map((key) => (
            <li key={key}>
              <strong>{key}</strong>:{' '}
              {
                requiredEnvironmentVariables[
                  key as keyof typeof requiredEnvironmentVariables
                ]
              }
            </li>
          ))}
        </ul>
      </>
    );
  }
  return children;
}
