import { assign, DoneInvokeEvent, MachineConfig } from 'xstate';
import { getPerson } from 'services/customer';
import { getAddress } from 'services/address';
import { getApplication, GetApplicationResponse } from 'services/application';
import merge from 'lodash.merge';
import { OnboardingContext, defaultOnboardingContext } from './context';
import { services as sharedServices } from './services';
import { actions } from './actions';
import { guards } from './guards';
import { DoneInvokeRetrieveUser, OnboardingEvent } from './events';
import { personToContext } from './person';

export type PCAOnboardingContext = OnboardingContext;

export const defaultPCAOnboardingContext = defaultOnboardingContext;

export type PCAOnboardingSchema = {
  context: OnboardingContext;
  states: {
    hydrate: {};
    settingsExpectations: {
      states: {
        editing: {};
        whyWeNeedThisInfo: {};
      };
    };
    magicLink: {};
    retrieveUser: {};
    retrievalError: {};
    evaluateUser: {};
    magicLinkIsValid: {};
    productInitial: {};
    steps: {
      states: {
        aboutYou: {
          states: {
            editing: {};
            updating: {};
            updateError: {};
          };
        };
        contactDetails: {
          states: {
            editing: {};
            updating: {};
            updateError: {};
          };
        };
        finances: {
          states: {
            editing: {};
            incomeIsReducing: {};
            outgoingsAreIncreasing: {};
            updating: {};
            updateError: {};
          };
        };
        account: {
          states: {
            editing: {};
            updating: {};
            updateError: {};
          };
        };
        overdraft: {
          states: {
            editing: {};
            updating: {};
            updateError: {};
          };
        };
      };
    };
    productFinal: {};
    legalBit: {
      states: {
        notAgreedToTerms: {};
        agreedToTerms: {};
        history: {};
      };
    };
    keyFacts: {};
    generalTerms: {
      states: {
        notAgreedToTerms: {};
        agreedToTerms: {};
        history: {};
      };
    };
    marketingPreferences: {};
    congratulations: {};
    idandV: {
      states: {
        pristine: {};
        submitting: {};
      };
    };
    identityFailure: {};
    complete: {};
    error: {};
  };
};

export const pcaServices = {
  retrieveUser: async ({
    auth: { userId, sessionId },
  }: Pick<PCAOnboardingContext, 'auth'>): Promise<
    Partial<PCAOnboardingContext>
  > => {
    if (!userId || !sessionId) {
      throw new Error(`userRetrieved with no ${userId} or ${sessionId}`);
    }
    const [{ data: person }, { data: addressHistory }] = await Promise.all([
      getPerson({ userId }),
      getAddress({
        userId,
        addressableType: 'CUSTOMER',
      }),
    ]);
    let applicationResponse: GetApplicationResponse | undefined;
    if (person.application_id) {
      const { data } = await getApplication(person.application_id);
      applicationResponse = data;
    }
    return personToContext(person, addressHistory, applicationResponse);
  },
};

const services = sharedServices(pcaServices.retrieveUser);

const pcaActions = {
  assignRetrieval: assign(
    (
      context: OnboardingContext,
      {
        data,
      }: DoneInvokeEvent<DoneInvokeRetrieveUser<PCAOnboardingContext>['data']>,
    ) => merge(context, data),
  ),
};

type Depromisified<T> = T extends Promise<infer R> ? R : never;

type DoneInvokeAboutYou = {
  type: 'done.invoke.UPDATE_ABOUT_YOU';
  data: Depromisified<ReturnType<typeof services.updatePersonAboutYou>>;
};

type DoneInvokeContactDetails = {
  type: 'done.invoke.UPDATE_CONTACT_DETAILS';
  data: Depromisified<ReturnType<typeof services.updatePersonContactDetails>>;
};

type DoneInvokeFinances = {
  type: 'done.invoke.UPDATE_FINANCES';
  data: Depromisified<ReturnType<typeof services.updatePersonFinances>>;
};

type DoneInvokeAccount = {
  type: 'done.invoke.UPDATE_ACCOUNT';
  data: Depromisified<ReturnType<typeof services.updatePersonAccount>>;
};

type DoneInvokeOverdraft = {
  type: 'done.invoke.UPDATE_OVERDRAFT';
  data: Depromisified<ReturnType<typeof services.updatePersonOverdraft>>;
};

export type PCAOnboardingEvent =
  | OnboardingEvent
  | DoneInvokeAboutYou
  | DoneInvokeContactDetails
  | DoneInvokeFinances
  | DoneInvokeAccount
  | DoneInvokeOverdraft;

export const pcaOnboardingMachine: MachineConfig<
  PCAOnboardingContext,
  PCAOnboardingSchema,
  PCAOnboardingEvent
> = {
  id: 'onboarding',
  context: defaultPCAOnboardingContext,
  initial: 'hydrate',
  states: {
    hydrate: {
      always: [
        {
          target: 'magicLink',
          cond: guards.hasUserOrMagicLink,
        },
        { target: 'settingsExpectations' },
      ],
    },
    settingsExpectations: {
      on: { CONTINUE: 'magicLink' },
      initial: 'editing',
      states: {
        editing: {
          on: { WHY_DO_WE_NEED_THIS: 'whyWeNeedThisInfo' },
        },
        whyWeNeedThisInfo: {
          on: { CLOSE: 'editing' },
        },
      },
    },
    magicLink: {
      on: {
        SET_AUTH: { actions: actions.setAuth },
        SET_EMAIL_ADDRESS: { actions: actions.setEmailAddress },
      },
      invoke: {
        id: 'MAGIC_LINK',
        src: services.magicLink,
        onDone: {
          target: 'retrieveUser',
          actions: actions.assignAuth,
        },
        onError: 'retrievalError',
      },
    },
    retrieveUser: {
      invoke: {
        id: 'RETRIEVE_USER',
        src: pcaServices.retrieveUser,
        onDone: {
          target: 'evaluateUser',
          actions: pcaActions.assignRetrieval,
        },
        onError: 'retrievalError',
      },
    },
    retrievalError: {
      on: {
        RETRY: 'magicLink',
        RESTART: {
          target: 'magicLink',
          actions: actions.clearAuth,
        },
      },
    },
    evaluateUser: {
      always: [
        {
          target: 'magicLinkIsValid',
          cond: guards.hasMagicLink,
        },
        {
          target: 'productFinal',
          cond: guards.overdraftComplete,
        },
        {
          target: 'steps.overdraft',
          cond: guards.accountComplete,
        },
        {
          target: 'steps.account',
          cond: guards.financesComplete,
        },
        {
          target: 'steps.finances',
          cond: guards.contactDetailsComplete,
        },
        {
          target: 'steps.contactDetails',
          cond: guards.aboutYouComplete,
        },
        {
          target: 'steps.aboutYou',
          cond: guards.hasProductCode,
        },
        { target: 'productInitial' },
      ],
    },
    magicLinkIsValid: {
      on: {
        CONTINUE: {
          target: 'productInitial',
          actions: actions.clearMagicLink,
        },
      },
    },
    productInitial: {
      on: {
        SELECT_PRODUCT_CODE: {
          target: 'steps.aboutYou',
          actions: actions.assignProductCode,
        },
      },
    },
    steps: {
      states: {
        aboutYou: {
          initial: 'editing',
          states: {
            editing: {
              on: {
                BACK: '#onboarding.productInitial',
                UPDATE_ABOUT_YOU: { actions: actions.assignAboutYou },
                CONTINUE: {
                  target: 'updating',
                  cond: guards.aboutYouComplete,
                },
                ADD_OTHER_NAME: {
                  actions: actions.addAnotherName,
                  cond: guards.canAddAnotherName,
                },
                REMOVE_OTHER_NAME: {
                  actions: actions.removeAnotherName,
                  cond: guards.canRemoveAnotherName,
                },
                CLEAR_OTHER_NAMES: { actions: actions.clearOtherNames },
                MAIDEN_NAME_YES: {
                  actions: actions.addMaidenName,
                  cond: guards.otherNameExistsAtIndex,
                },
                MAIDEN_NAME_NO: {
                  actions: actions.removeMaidenName,
                  cond: guards.otherNameExistsAtIndex,
                },
                UPDATE_OTHER_NAME: {
                  actions: actions.updateOtherName,
                  cond: guards.otherNameExistsAtIndex,
                },
                UPDATE_NATIONALITY: {
                  actions: actions.updateNationality,
                  cond: guards.nationalityExistsAtIndex,
                },
                ADD_NATIONALITY: {
                  actions: actions.addNationality,
                  cond: guards.canAddNationality,
                },
                REMOVE_NATIONALITY: {
                  actions: actions.removeNationality,
                  cond: guards.canRemoveNationality,
                },
                UPDATE_TAX_RESIDENCE_DATA: {
                  actions: actions.updateTaxResidenceData,
                  cond: guards.taxResidenceDataExistsAtIndex,
                },
                ADD_TAX_RESIDENCE_DATA: {
                  actions: actions.addTaxResidenceData,
                  cond: guards.canAddTaxResidenceData,
                },
                REMOVE_TAX_RESIDENCE_DATA: {
                  actions: actions.removeTaxResidenceData,
                  cond: guards.canRemoveTaxResidenceData,
                },
              },
            },
            updating: {
              invoke: {
                id: 'UPDATE_ABOUT_YOU',
                src: services.updatePersonAboutYou,
                onDone: {
                  target: '#onboarding.steps.contactDetails',
                  actions: actions.assignRefreshedDetails,
                },
                onError: 'updateError',
              },
            },
            updateError: {
              on: { CLOSE: 'editing', RETRY: 'updating' },
            },
          },
        },
        contactDetails: {
          initial: 'editing',
          states: {
            editing: {
              on: {
                BACK: '#onboarding.steps.aboutYou',
                UPDATE_PRIMARY_TEL_NUMBER: {
                  actions: actions.assignPrimaryTelNumber,
                },
                UPDATE_ACCOMMODATION_CIRCUMSTANCES: {
                  actions: actions.updateAccommodationCircumstances,
                },
                CONTINUE: {
                  target: 'updating',
                  cond: guards.contactDetailsComplete,
                },
                UPDATE_ADDRESS: {
                  actions: actions.updateAddress,
                  cond: guards.addressExistsAtIndex,
                },
                ADD_ADDRESS: {
                  actions: actions.addAddress,
                  cond: guards.canAddAddress,
                },
                REMOVE_ADDRESS: {
                  actions: actions.removeAddress,
                  cond: guards.canRemoveAddress,
                },
              },
            },
            updating: {
              invoke: {
                id: 'UPDATE_CONTACT_DETAILS',
                src: services.updatePersonContactDetails,
                onDone: {
                  target: '#onboarding.steps.finances',
                  actions: [
                    actions.assignRefreshedDetails,
                    actions.assignUpdatedAddresses,
                  ],
                },
                onError: 'updateError',
              },
            },
            updateError: {
              on: { CLOSE: 'editing', RETRY: 'updating' },
            },
          },
        },
        finances: {
          initial: 'editing',
          states: {
            editing: {
              on: {
                BACK: '#onboarding.steps.contactDetails',
                CONTINUE: {
                  target: 'updating',
                  cond: guards.financesComplete,
                },
                UPDATE_FINANCES: { actions: actions.assignFinances },
                UPDATE_INCOME_SOURCE: {
                  actions: actions.updateIncomeSource,
                  cond: guards.incomeSourceExistsAtIndex,
                },
                ADD_INCOME_SOURCE: {
                  actions: actions.addIncomeSource,
                  cond: guards.canAddIncomeSource,
                },
                REMOVE_INCOME_SOURCE: {
                  actions: actions.removeIncomeSource,
                  cond: guards.canRemoveIncomeSource,
                },
                INCOME_IS_REDUCING: 'incomeIsReducing',
                OUTGOINGS_ARE_INCREASING: 'outgoingsAreIncreasing',
              },
            },
            incomeIsReducing: { on: { CLOSE: 'editing' } },
            outgoingsAreIncreasing: { on: { CLOSE: 'editing' } },
            updating: {
              invoke: {
                id: 'UPDATE_FINANCES',
                src: services.updatePersonFinances,
                onDone: {
                  target: '#onboarding.steps.account',
                  actions: actions.assignRefreshedDetails,
                },
                onError: 'updateError',
              },
            },
            updateError: {
              on: { CLOSE: 'editing', RETRY: 'updating' },
            },
          },
        },
        account: {
          initial: 'editing',
          states: {
            editing: {
              on: {
                BACK: '#onboarding.steps.finances',
                CONTINUE: {
                  target: 'updating',
                  cond: guards.accountComplete,
                },
                UPDATE_ACCOUNT: { actions: actions.assignAccount },
                UPDATE_INITIAL_DEPOSIT: {
                  actions: actions.updateInitialDeposit,
                  cond: guards.initialDepositExistsAtIndex,
                },
                ADD_INITIAL_DEPOSIT: {
                  actions: actions.addInitialDeposit,
                  cond: guards.canAddInitialDeposit,
                },
                REMOVE_INITIAL_DEPOSIT: {
                  actions: actions.removeInitialDeposit,
                  cond: guards.canRemoveInitialDeposit,
                },
                CLEAR_INITIAL_DEPOSITS: {
                  actions: actions.clearInitialDeposits,
                },
              },
            },
            updating: {
              invoke: {
                id: 'UPDATE_ACCOUNT',
                src: services.updatePersonAccount,
                onDone: {
                  target: '#onboarding.steps.overdraft',
                  actions: actions.assignRefreshedDetails,
                },
                onError: 'updateError',
              },
            },
            updateError: {
              on: { CLOSE: 'editing', RETRY: 'updating' },
            },
          },
        },
        overdraft: {
          initial: 'editing',
          states: {
            editing: {
              on: {
                BACK: '#onboarding.steps.account',
                CONTINUE: {
                  target: 'updating',
                  cond: guards.overdraftComplete,
                },
                UPDATE_ACCOUNT: { actions: actions.assignAccount },
              },
            },
            updating: {
              invoke: {
                id: 'UPDATE_OVERDRAFT',
                src: services.updatePersonOverdraft,
                onDone: {
                  target: '#onboarding.productFinal',
                  actions: actions.assignRefreshedDetails,
                },
                onError: 'updateError',
              },
            },
            updateError: {
              on: { CLOSE: 'editing', RETRY: 'updating' },
            },
          },
        },
      },
    },
    productFinal: {},
    legalBit: {
      initial: 'notAgreedToTerms',
      states: {
        notAgreedToTerms: { on: { TOGGLE: 'agreedToTerms' } },
        agreedToTerms: {
          on: { TOGGLE: 'notAgreedToTerms', CONTINUE: '#onboarding.keyFacts' },
        },
        history: { type: 'history' },
      },
      on: { BACK: 'productFinal' },
    },
    keyFacts: {
      on: { BACK: 'legalBit.history' },
    },
    generalTerms: {
      initial: 'notAgreedToTerms',
      states: {
        notAgreedToTerms: { on: { TOGGLE: 'agreedToTerms' } },
        agreedToTerms: {
          on: {
            TOGGLE: 'notAgreedToTerms',
            CONTINUE: '#onboarding.marketingPreferences',
          },
        },
        history: { type: 'history' },
      },
      on: { BACK: 'keyFacts' },
    },
    marketingPreferences: {
      on: { BACK: 'generalTerms.history', CONTINUE: 'idandV' },
    },
    congratulations: {},
    idandV: {
      initial: 'pristine',
      states: {
        pristine: {
          on: {
            BACK: '#onboarding.steps.overdraft',
            CONTINUE: 'submitting',
          },
        },
        submitting: {
          invoke: {
            id: 'FETCH_SESSION_ID',
            src: services.fetchSessionId,
            onDone: {
              target: '#onboarding.complete',
              actions: actions.assignSessionId,
            },
            onError: '#onboarding.identityFailure',
          },
        },
      },
    },
    identityFailure: {},
    complete: {
      type: 'final',
    },
    error: {},
  },
};
