import BusinessLogo from 'components/BusinessLogo';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import { ReactComponent as Icon } from 'assets/walking-figure.svg';
import PrimaryButton from 'components/Button/PrimaryButton';
import { useBCAOnboarding } from 'lib/useBCAOnboarding';
import BackArrow from 'components/BackArrow/BackArrow';

export default function AccountIntro() {
  const [, send, canTransition] = useBCAOnboarding();
  return (
    <Page>
      <BackArrow send={send} canTransition={canTransition} />
      <BusinessLogo />
      <CenterBlockSVG>
        <Icon />
      </CenterBlockSVG>
      <ProgressBar inline step="account" title="The last bit" />
      <PrimaryButton
        onClick={() => send({ type: 'CONTINUE' })}
        status={!canTransition({ type: 'CONTINUE' }) ? 'DISABLED' : 'ACTIVE'}>
        Continue
      </PrimaryButton>
    </Page>
  );
}
