import { assertIsDefined } from 'statelib/assert';

let getPersistedData: undefined | ((name: string) => string | undefined);
let setPersistedData: undefined | ((name: string, value: string) => void);
let removePersistedData: undefined | ((name: string) => void);

type Dependencies = {
  getPersistedData: typeof getPersistedData;
  setPersistedData: typeof setPersistedData;
  removePersistedData: typeof removePersistedData;
};

export function initialiseDependencies(dependencies: Dependencies) {
  ({ getPersistedData, setPersistedData, removePersistedData } = dependencies);
}

export function setItem(name: string, value: string) {
  assertIsDefined(
    setPersistedData,
    `setPersistedData(${name}): dependencies have not been initialised`,
  );
  return setPersistedData(name, value);
}

export function getItem(name: string) {
  assertIsDefined(
    getPersistedData,
    `getPersistedData(${name}): dependencies have not been initialised`,
  );
  return getPersistedData(name);
}

export function removeItem(name: string) {
  assertIsDefined(
    removePersistedData,
    `removePersistedData(${name}): dependencies have not been initialised`,
  );
  return removePersistedData(name);
}
