import { ComponentProps, useMemo, useState } from 'react';
import { Machine } from 'xstate';
import {
  employmentMachine,
  logToConsole,
  startService,
} from 'sanstatecharts-onboarding';
import { searchOccupations } from 'services/customer';
import EmploymentInput from './EmploymentInput';

const occupationMachine = employmentMachine(
  'occupation',
  (searchTerm: string) =>
    searchOccupations({ searchTerm }).then(({ data }) => data.occupations),
);

type EmploymentInputProps = ComponentProps<typeof EmploymentInput>;

type Props = Omit<
  EmploymentInputProps,
  'service' | 'placeholder' | 'searching'
>;

export default function OccupationInput(props: Props) {
  const [firstLine] = useState(props.value || '');
  const service: EmploymentInputProps['service'] = useMemo(() => {
    const occupationService = startService(
      Machine(occupationMachine).withContext({
        line: typeof firstLine === 'string' ? firstLine : firstLine.name,
        searchResults:
          typeof firstLine === 'string'
            ? {}
            : { [firstLine.name]: [firstLine] },
      }),
    );
    if (process.env.REACT_APP_XSTATE_LOG && process.env.NODE_ENV !== 'test') {
      logToConsole(occupationService);
    }
    return occupationService;
  }, [firstLine]);
  return (
    <EmploymentInput
      {...props}
      service={service}
      name="occupation"
      placeholder="Start typing occupation..."
      searching="Searching for your occupation"
    />
  );
}
