import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import Heading from 'components/Text/Heading';
import { useOnboarding } from 'lib/useOnboarding';
import {
  accountValidation,
  stateMatches,
  purposesRequiringIncomingsOutgoings,
} from 'sanstatecharts-onboarding';
import PersonUpdateError from 'components/PersonUpdateError/PersonUpdateError';
import { useValidation } from 'lib/useValidation';
import Form from 'components/Form/Form';
import PurposeOfNewAccountField from 'components/Fields/PurposeOfNewAccountFields';
import LargeCashField from 'components/Fields/LargeCashField';
import Label from 'components/Form/Label';
import MoneyInput from 'components/Form/MoneyInput';
import FundsInput from 'components/Form/FundsInput';
import FormLink from 'components/Form/FormLink';
import { fieldError } from 'components/ValidationErrors/findError';
import Radios from 'components/Form/Radios';
import styled from 'styled-components';
import ContinueButton from 'components/Form/ContinueButton';
import { PCAStepState } from 'lib/StepState';
import InfoBlockWithIcon from 'components/InfoBlock/InfoBlockWithIcon';

const InitialDepositContainer = styled.div`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
`;

export default function Account() {
  const [state, send, canTransition] = useOnboarding();
  const { account } = state.context;
  const disabled = !canTransition({
    type: 'UPDATE_ACCOUNT',
    field: 'incoming',
    value: 1,
  });

  const validationErrors = accountValidation(account);
  const { shownErrors, refFor, refCreate, ...validation } = useValidation(
    validationErrors,
  );

  return (
    <Page>
      <ProgressBar step="account" title="Your account" />
      <Heading>Your account</Heading>
      <Form>
        <PurposeOfNewAccountField
          shownErrors={shownErrors}
          account={account}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        {account.purposeOfNewAccount &&
          purposesRequiringIncomingsOutgoings.includes(
            account.purposeOfNewAccount,
          ) && (
            <>
              <Label error={shownErrors['incoming']} ref={refFor('incoming')}>
                What will you pay into the account each month?
                <MoneyInput
                  id="incoming"
                  onChange={(value) =>
                    send({
                      type: 'UPDATE_ACCOUNT',
                      field: 'incoming',
                      value,
                    })
                  }
                  error={shownErrors['incoming']}
                  disabled={disabled}
                  value={account.incoming}
                />
              </Label>
              <Label error={shownErrors['outgoings']} ref={refFor('outgoings')}>
                What are your expected monthly outgoings from the account?
                <MoneyInput
                  id="outgoings"
                  onChange={(value) =>
                    send({
                      type: 'UPDATE_ACCOUNT',
                      field: 'outgoings',
                      value,
                    })
                  }
                  error={shownErrors['outgoings']}
                  disabled={disabled}
                  value={account.outgoings}
                />
              </Label>
            </>
          )}
        <LargeCashField
          shownErrors={shownErrors}
          account={account}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <Label
          error={shownErrors['initialDeposits']}
          ref={refFor('initialDeposits')}>
          Will you be making an initial deposit?
          <Radios
            name="initialDeposits"
            options={['Yes', 'No']}
            onChange={(initialDeposits) =>
              send({
                type:
                  initialDeposits === 'Yes'
                    ? 'ADD_INITIAL_DEPOSIT'
                    : 'CLEAR_INITIAL_DEPOSITS',
              })
            }
            disabled={disabled}
            value={
              account.initialDeposits === undefined
                ? undefined
                : account.initialDeposits.length > 0
                ? 'Yes'
                : 'No'
            }
            error={shownErrors['initialDeposits']}
          />
        </Label>
        {account.initialDeposits?.map((initialDeposit, index) => (
          <InitialDepositContainer key={`initialDeposit${index}`}>
            <FundsInput
              title="Source of initial deposit"
              amountTitle="How much will you initially deposit?"
              originTitle="Is the source from the UK?"
              name={`initialDeposit${index}`}
              value={initialDeposit}
              error={fieldError(shownErrors, 'initialDeposits', index)}
              refFor={refCreate('initialDeposits', index)}
              onChange={(value) =>
                send({ type: 'UPDATE_INITIAL_DEPOSIT', index, value })
              }
            />
            {canTransition({ type: 'REMOVE_INITIAL_DEPOSIT', index }) && (
              <FormLink
                id={`removeInitialDeposit${index}`}
                inline
                onClick={() => send({ type: 'REMOVE_INITIAL_DEPOSIT', index })}>
                Remove
              </FormLink>
            )}
          </InitialDepositContainer>
        ))}
        {account.initialDeposits &&
          account.initialDeposits.length > 0 &&
          canTransition({ type: 'ADD_INITIAL_DEPOSIT' }) && (
            <FormLink
              id="addInitialDeposit"
              onClick={() => send({ type: 'ADD_INITIAL_DEPOSIT' })}>
              Add another initial deposit
            </FormLink>
          )}
        <PersonUpdateError
          enabled={stateMatches(state, 'steps', 'account', 'updateError')}
        />
        <InfoBlockWithIcon>
          When you hit continue we’re going to do a soft credit check - this
          won’t affect your credit score
        </InfoBlockWithIcon>
        <ContinueButton
          validationErrors={validationErrors}
          {...validation}
          status={(state.value as PCAStepState)?.steps?.account}
        />
      </Form>
    </Page>
  );
}
