import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import { useBCAOnboarding } from 'lib/useBCAOnboarding';
import Heading from 'components/Text/Heading';
import Form from 'components/Form/Form';
import { nationalitiesValidation } from 'sanstatecharts-onboarding';
import { useValidation } from 'lib/useValidation';
import NationalityFields from 'components/Fields/NationalityFields';
import ContinueButton from 'components/Form/ContinueButton';
import { BCAStepState } from 'lib/StepState';

export default function AboutYouNationality() {
  const [state, send, canTransition] = useBCAOnboarding();
  const { aboutYou } = state.context;
  const validationErrors = nationalitiesValidation(aboutYou);
  const { shownErrors, refFor, ...validation } = useValidation(
    validationErrors,
  );
  return (
    <Page>
      <ProgressBar step="aboutYou" title="About you" />
      <Heading>Nationality</Heading>
      <Form>
        <NationalityFields
          shownErrors={shownErrors}
          aboutYou={aboutYou}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <ContinueButton
          validationErrors={validationErrors}
          {...validation}
          status={(state.value as BCAStepState)?.steps?.aboutYou?.nationality}
        />
      </Form>
    </Page>
  );
}
