import BackArrow from 'components/BackArrow/BackArrow';
import { useContext } from 'react';
import {
  PCAOnboardingSchema,
  BCAOnboardingSchema,
} from 'sanstatecharts-onboarding';
import styled from 'styled-components';
import { StateMachineContext } from 'state-machines/StateMachineContext';
import { assertIsDefined } from 'statelib/assert';
import { useOnboarding } from 'lib/useOnboarding';

const StyledProgressBarContainer = styled.div`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s5};
  display: flex;
  flex-direction: row;
`;

const StyledProgressBarWrap = styled.div`
  flex: 1;
  margin-left: ${({ theme: { spacing } }) => spacing.s3};
`;

const StyledProgressBarOuter = styled.div`
  height: ${({ theme: { spacing } }) => spacing.s2};
  position: relative;
  background-color: ${({ theme: { color } }) => color.superLightGrey};
  border-radius: ${({ theme: { spacing } }) => spacing.s4};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s3};
`;

const StyledProgressBarInner = styled.div`
  background-color: ${({ theme: { color } }) => color.primary};
  height: ${({ theme: { spacing } }) => spacing.s2};
  border-radius: ${({ theme: { spacing } }) =>
    `${spacing.s4} 0 0 ${spacing.s4}`};
`;

type Props = {
  step:
    | keyof PCAOnboardingSchema['states']['steps']['states']
    | keyof BCAOnboardingSchema['states']['steps']['states'];
  title: string;
  inline?: boolean;
};

const matches = (s: string) => (t: string) => s === t;

export default function ProgressBar({ step, title, inline }: Props) {
  const stateContext = useContext(StateMachineContext);
  const [, send, canTransition] = useOnboarding();
  assertIsDefined(
    stateContext,
    '<ProgressBar /> must be used within <StateMachines />',
  );
  const [service] = stateContext;
  const steps = Object.keys(service.machine.states?.steps.states || {});
  const totalStepsForFlow = steps.length;
  const incrementsPercentage = 100 / totalStepsForFlow;
  const stepNo = steps.findIndex(matches(step)) + 1;

  return (
    <StyledProgressBarContainer>
      {!inline && <BackArrow send={send} canTransition={canTransition} />}
      <StyledProgressBarWrap>
        <StyledProgressBarOuter>
          <StyledProgressBarInner
            style={{
              width: `${incrementsPercentage * stepNo}%`,
            }}
          />
        </StyledProgressBarOuter>
        <span className="h2 mb-2">
          <span className="text-bold">{title}</span>: Step {stepNo} of{' '}
          {totalStepsForFlow}
        </span>
      </StyledProgressBarWrap>
    </StyledProgressBarContainer>
  );
}
