import { ReactNode, useCallback, useMemo, useState } from 'react';
import { Interpreter } from 'xstate';
import { logToConsole } from 'sanstatecharts-onboarding';
import { StateMachineContext, RecordedServices } from './StateMachineContext';
import StateMachinesDebug from './StateMachinesDebug';

type Props = {
  rootService: Interpreter<any, any, any>;
  consumeAllServices?: boolean;
  debug?: boolean;
  children: ReactNode;
};

export default function StateMachines({
  rootService,
  debug = false,
  consumeAllServices = false,
  children,
}: Props) {
  const service = useMemo(() => {
    if (process.env.REACT_APP_XSTATE_LOG && process.env.NODE_ENV !== 'test') {
      logToConsole(rootService);
    }
    return rootService;
  }, [rootService]);
  const [recordedServices, setRecordedServices] = useState<RecordedServices>({
    [rootService.id]: service,
  });
  const setRecordedService = useCallback(
    (name: string, recordedService: Interpreter<any>) => {
      setRecordedServices((old) => ({ ...old, [name]: recordedService }));
    },
    [],
  );

  return (
    <StateMachineContext.Provider
      value={[
        service,
        consumeAllServices,
        recordedServices,
        setRecordedService,
      ]}>
      {children}
      {debug && <StateMachinesDebug />}
    </StateMachineContext.Provider>
  );
}
