import {
  OnboardingContext,
  OnboardingEvent,
  OnboardingSchema,
} from 'sanstatecharts-onboarding';
import React from 'react';
import { Interpreter } from 'xstate';

export type RecordedServices = { [key: string]: Interpreter<any> };

export const StateMachineContext = React.createContext<
  | undefined
  | [
      Interpreter<OnboardingContext, OnboardingSchema, OnboardingEvent>,
      boolean,
      RecordedServices,
      (name: string, service: Interpreter<any, any, any>) => void,
    ]
>(undefined);
