import { useCallback, useState } from 'react';
import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';
import SelectionList from './SelectionList';
import { ReactComponent as Chevron } from 'assets/chevron.svg';
import { countriesData, CountryInfo } from 'lib/countries';
import { useUncontrolledInput } from 'lib/useUncontrolledInput';

const StyledInput = styled.input`
  color: ${({ theme: { color } }) => color.input};
  border: none;
  padding: 0;
  background-color: transparent;
  margin-left: ${({ theme: { spacing } }) => spacing.s2};
  &:disabled {
    border-bottom: 1px solid ${({ theme: { color } }) => color.borderBlue};
    color: ${({ theme: { color } }) => color.superLightGrey};
  }
  flex: 1;
  line-height: 1.5;
`;

const Container = styled.div`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  width: 100%;
`;
const ContainerError = styled(Container)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
`;
const InputContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;
const ButtonFlag = styled.span`
  font-size: ${({ theme: { type } }) => type.content.xl};
  margin-right: ${({ theme: { spacing } }) => spacing.s1};
  line-height: 1.25;
`;
const ButtonNumber = styled.span`
  margin-left: ${({ theme: { spacing } }) => spacing.s1};
  font-family: ${({ theme: { font } }) => font.regular.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  color: ${({ theme: { color } }) => color.content};
`;
const DropdownButton = styled.button`
  border: none;
  background: transparent;
  cursor: pointer;
  margin: 0;
  padding: ${({ theme: { spacing } }) => `0 ${spacing.s2} 0 ${spacing.s3}`};
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  text-align: left;
  color: ${({ theme: { color } }) => color.teal};
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const countriesToOptions = (
  a: Record<string, string>,
  { ISOcode, flag, countryName, countryDiallingCode }: CountryInfo,
) => ({
  ...a,
  [ISOcode]: `${flag} ${countryName} ${countryDiallingCode}`,
});

const countryMatchesString = (value: string) => ({
  countryDiallingCode,
}: CountryInfo) => value.includes(countryDiallingCode || '');

const countryMatchesIso = (value: string) => ({ ISOcode }: CountryInfo) =>
  value === ISOcode;

const isUK = (c: CountryInfo) =>
  c.countryDiallingCode === countriesData[0]!.countryDiallingCode;

export default function PhoneInput({
  onChange,
  name,
  value,
  placeholder,
  error,
  autoFocus = false,
  disabled = false,
}: {
  onChange: (s: string) => void;
  name: string;
  value: string;
  placeholder?: string;
  error?: ValidationError;
  disabled?: boolean;
  autoFocus?: boolean;
}) {
  const [dropdown, setDropDown] = useState(false);
  const [selectedCountry, setSelectedCountry] = useState(
    () => countriesData.find(countryMatchesString(value)) || countriesData[0]!,
  );
  const inputValue = value
    .replace(selectedCountry.countryDiallingCode, '')
    .trim();
  const numberChange = useCallback(
    (value: string) =>
      onChange(`${selectedCountry.countryDiallingCode}${value}`),
    [onChange, selectedCountry.countryDiallingCode],
  );
  const inputProps = useUncontrolledInput(
    isUK(selectedCountry) &&
      inputValue.length > 0 &&
      inputValue.charAt(0) !== '0'
      ? `0${inputValue}`
      : inputValue,
    numberChange,
  );
  const Wrapper = error ? ContainerError : Container;
  return (
    <Wrapper>
      {dropdown ? (
        <SelectionList
          name={name}
          options={countriesData.reduce(countriesToOptions, {})}
          onChange={(iso) => {
            const newCountry =
              countriesData.find(countryMatchesIso(iso)) || countriesData[0]!;
            const stripped = value.replace(
              selectedCountry.countryDiallingCode,
              '',
            );
            onChange(`${newCountry.countryDiallingCode}${stripped}`);
            setSelectedCountry(
              countriesData.find(countryMatchesIso(iso)) || countriesData[0]!,
            );
            setDropDown(false);
          }}
          onCancel={() => setDropDown(false)}
          disabled={disabled}
        />
      ) : (
        <InputContainer>
          <DropdownButton
            aria-label="Show countries"
            data-testid={`${name}Countries`}
            type="button"
            onKeyDown={(e: React.KeyboardEvent) => { 
              e.preventDefault(); // otherwise it will bubble to the onChange in <SelectionList>
              setDropDown(true);
            }}
            onClick={(e:  React.MouseEvent) => {
              e.preventDefault(); // otherwise it will bubble to the onChange in <SelectionList>
              setDropDown(true)
            }}
            disabled={disabled}>
            <ButtonFlag>{selectedCountry.flag}</ButtonFlag>
            <Chevron />
            <ButtonNumber>{selectedCountry.countryDiallingCode}</ButtonNumber>
          </DropdownButton>
          <StyledInput
            {...inputProps}
            id={name}
            type="tel"
            // https://en.wikipedia.org/wiki/Telephone_numbering_plan
            // (country code, 3 max) + (NDC + SN, 12 max)
            maxLength={12} 
            data-testid={name}
            placeholder={placeholder}
            autoFocus={autoFocus}
            inputMode="numeric"
            pattern="[0-9]*"
          />
        </InputContainer>
      )}
    </Wrapper>
  );
}
