import React, { MutableRefObject, useEffect, useMemo, useState } from 'react';
import { Interpreter, Machine } from 'xstate';
import styled from 'styled-components';
import {
  AddressContext,
  AddressEvent,
  AddressSchema,
  addressMachine,
  startService,
  Address,
  stateMatches,
  defaultAddressContext,
  logToConsole,
  addressValidationSchema,
  emptyAddress,
  addressResponseToAddress,
  ValidationError,
  noErrors,
} from 'sanstatecharts-onboarding';
import Spinner from 'components/Spinner/Spinner';
import { PostcodeLookupAddresses } from 'services/address';
import usePrevious from 'lib/usePrevious';
import Label from './Label';
import SelectionList from './SelectionList';
import StandardInput from './StandardInput';
import FormLink from './FormLink';
import useXstate from 'lib/useXstate';

const StyledAddressBlock = styled.div`
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  background: ${({ theme: { color } }) => color.white};
  padding: ${({ theme: { spacing } }) => spacing.s3};
  margin-top: ${({ theme: { spacing } }) => spacing.s3};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
`;
const StyledAddressDetails = styled.p`
  margin: 0;
`;
const ManualEntry = styled.div`
  margin-top: ${({ theme: { spacing } }) => spacing.s4};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
`;
const StyledResultsBlock = styled.div`
  padding-left: ${({ theme: { spacing } }) => spacing.s3};
  margin-top: -${({ theme: { spacing } }) => spacing.s4half};
  position: relative;
`;
const NoResultsBlock = styled.div`
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  background: ${({ theme: { color } }) => color.white};
  padding: ${({ theme: { spacing } }) => spacing.s4};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
`;
const InlineSpinner = styled.div`
  display: flex;
  flex-direction: row;
  div + p {
    margin-left: ${({ theme: { spacing } }) => spacing.s4};
  }
`;
const Highlight = styled.span`
  color: ${({ theme: { color } }) => color.teal};
  font-weight: bold;
`;
const SearchingText = styled.p`
  margin: 0;
  padding: 0;
`;
const StyledSingleLineBlock = styled.div`
  margin: ${({ theme: { spacing } }) => spacing.s3} 0
    ${({ theme: { spacing } }) => spacing.s4};
`;

const notEmpty = (s: string | null | undefined) =>
  s !== null && s !== undefined && !!s.trim();
const addressToOption = (
  a: Record<string, string>,
  address: PostcodeLookupAddresses,
  index: number,
) => {
  return {
    ...a,
    [index.toString()]: [
      address.address_line_1,
      address.address_line_2,
      address.address_line_3,
      address.city || address.district_or_county,
      address.postal_code,
    ]
      .filter(notEmpty)
      .join(', '),
  };
};
const searchResultsToOptions = (addresses: PostcodeLookupAddresses[]) =>
  addresses.reduce(addressToOption, {});

type Props = {
  onChange: (a: Address) => void;
  name: string;
  title?: string;
  value: string | Address;
  disabled?: boolean;
  children?: React.ReactNode;
  error: (key?: keyof Address) => ValidationError | undefined;
  refFor: (key?: keyof Address) => MutableRefObject<any>;
};

export default function AddressInput({
  onChange,
  name,
  children,
  error,
  refFor,
  title = 'Address',
  value: selectedAddress,
  disabled = false,
}: Props) {
  const [firstLine] = useState(() =>
    typeof selectedAddress === 'string'
      ? selectedAddress
      : selectedAddress.postcode,
  );
  const [autoFocus, setAutoFocus] = useState(false);
  const service: Interpreter<
    AddressContext,
    AddressSchema,
    AddressEvent
  > = useMemo(() => {
    const s = startService(
      Machine(addressMachine).withContext({
        ...defaultAddressContext,
        line: firstLine,
      }),
    );
    if (process.env.REACT_APP_XSTATE_LOG && process.env.NODE_ENV !== 'test') {
      logToConsole(s);
    }
    return s;
  }, [firstLine]);
  const [state, send, canTransition] = useXstate(service);
  const addressWasFullyDefined = usePrevious(
    noErrors(addressValidationSchema, selectedAddress),
  );
  useEffect(() => {
    if (noErrors(addressValidationSchema, selectedAddress)) {
      if (
        canTransition({ type: 'SET_FIXED' }) &&
        stateMatches(state, 'singleLine')
      ) {
        send({ type: 'SET_FIXED' });
        setAutoFocus(true);
      }
    } else {
      if (
        canTransition({ type: 'RESET' }) &&
        addressWasFullyDefined &&
        JSON.stringify(selectedAddress) === JSON.stringify(emptyAddress)
      ) {
        send({ type: 'RESET' });
        setAutoFocus(true);
      }
    }
  }, [
    selectedAddress,
    send,
    service,
    state,
    addressWasFullyDefined,
    canTransition,
  ]);
  const searchResults =
    state.context.searchResults[state.context.line.trim().toUpperCase()] || [];
  const linedAddress =
    typeof selectedAddress === 'string' ? undefined : selectedAddress;
  function manualEntry() {
    send({ type: 'ENTER_MANUALLY' });
    onChange({ ...emptyAddress, postcode: state.context.line.trim() });
    setAutoFocus(true);
  }

  return (
    <>
      <Label error={error()} ref={refFor()}>
        {title}
      </Label>
      {stateMatches(state, 'singleLine') && (
        <StyledSingleLineBlock>
          <StandardInput
            id={`${name}line`}
            value={state.context.line}
            onChange={(value) => send({ type: 'UPDATE_LINE', value })}
            placeholder="Please type your full postcode"
            disabled={disabled}
            autoFocus={autoFocus}
            error={error()}
            controlled
          />
          {stateMatches(state, 'singleLine', 'search') && (
            <StyledResultsBlock>
              <NoResultsBlock>
                <InlineSpinner>
                  <Spinner size={1} />
                  <SearchingText>Searching for your address</SearchingText>
                </InlineSpinner>
              </NoResultsBlock>
            </StyledResultsBlock>
          )}
          {stateMatches(state, 'singleLine', 'searchError') && (
            <StyledResultsBlock>
              <NoResultsBlock>
                <SearchingText>
                  Oops, we couldn't retrieve results for{' '}
                  <Highlight>{state.context.line}</Highlight>
                </SearchingText>
              </NoResultsBlock>
            </StyledResultsBlock>
          )}
          {stateMatches(state, 'singleLine', 'results') && (
            <StyledResultsBlock>
              {searchResults.length === 0 ? (
                <NoResultsBlock>
                  <SearchingText>
                    There are no search results for{' '}
                    <Highlight>{state.context.line}</Highlight>
                  </SearchingText>
                </NoResultsBlock>
              ) : (
                <SelectionList
                  name={`${name}results`}
                  options={searchResultsToOptions(searchResults)}
                  onChange={(index) => {
                    const address = searchResults[parseInt(index)]!;
                    send({
                      type: 'SELECT_ADDRESS',
                      address,
                    });
                    onChange(
                      addressResponseToAddress({
                        ...address,
                        id: '',
                        city:
                          address.city ||
                          address.district_or_county ||
                          address.state_or_region,
                      }),
                    );
                  }}
                  onCancel={() => send({ type: 'DISMISS_RESULTS' })}
                />
              )}
            </StyledResultsBlock>
          )}
          {canTransition({ type: 'ENTER_MANUALLY' }) && (
            <FormLink id={`${name}EnterManually`} onClick={manualEntry}>
              Can't find your address?
            </FormLink>
          )}
        </StyledSingleLineBlock>
      )}
      {stateMatches(state, 'manualEntry', 'editing') && (
        <ManualEntry>
          <StandardInput
            id={`${name}address1`}
            placeholder="Address line 1"
            value={linedAddress?.addressLineOne}
            onChange={(addressLineOne) =>
              onChange({ ...emptyAddress, ...linedAddress, addressLineOne })
            }
            autoFocus={autoFocus}
            error={error('addressLineOne')}
            ref={refFor('addressLineOne')}
          />
          <StandardInput
            id={`${name}address2`}
            placeholder="Address line 2 (optional)"
            value={linedAddress?.addressLineTwo}
            onChange={(addressLineTwo) =>
              onChange({ ...emptyAddress, ...linedAddress, addressLineTwo })
            }
            error={error('addressLineTwo')}
            ref={refFor('addressLineTwo')}
          />
          <StandardInput
            id={`${name}address3`}
            placeholder="Address line 3 (optional)"
            value={linedAddress?.addressLineThree}
            onChange={(addressLineThree) =>
              onChange({ ...emptyAddress, ...linedAddress, addressLineThree })
            }
            error={error('addressLineThree')}
            ref={refFor('addressLineThree')}
          />
          <StandardInput
            id={`${name}city`}
            placeholder="City"
            value={linedAddress?.city}
            onChange={(city) =>
              onChange({ ...emptyAddress, ...linedAddress, city })
            }
            error={error('city')}
            ref={refFor('city')}
          />
          <StandardInput
            id={`${name}postcode`}
            placeholder="Postcode"
            value={linedAddress?.postcode}
            onChange={(postcode) =>
              onChange({ ...emptyAddress, ...linedAddress, postcode })
            }
            error={error('postcode')}
            ref={refFor('postcode')}
          />
          {children}
        </ManualEntry>
      )}
      {stateMatches(state, 'manualEntry', 'fixed') && (
        <StyledAddressBlock>
          <StyledAddressDetails
            onClick={() => send({ type: 'ENTER_MANUALLY' })}>
            {typeof selectedAddress === 'string' ? (
              selectedAddress
            ) : (
              <>
                {[
                  selectedAddress.addressLineOne,
                  selectedAddress.addressLineTwo,
                  selectedAddress.addressLineThree,
                ]
                  .filter(notEmpty)
                  .filter(notEmpty)
                  .map((line, index) => (
                    <React.Fragment key={line}>
                      {line},<br />
                    </React.Fragment>
                  ))}
                {selectedAddress.city}, {selectedAddress.postcode}
              </>
            )}
          </StyledAddressDetails>
          {children}
        </StyledAddressBlock>
      )}
    </>
  );
}
