/* eslint-disable new-cap */

// AES encryption via Cipher-Block Chaining
// https://github.com/ricmoo/aes-js#cbc---cipher-block-chaining-recommended

import aesjs from 'aes-js';
import randomBytes from './randomBytes';

let installTime = localStorage.getItem('install');
if (!installTime) {
  installTime = Date.now().toString();
  localStorage.setItem('install', installTime);
}

const key = randomBytes(parseInt(installTime, 10), 32);

export function encrypt(item: any) {
  let stringified = encodeURI(JSON.stringify(item) || 'undefined');
  const byteLength = aesjs.utils.utf8.toBytes(stringified).length;
  const padding = byteLength % 16;
  if (padding) {
    stringified = stringified.padEnd(byteLength + 16 - padding, '=');
  }
  const inBytes = aesjs.utils.utf8.toBytes(stringified);
  const iv = randomBytes(Date.now(), 16);
  const ivString = iv.reduce((a, n) => a + n.toString(16).padStart(2, '0'), '');
  try {
    const cbc = new aesjs.ModeOfOperation.cbc(key, iv);
    const encrypted = cbc.encrypt(inBytes);
    const encryptedHex = aesjs.utils.hex.fromBytes(encrypted);
    return `${encryptedHex}${ivString}`;
  } catch (err) {
    if (process.env.NODE_ENV !== 'test') {
      // eslint-disable-next-line no-console
      console.error(
        `encrypt() failed - please add \`expect(encrypt(JSON.parse(${stringified}))).not.toBe('undefined')\` to encrypt.test.ts`,
        err.message,
      );
    }
    return 'undefined';
  }
}

export function decrypt(encrypted: string | undefined) {
  if (encrypted === undefined || encrypted === null) {
    return encrypted;
  }
  const ivString = encrypted.substring(encrypted.length - 32);
  const iv = Array.from({ length: 16 }).map((_, i) =>
    parseInt(ivString.substring(i * 2, i * 2 + 2), 16),
  );
  const encryptedHex = encrypted.substring(0, encrypted.length - 32);
  const encryptedBytes = aesjs.utils.hex.toBytes(encryptedHex);
  try {
    const cbc = new aesjs.ModeOfOperation.cbc(key, iv);
    const decryptedBytes = cbc.decrypt(encryptedBytes);
    const decrypted = aesjs.utils.utf8
      .fromBytes(decryptedBytes)
      .replace(/=+$/, '');
    try {
      return JSON.parse(decodeURI(decrypted));
    } catch (err) {
      return decrypted === 'undefined' ? undefined : decodeURI(decrypted);
    }
  } catch (err) {
    if (process.env.NODE_ENV !== 'test') {
      // eslint-disable-next-line no-console
      console.error(
        `decrypt() failed - please add \`expect(decrypt(${encrypted})).not.toBeUndefined()\` to encrypt.test.ts`,
        err.message,
      );
    }
    return undefined;
  }
}
