import styled from 'styled-components';

const StyledLink = styled.button`
  border: none;
  background: transparent;
  color: ${({ theme: { color } }) => color.linkColor};
  &:disabled {
    color: ${({ theme: { color } }) => color.linkHover};
  }
  cursor: pointer;
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s3};
  text-decoration: underline;
  padding: 0;
`;

const StyledContainerCentred = styled.div`
  margin-top: ${({ theme: { spacing } }) => spacing.s3};
  text-align: center;
`;
const StyledContainer = styled.div``;

export default function ModalLink({
  children,
  onClick,
  disabled = false,
  centred = false,
}: {
  children: string;
  onClick: () => void;
  disabled?: boolean;
  centred?: boolean;
}) {
  const Container = centred ? StyledContainerCentred : StyledContainer;
  return (
    <Container>
      <StyledLink type="button" disabled={disabled} onClick={onClick}>
        {children}
      </StyledLink>
    </Container>
  );
}
