import { ReactComponent as Coffee } from 'assets/coffee.svg';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import PrimaryButton from 'components/Button/PrimaryButton';
import { stateMatches } from 'statelib/stateMatches';
import ModalLink from 'components/Modals/ModalLink';
import Modal from 'components/Modals/Modal';
import Heading from 'components/Text/Heading';
import BodyText from 'components/Text/BodyText';
import Page from 'components/Page/Page';
import WhyDoWeNeedThisInfo from 'modals/WhyDoWeNeedThisInfo';
import { useOnboarding } from 'lib/useOnboarding';
import Centred from 'components/Text/Centred';

export default function SettingsExpectations() {
  const [state, send, canTransition] = useOnboarding();
  return (
    <Page>
      <CenterBlockSVG>
        <Coffee />
      </CenterBlockSVG>
      <Centred>
        <Heading>It'll take 10 minutes to apply</Heading>
        <BodyText>
          Make sure you have your ID (e.g. passport and driving licence) to hand
        </BodyText>
      </Centred>
      <PrimaryButton
        onClick={() => send({ type: 'CONTINUE' })}
        status={!canTransition({ type: 'CONTINUE' }) ? 'DISABLED' : 'ACTIVE'}>
        Let's go
      </PrimaryButton>
      <ModalLink
        centred
        onClick={() => send({ type: 'WHY_DO_WE_NEED_THIS' })}
        disabled={!canTransition({ type: 'WHY_DO_WE_NEED_THIS' })}>
        Why do we need this info?
      </ModalLink>
      <Modal
        enabled={stateMatches(
          state,
          'settingsExpectations',
          'whyWeNeedThisInfo',
        )}>
        <WhyDoWeNeedThisInfo />
      </Modal>
    </Page>
  );
}
