import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';
import { useUncontrolledInput } from 'lib/useUncontrolledInput';
import { useCallback } from 'react';

type InputProps = Omit<
  React.InputHTMLAttributes<HTMLInputElement>,
  'onChange' | 'value' | 'id'
> & {
  onChange: (s: { month: number; year: number }) => void;
  value: { month: number; year: number };
  error?: ValidationError;
  id: string;
};

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
`;

const StyledLabel = styled.label`
  display: flex;
  flex-direction: column;
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  text-align: center;
  + label {
    margin-left: ${({ theme: { spacing } }) => spacing.s4};
  }
`;

const StyledInput = styled.input`
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  color: ${({ theme: { color } }) => color.input};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s2};
  padding: 0.5rem 0;
  text-align: center;
  &:disabled {
    background-color: transparent;
    border-bottom: 1px solid ${({ theme: { color } }) => color.borderBlue};
    color: ${({ theme: { color } }) => color.superLightGrey};
  }
`;

const StyledInput2 = styled(StyledInput)`
  width: 3rem;
`;

const StyledInput4 = styled(StyledInput)`
  width: 6rem;
`;

export default function MonthInput({
  onChange,
  value,
  error,
  id,
  ...props
}: InputProps) {
  const { month, year } = value;
  const monthChange = useCallback(
    (value: string) =>
      onChange({
        month: parseInt(value) <= 12 ? parseInt(value) : month,
        year,
      }),
    [month, onChange, year],
  );
  const yearChange = useCallback(
    (value: string) =>
      onChange({
        month,
        year:
          parseInt(value) <= new Date().getFullYear() ? parseInt(value) : year,
      }),
    [month, onChange, year],
  );
  const inputPropsMonth = useUncontrolledInput(
    month === 0 ? '' : month.toString(),
    monthChange,
  );
  const inputPropsYear = useUncontrolledInput(
    year === 0 ? '' : year.toString(),
    yearChange,
  );
  return (
    <Container>
      <StyledLabel>
        <StyledInput2
          {...props}
          {...inputPropsMonth}
          id={`${id}MM`}
          data-testid={`${id}MM`}
          maxLength={2}
          type="number"
        />
        MM
      </StyledLabel>
      <StyledLabel>
        <StyledInput4
          {...props}
          {...inputPropsYear}
          id={`${id}YYYY`}
          data-testid={`${id}YYYY`}
          maxLength={4}
          type="number"
        />
        YYYY
      </StyledLabel>
    </Container>
  );
}
