import { EventObject, Machine, MachineConfig, State } from 'xstate';
import { startService } from 'statelib/service';
import { Auth } from 'machines/magicLink.machine';
import { getItem } from './dependencies';
import { persistWatcher } from './watcher';

type PartialContext<Context extends { auth: Auth }> = {
  [key in keyof Context]?: Partial<Context[key]>;
};

const hydrateFromLocalStorage = <Context extends { auth: Auth }>(
  initialHydration: PartialContext<Context>,
) => (context: Context, hydrationKey: keyof Context): Context => {
  const json = getItem(hydrationKey as string);
  if (json === undefined && !initialHydration[hydrationKey]) return context;
  const item = json === undefined ? undefined : JSON.parse(json);
  return {
    ...context,
    [hydrationKey]:
      typeof item === 'string'
        ? item
        : {
            ...item,
            ...initialHydration[hydrationKey],
          },
  };
};

export function startOnboardingService<
  Context extends { auth: Auth },
  Schema,
  Event extends EventObject
>(
  machine: MachineConfig<Context, Schema, Event>,
  context: Context,
  initialHydration: PartialContext<Context>,
  persistKeys: Array<keyof Context>,
  initialState: State<Context, Event, Schema> | undefined,
) {
  const initialContext = persistKeys.reduce(
    hydrateFromLocalStorage(initialHydration),
    context,
  );
  const service = startService(
    Machine(machine).withContext(initialContext),
    initialState,
  );
  service.onChange(persistWatcher(persistKeys, initialContext));
  return service;
}
