import { Interpreter } from 'xstate';
import useXstate from 'lib/useXstate';
import styled from 'styled-components';
import {
  EmploymentContext,
  EmploymentEvent,
  EmploymentSchema,
  stateMatches,
  ValidationError,
} from 'sanstatecharts-onboarding';
import Spinner from 'components/Spinner/Spinner';
import { EmploymentTuple } from 'services/customer';
import SelectionList from './SelectionList';
import StandardInput from './StandardInput';

const StyledResultsBlock = styled.div`
  padding-left: ${({ theme: { spacing } }) => spacing.s3};
  margin-top: -${({ theme: { spacing } }) => spacing.s4half};
  position: relative;
`;
const NoResultsBlock = styled.div`
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  background: ${({ theme: { color } }) => color.white};
  padding: ${({ theme: { spacing } }) => spacing.s4};
`;
const InlineSpinner = styled.div`
  display: flex;
  flex-direction: row;
  div + p {
    margin-left: ${({ theme: { spacing } }) => spacing.s4};
  }
`;
const Highlight = styled.span`
  color: ${({ theme: { color } }) => color.teal};
  font-weight: bold;
`;
const SearchingText = styled.p`
  margin: 0;
  padding: 0;
`;
const StyledSingleLineBlock = styled.div`
  margin-top: ${({ theme: { spacing } }) => spacing.s3};
`;

const employmentToOption = (
  a: Record<string, string>,
  { name, code }: EmploymentTuple,
) => {
  return {
    ...a,
    [code]: name,
  };
};
const searchResultsToOptions = (employment: EmploymentTuple[]) =>
  employment.reduce(employmentToOption, {});
const matchesCode = (match: string) => ({ code }: EmploymentTuple) =>
  code === match;

type Props = {
  service: Interpreter<EmploymentContext, EmploymentSchema, EmploymentEvent>;
  onChange: (a: string | EmploymentTuple) => void;
  name?: string;
  value: string | EmploymentTuple;
  disabled?: boolean;
  error?: ValidationError;
  placeholder: string;
  searching: string;
};

export default function EmploymentInput({
  service,
  onChange,
  name = 'occupation',
  value: selectedEmployment,
  disabled = false,
  error,
  placeholder,
  searching,
}: Props) {
  const [state, send] = useXstate(service);
  const { line, searchResults } = state.context;
  const results = searchResults[line.trim().toUpperCase()] || [];
  return (
    <StyledSingleLineBlock>
      <StandardInput
        id={`${name}line`}
        value={line}
        onChange={(value) => send({ type: 'UPDATE_LINE', value })}
        placeholder={placeholder}
        disabled={disabled}
        error={error}
        controlled
      />
      {stateMatches(state, 'search') && (
        <StyledResultsBlock>
          <NoResultsBlock>
            <InlineSpinner>
              <Spinner size={1} />
              <SearchingText>{searching}</SearchingText>
            </InlineSpinner>
          </NoResultsBlock>
        </StyledResultsBlock>
      )}
      {stateMatches(state, 'searchError') && (
        <StyledResultsBlock>
          <NoResultsBlock>
            <SearchingText>
              Oops, we couldn't retrieve results for{' '}
              <Highlight>{line}</Highlight>
            </SearchingText>
          </NoResultsBlock>
        </StyledResultsBlock>
      )}
      {stateMatches(state, 'results') &&
        (results.length !== 1 ||
          typeof selectedEmployment === 'string' ||
          line !== selectedEmployment.name ||
          results[0]?.code !== selectedEmployment.code) && (
          <StyledResultsBlock>
            {results.length === 0 ? (
              <NoResultsBlock>
                <SearchingText>
                  There are no search results for <Highlight>{line}</Highlight>
                </SearchingText>
              </NoResultsBlock>
            ) : (
              <SelectionList
                name={`${name}Results`}
                options={searchResultsToOptions(results)}
                onChange={(code) => {
                  const result = results.find(matchesCode(code));
                  if (result) {
                    send({
                      type: 'SELECT_RESULT',
                      result,
                    });
                    onChange({ ...result });
                  }
                }}
                onCancel={() => send({ type: 'DISMISS_RESULTS' })}
              />
            )}
          </StyledResultsBlock>
        )}
    </StyledSingleLineBlock>
  );
}
