import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import CountryInput from 'components/Form/CountryInput';
import { findError } from 'components/ValidationErrors/findError';
import FormLink from 'components/Form/FormLink';
import Radios from 'components/Form/Radios';
import { countriesData, countriesOfBirthAndIncome } from 'lib/countries';
import StandardInput from 'components/Form/StandardInput';
import React from 'react';

type Values = Pick<OnboardingContext['aboutYou'], 'taxResidenceData'>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  aboutYou: Values;
  disabled?: boolean;
};

export default function TaxStatusFields({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
  disabled = false,
}: Props) {
  const showTaxResidenceDataInput =
    aboutYou.taxResidenceData.length > 1 ||
    (aboutYou.taxResidenceData.length === 1 &&
      aboutYou.taxResidenceData[0]?.country_of_tax_residence !==
        countriesData[0]!.ISOcode);

  return (
    <>
      <Label
        error={shownErrors['taxResidenceData']}
        ref={refFor('taxResidenceData')}>
        Are you obliged to submit a personal tax return in another country
        because of residence or citizenship?
        <Radios
          name="taxCountriesForeign"
          options={['Yes', 'No']}
          onChange={(value) => {
            if (value === 'Yes') {
              console.log('value is yes');
              console.log(aboutYou.taxResidenceData);
              if (aboutYou.taxResidenceData.length === 0) {
                console.log('adding residence because length is 0');
                send({ type: 'ADD_TAX_RESIDENCE_DATA' });
                console.log(aboutYou.taxResidenceData);
              } else {
                console.log('changing residence because not 0 and yes');
                send({
                  type: 'UPDATE_TAX_RESIDENCE_DATA',
                  index: 0,
                  value: {
                    country_of_tax_residence: '',
                    tax_identification_number: null,
                  },
                });
              }
            }
            if (value === 'No') {
              console.log('value is no');
              console.log(aboutYou.taxResidenceData);
              if (aboutYou.taxResidenceData.length === 0) {
                console.log('adding new residence as length 0');
                send({ type: 'ADD_TAX_RESIDENCE_DATA' });
              }
              if (aboutYou.taxResidenceData.length > 1) {
                console.log('deleting all old residences');
                Array.from({
                  length: aboutYou.taxResidenceData.length - 1,
                }).forEach(() =>
                  send({ type: 'REMOVE_TAX_RESIDENCE_DATA', index: 1 }),
                );
              }
              console.log('updating tax residence to be GB');
              send({
                type: 'UPDATE_TAX_RESIDENCE_DATA',
                index: 0,
                value: {
                  country_of_tax_residence: countriesData[0]!.ISOcode,
                  tax_identification_number: null,
                },
              });
            }
          }}
          disabled={disabled}
          value={
            aboutYou.taxResidenceData.length === 0
              ? undefined
              : aboutYou.taxResidenceData.length === 1 &&
                aboutYou.taxResidenceData[0]?.country_of_tax_residence ===
                  countriesData[0]!.ISOcode
              ? 'No'
              : 'Yes'
          }
          error={shownErrors['taxResidenceData']}
        />
      </Label>
      {showTaxResidenceDataInput &&
        aboutYou.taxResidenceData?.map((taxResidenceEntry, index) => (
          <React.Fragment key={`taxResidenceEntry${index}`}>
            <Label
              error={shownErrors['taxResidenceData']}
              ref={refFor('taxResidenceData', index)}>
              Country of tax residence
              <CountryInput
                key={`taxCountry${index}`}
                name={`taxCountry${index}`}
                placeholder={
                  index === aboutYou.taxResidenceData.length - 1
                    ? 'Please select...'
                    : undefined
                }
                dataSource={countriesOfBirthAndIncome}
                onChange={(country: string) =>
                  send({
                    type: 'UPDATE_TAX_RESIDENCE_DATA',
                    index,
                    value: {
                      ...taxResidenceEntry,
                      country_of_tax_residence: country,
                    },
                  })
                }
                disabled={disabled}
                value={taxResidenceEntry.country_of_tax_residence}
                error={findError(
                  shownErrors,
                  'taxResidenceData',
                  index,
                  'country_of_tax_residence',
                )}
                ref={refFor('taxResidenceData', index)}
                filterOut={[
                  countriesData[0]!.ISOcode,
                  ...aboutYou.taxResidenceData.map(
                    (entry) => entry.country_of_tax_residence,
                  ),
                ]}></CountryInput>
            </Label>
            <Label
              error={findError(
                shownErrors,
                'taxResidenceData',
                index,
                'tax_identification_number',
              )}>
              Tax identification number (TIN)
              <StandardInput
                id={`taxIdentificationNumber${index}`}
                placeholder="Enter your tax identification number (TIN)"
                onChange={(tin) =>
                  send({
                    type: 'UPDATE_TAX_RESIDENCE_DATA',
                    index,
                    value: {
                      ...taxResidenceEntry,
                      tax_identification_number: tin,
                    },
                  })
                }
                disabled={disabled}
                value={taxResidenceEntry.tax_identification_number || ''}
                error={findError(shownErrors, 'taxResidenceData', index)}
                maxLength={20}
              />
            </Label>
            {canTransition({ type: 'REMOVE_TAX_RESIDENCE_DATA', index }) && (
              <FormLink
                id={`removeTaxResidenceEntry${index}`}
                inline
                onClick={() =>
                  send({ type: 'REMOVE_TAX_RESIDENCE_DATA', index })
                }>
                Remove
              </FormLink>
            )}
          </React.Fragment>
        ))}
      {canTransition({ type: 'ADD_TAX_RESIDENCE_DATA' }) &&
        aboutYou.taxResidenceData.length !== 0 && (
          <FormLink
            id="addTaxResidenceData"
            onClick={() => send({ type: 'ADD_TAX_RESIDENCE_DATA' })}>
            Add another
          </FormLink>
        )}
    </>
  );
}
