import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import CountryInput from 'components/Form/CountryInput';
import { countriesOfBirthAndIncome } from 'lib/countries';

type Values = Pick<OnboardingContext['aboutYou'], 'countryOfBirth'>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  aboutYou: Values;
};

export default function CountryOfBirthFields({
  shownErrors,
  aboutYou,
  send,
  refFor,
}: Props) {
  return (
    <>
      <Label
        error={shownErrors['countryOfBirth']}
        ref={refFor('countryOfBirth')}>
        Country of birth
        <CountryInput
          name="countryOfBirth"
          placeholder={
            aboutYou.countryOfBirth === '' ? 'Please select...' : undefined
          }
          onChange={(value: string) =>
            send({ type: 'UPDATE_ABOUT_YOU', field: 'countryOfBirth', value })
          }
          value={aboutYou.countryOfBirth}
          error={shownErrors['countryOfBirth']}
          ref={refFor('countryOfBirth')}
          dataSource={countriesOfBirthAndIncome}></CountryInput>
      </Label>
    </>
  );
}
