import { magicLinkMachine } from 'machines/magicLink.machine';
import { useStatechart, useStatechartIfExists } from 'state-machines/hooks';
import { MachineConfig } from 'xstate';

export function useMagicLink<
  Config extends MachineConfig<any, any, any> = typeof magicLinkMachine
>() {
  return useStatechart<Config>('MAGIC_LINK');
}

export function useMagicLinkIfExists<
  Config extends MachineConfig<any, any, any> = typeof magicLinkMachine
>() {
  return useStatechartIfExists<Config>('MAGIC_LINK');
}
