import { bcaOnboardingMachine } from 'sanstatecharts-onboarding';
import { useStatechart } from 'state-machines/hooks';
import { MachineConfig } from 'xstate';

export function useBCAOnboarding<
  Config extends MachineConfig<any, any, any> = typeof bcaOnboardingMachine
>() {
  return useStatechart<Config>('onboarding');
}
