import styled from 'styled-components';
import PrimaryButton from 'components/Button/PrimaryButton';
import PersonUpdateError from 'components/PersonUpdateError/PersonUpdateError';
import ValidationErrors from 'components/ValidationErrors/ValidationErrors';
import { useOnboarding } from 'lib/useOnboarding';
import { useState } from 'react';
import { Errors } from 'statelib/validate';

const Container = styled.div`
  margin-top: ${({ theme: { spacing } }) => spacing.s5};
`;

export default function ContinueButton<Values extends object>({
  status,
  validationErrors,
  onScrollToError,
  setErrorsVisible,
  hideErrorBlock = false,
}: {
  status: 'editing' | 'updating' | 'updateError' | undefined;
  validationErrors: Errors<Values>;
  onScrollToError?: (
    field: keyof Values,
    index?: number,
    itemField?: string,
  ) => void;
  setErrorsVisible: () => void;
  hideErrorBlock?: boolean;
}) {
  const [, send, canTransition] = useOnboarding();
  const [errors, setErrors] = useState(false);
  const canContinue = canTransition({ type: 'CONTINUE' });
  const onClick = canContinue
    ? () => send({ type: 'CONTINUE' })
    : () => {
        setErrors(true);
        setErrorsVisible();
      };
  return (
    <Container>
      {errors && !hideErrorBlock && (
        <ValidationErrors
          validationErrors={validationErrors}
          onScrollToError={onScrollToError}
        />
      )}
      <PersonUpdateError enabled={status === 'updateError'} />
      <PrimaryButton
        testId="continue"
        type="submit"
        onClick={onClick}
        status={
          status === 'updating'
            ? 'SUBMITTING'
            : status === 'editing' || status === undefined
            ? 'ACTIVE'
            : 'DISABLED'
        }>
        Continue
      </PrimaryButton>
    </Container>
  );
}
