import { assign, DoneInvokeEvent, MachineConfig } from 'xstate';
import { escalate, sendParent } from 'xstate/lib/actions';
import queryString from 'query-string';
import { noErrors } from 'statelib/validate';
import jwtDecode from 'jwt-decode';
import {
  authenticateUserRequest,
  registerUserRequest,
  setTokens,
} from 'services/auth';
import { emailValidationSchema } from './validation/emailValidation';
import { getEnv } from '../environment';

export const defaultAuth = {
  initialUrl: '',
  userId: '',
  sessionId: '',
};

export type Auth = typeof defaultAuth;

export const defaultMagicLinkContext = {
  auth: { ...defaultAuth },
  emailAddress: '',
};

export type MagicLinkContext = typeof defaultMagicLinkContext;

export type MagicLinkSchema = {
  context: MagicLinkContext;
  states: {
    hydrate: {};
    validateMagicLink: {};
    enterEmail: {
      states: {
        editing: {};
        resendEmail: {};
      };
    };
    sendMagicLink: {};
    weveSentAnEmail: {};
    invalidMagicLink: {};
    userRetrieved: {};
    error: {};
  };
};

type Continue = { type: 'CONTINUE' };
type Back = { type: 'BACK' };
type Close = { type: 'CLOSE' };
type ResendEmail = { type: 'RESEND_EMAIL' };
type UpdateEmail = { type: 'UPDATE_EMAIL'; emailAddress: string };
type Depromisified<T> = T extends Promise<infer R> ? R : never;

type DoneInvokeValidateMagicLink = {
  type: 'done.invoke.VALIDATE_MAGIC_LINK';
  data: Depromisified<ReturnType<typeof magicLinkServices.validateMagicLink>>;
};

type DoneInvokeSendMagicLink = {
  type: 'done.invoke.SEND_MAGIC_LINK';
  data: Depromisified<ReturnType<typeof magicLinkServices.sendMagicLink>>;
};

export type SetAuth = { type: 'SET_AUTH'; auth: Auth };

export type SetEmailAddress = {
  type: 'SET_EMAIL_ADDRESS';
  emailAddress: string;
};

export type MagicLinkEvent =
  | Continue
  | Back
  | Close
  | ResendEmail
  | UpdateEmail
  | DoneInvokeValidateMagicLink
  | DoneInvokeSendMagicLink;

const guards = {
  hasMagicLink: ({ auth: { initialUrl } }: MagicLinkContext) =>
    !!queryString.parse(initialUrl).token,
  hasPersistedUser: ({ auth: { userId, sessionId } }: MagicLinkContext) =>
    !!userId && !!sessionId,
  emailOk: ({ emailAddress }: MagicLinkContext) =>
    noErrors(emailValidationSchema, emailAddress),
};

type MagicLinkAuth = {
  accessToken: string;
  accessTokenExpiry: Date;
  refreshToken: string;
  sessionId: string;
  userId: string;
};

const noTokenExpiry =
  process.env.REACT_APP_NO_TOKEN_EXPIRY !== undefined &&
  process.env.REACT_APP_NO_TOKEN_EXPIRY !== 'false';

export const magicLinkServices = {
  validateMagicLink: async ({
    auth: { initialUrl, sessionId },
    emailAddress,
  }: MagicLinkContext): Promise<MagicLinkAuth> => {
    const responseToken = queryString.parse(initialUrl).token as string;
    const decodedToken = jwtDecode<{ exp: number; email: string }>(
      responseToken,
    );
    if (
      !sessionId ||
      (!noTokenExpiry && Date.now() / 1000 > decodedToken.exp)
    ) {
      throw new Error('magic link has expired');
    }
    if (emailAddress !== decodedToken.email) {
      throw new Error('Email address does not match');
    }
    return authenticateUserRequest({
      responseToken,
      sessionId,
    }).then(({ data }) => {
      const accessTokenExpiry = new Date(Date.now());
      accessTokenExpiry.setSeconds(
        accessTokenExpiry.getSeconds() + data.access_token_expiry,
      );
      const tokens = {
        accessToken: data.access_token,
        accessTokenExpiry,
        refreshToken: data.refresh_token,
        sessionId,
        userId: data.customer_id,
      };
      setTokens(tokens);
      return tokens;
    });
  },
  sendMagicLink: async ({ emailAddress }: MagicLinkContext) =>
    registerUserRequest({
      email: emailAddress,
      termsVersion: getEnv('registrationTermsVersion'),
      attribution: {
        product_key: getEnv('registrationAttribution').productKey,
        channel_key: getEnv('registrationAttribution').channelKey,
      },
    }),
};

const actions = {
  assignMagicLinkUser: assign(
    (
      { auth }: MagicLinkContext,
      {
        data: { userId, sessionId },
      }: DoneInvokeEvent<DoneInvokeValidateMagicLink['data']>,
    ) => ({
      auth: {
        ...auth,
        userId,
        sessionId,
      },
      emailAddress: '',
    }),
  ),
  assignEmail: assign(
    (_context: MagicLinkContext, { emailAddress }: UpdateEmail) => ({
      emailAddress,
    }),
  ),
  assignSessionId: assign(
    (
      { auth }: MagicLinkContext,
      {
        data: {
          data: { session_id },
        },
      }: DoneInvokeEvent<DoneInvokeSendMagicLink['data']>,
    ) => ({
      auth: {
        ...auth,
        sessionId: session_id,
      },
    }),
  ),
  sendSessionIdToParent: sendParent<
    MagicLinkContext,
    DoneInvokeEvent<DoneInvokeSendMagicLink['data']>,
    SetAuth
  >(({ auth }, { data: { data: { session_id } } }) => ({
    type: 'SET_AUTH',
    auth: {
      ...auth,
      sessionId: session_id,
    },
  })),
  sendEmailAddressToParent: sendParent<
    MagicLinkContext,
    DoneInvokeEvent<DoneInvokeSendMagicLink['data']>,
    SetEmailAddress
  >(({ emailAddress }) => ({
    type: 'SET_EMAIL_ADDRESS',
    emailAddress,
  })),
  failure: escalate<MagicLinkContext, MagicLinkEvent, MagicLinkEvent>(
    (_context, event) => event,
  ),
};

const data = ({ auth }: MagicLinkContext): Auth => auth;

export const magicLinkMachine: MachineConfig<
  MagicLinkContext,
  MagicLinkSchema,
  MagicLinkEvent
> = {
  initial: 'hydrate',
  context: defaultMagicLinkContext,
  states: {
    hydrate: {
      always: [
        {
          target: 'validateMagicLink',
          cond: guards.hasMagicLink,
        },
        {
          target: 'userRetrieved',
          cond: guards.hasPersistedUser,
        },
        { target: 'enterEmail' },
      ],
    },
    validateMagicLink: {
      invoke: {
        id: 'VALIDATE_MAGIC_LINK',
        src: magicLinkServices.validateMagicLink,
        onDone: {
          target: 'userRetrieved',
          actions: actions.assignMagicLinkUser,
        },
        onError: 'invalidMagicLink',
      },
    },
    enterEmail: {
      initial: 'editing',
      states: {
        editing: {},
        resendEmail: {},
      },
      on: {
        UPDATE_EMAIL: { actions: actions.assignEmail },
        CONTINUE: {
          target: 'sendMagicLink',
          cond: guards.emailOk,
        },
      },
    },
    sendMagicLink: {
      invoke: {
        id: 'SEND_MAGIC_LINK',
        src: magicLinkServices.sendMagicLink,
        onDone: {
          target: 'weveSentAnEmail',
          actions: [
            actions.assignSessionId,
            actions.sendSessionIdToParent,
            actions.sendEmailAddressToParent,
          ],
        },
        onError: 'error',
      },
    },
    weveSentAnEmail: {
      on: {
        RESEND_EMAIL: 'enterEmail.resendEmail',
        BACK: 'enterEmail.resendEmail',
      },
    },
    invalidMagicLink: {
      on: {
        RESEND_EMAIL: 'enterEmail.resendEmail',
        BACK: 'enterEmail.resendEmail',
      },
    },
    userRetrieved: {
      type: 'final',
      data,
    },
    error: {
      entry: actions.failure,
      on: { RESEND_EMAIL: 'enterEmail.resendEmail' },
    },
  },
};
