import BackArrow from 'components/BackArrow/BackArrow';
import QuestionMark from 'components/QuestionMark/QuestionMark';
import { useContext } from 'react';
import styled from 'styled-components';
import { StateMachineContext } from 'state-machines/StateMachineContext';
import { assertIsDefined } from 'statelib/assert';
import { useOnboarding } from 'lib/useOnboarding';
import Centred from 'components/Text/Centred';

const StyledProgressBarContainer = styled.div`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s5};
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`;

type Props = {
  title: string;
  inline?: boolean;
};

export default function TopBar({ title, inline }: Props) {
  const stateContext = useContext(StateMachineContext);
  const [, send, canTransition] = useOnboarding();
  assertIsDefined(
    stateContext,
    '<TopBar /> must be used within <StateMachines />',
  );

  return (
    <StyledProgressBarContainer>
      {!inline && <BackArrow send={send} canTransition={canTransition} />}
      <Centred>{title}</Centred>
      <QuestionMark />
    </StyledProgressBarContainer>
  );
}
