import Spinner from 'components/Spinner/Spinner';
import React from 'react';
import { StyledBaseButton } from './Button.styles';

type Props = Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, 'disabled'> & {
  children: string;
  testId?: string;
  status?: 'ACTIVE' | 'DISABLED' | 'SUBMITTING';
};

export default function SecondaryButton({
  children: title,
  testId,
  status = 'ACTIVE',
  ...props
}: Props) {
  return (
    <StyledBaseButton
      type="button"
      className="secondary"
      {...props}
      disabled={status === 'DISABLED'}
      data-testid={testId || 'SecondaryButton'}>
      {status === 'SUBMITTING' ? <Spinner size={1} /> : title}
    </StyledBaseButton>
  );
}
