type SessionIDMessage = {
  idvSessionId: string;
}
declare let window: {
  astoWebView: {
    postMessage: (message: string) => void;
  };
  webkit: {
    messageHandlers: {
      astoWebView: {
        postMessage: (message: string) => void;
      };
    }
  },
  MSStream: undefined | Object;
};

const getMobileOperatingSystem = () => {
  const userAgent = navigator.userAgent || navigator.vendor;

  if (/android/i.test(userAgent)) {
    return 'Android';
  }

  // iOS detection from: http://stackoverflow.com/a/9039885/177710
  if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
    return 'iOS';
  }

  return 'unknown';
};

function postToSdk(message: SessionIDMessage) {
  const stringifiedMessage = JSON.stringify(message);
  const oS = getMobileOperatingSystem();
  if (oS === 'Android') {
    window.astoWebView?.postMessage(stringifiedMessage);
  } else if (oS === 'iOS') {
    window.webkit?.messageHandlers.astoWebView.postMessage(stringifiedMessage);
  } else {
    // normal browser ?
  }
}

export function sendSessionIdToSdk(sessionId: string) {
  postToSdk({ idvSessionId: sessionId })
}
