import Spinner from 'components/Spinner/Spinner';
import React, { forwardRef } from 'react';
import { StyledBaseButton } from './Button.styles';

type Props = Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, 'disabled'> & {
  children: string;
  testId?: string;
  status?: 'ACTIVE' | 'DISABLED' | 'SUBMITTING';
};

function PrimaryButton(
  { children: title, testId, status = 'ACTIVE', ...props }: Props,
  ref?: React.Ref<HTMLButtonElement>,
) {
  return (
    <StyledBaseButton
      ref={ref}
      type="button"
      className="primary"
      {...props}
      disabled={status === 'DISABLED'}
      data-testid={testId || 'PrimaryButton'}>
      {status === 'SUBMITTING' ? <Spinner colour="white" size={1} /> : title}
    </StyledBaseButton>
  );
}

export default forwardRef(PrimaryButton);
