import { stateMatches } from 'statelib/stateMatches';
import { useOnboarding } from 'lib/useOnboarding';
import AboutYou from 'pages/AboutYou';
import Account from 'pages/Account';
import Congratulations from 'pages/Congratulations';
import ContactDetails from 'pages/ContactDetails';
import Error from 'pages/Error';
import Finances from 'pages/Finances';
import GeneralTerms from 'pages/GeneralTerms';
import IdandV from 'pages/IdandV';
import IdentityFailure from 'pages/IdentityFailure';
import KeyFacts from 'pages/KeyFacts';
import LegalBit from 'pages/LegalBit';
import MarketingPreferences from 'pages/MarketingPreferences';
import Overdraft from 'pages/Overdraft';
import ProductFinal from 'pages/ProductFinal';
import ProductInitial from 'pages/ProductInitial';
import RetrievalError from 'pages/RetrievalError';
import SettingsExpectations from 'pages/SettingsExpectations';
import StyledAppRouter from 'components/StyledAppRouter';
import ValidMagicLink from 'pages/ValidMagicLink';
import MagicLinkRouter from './MagicLinkRouter';
import RetrieveUser from 'pages/RetrieveUser';
import { useMagicLinkIfExists } from 'lib/useMagicLink';

export default function PCARouter() {
  const [state] = useOnboarding();
  const [mlState] = useMagicLinkIfExists();
  const validating =
    mlState &&
    (stateMatches(mlState, 'validateMagicLink') ||
      stateMatches(mlState, 'userRetrieved'));
  return (
    <StyledAppRouter>
      {stateMatches(state, 'settingsExpectations') && <SettingsExpectations />}
      {stateMatches(state, 'retrievalError') && <RetrievalError />}
      {stateMatches(state, 'magicLinkIsValid') && <ValidMagicLink />}
      {stateMatches(state, 'magicLink') && !validating && <MagicLinkRouter />}
      {(stateMatches(state, 'retrieveUser') || validating) && <RetrieveUser />}
      {stateMatches(state, 'productInitial') && <ProductInitial />}
      {stateMatches(state, 'steps', 'aboutYou') && <AboutYou />}
      {stateMatches(state, 'steps', 'contactDetails') && <ContactDetails />}
      {stateMatches(state, 'steps', 'finances') && <Finances />}
      {stateMatches(state, 'steps', 'account') && <Account />}
      {stateMatches(state, 'steps', 'overdraft') && <Overdraft />}
      {stateMatches(state, 'productFinal') && <ProductFinal />}
      {stateMatches(state, 'legalBit') && <LegalBit />}
      {stateMatches(state, 'keyFacts') && <KeyFacts />}
      {stateMatches(state, 'generalTerms') && <GeneralTerms />}
      {stateMatches(state, 'marketingPreferences') && <MarketingPreferences />}
      {stateMatches(state, 'congratulations') && <Congratulations />}
      {stateMatches(state, 'idandV') && <IdandV />}
      {stateMatches(state, 'identityFailure') && <IdentityFailure />}
      {stateMatches(state, 'error') && <Error />}
    </StyledAppRouter>
  );
}
