import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import Heading from 'components/Text/Heading';
import { useOnboarding } from 'lib/useOnboarding';
import { overdraftValidation, stateMatches } from 'sanstatecharts-onboarding';
import PersonUpdateError from 'components/PersonUpdateError/PersonUpdateError';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import Wallet from 'components/Wallet/Wallet';
import Form from 'components/Form/Form';
import Centred from 'components/Text/Centred';
import ContinueButton from 'components/Form/ContinueButton';
import { PCAStepState } from 'lib/StepState';
import { useValidation } from 'lib/useValidation';

export default function Overdraft() {
  const [state] = useOnboarding();
  const { overdraft } = state.context;
  // const disabled = !canTransition({
  //   type: 'UPDATE_OVERDRAFT',
  //   overdraft: 0,
  // });
  const validationErrors = overdraftValidation({ overdraft });
  const { ...validation } = useValidation(validationErrors);

  return (
    <Page>
      <ProgressBar step="overdraft" title="Your overdraft" />
      <CenterBlockSVG>
        <Wallet />
      </CenterBlockSVG>
      <Centred>
        <Heading>How much overdraft do you want?</Heading>
      </Centred>
      <Form>
        <PersonUpdateError
          enabled={stateMatches(state, 'steps', 'account', 'updateError')}
        />
        <ContinueButton
          validationErrors={validationErrors}
          {...validation}
          status={(state.value as PCAStepState)?.steps?.overdraft}
        />
      </Form>
    </Page>
  );
}
