import React from 'react';
import styled from 'styled-components';

const StyledLi = styled.li`
  list-style: none;
  padding-left: ${({ theme: { spacing } }) => spacing.s4half};
  position: relative;
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s3};
  &:before {
    position: absolute;
    left: 0;
    top: ${({ theme: { spacing } }) => spacing.s3};
    display: block;
    border-radius: 0.8rem;
    background: black;
    width: 0.4rem;
    height: 0.4rem;
    content: ' ';
  }
  &:last-child {
    margin-bottom: 0;
  }
  ul {
    margin-top: ${({ theme: { spacing } }) => spacing.s3};
  }
`;

export default function BodyItem({ children }: { children: React.ReactNode }) {
  return <StyledLi>{children}</StyledLi>;
}
