/* eslint-disable no-param-reassign */
import { SchemaOf, ValidationError as YupError } from 'yup';

export type ValidationError = {
  message: string;
  items?: Record<number, Record<string, ValidationError>>;
};

export type Errors<Values extends object> = {
  [key in keyof Values]?: ValidationError;
};

export const validateSimple = <Value>(schema: SchemaOf<Value>) => (
  value: Value,
): ValidationError | undefined => {
  try {
    schema.validateSync(value, { abortEarly: false });
    return undefined;
  } catch (error) {
    if (!error || !error.inner) {
      return {
        message: 'an error has occurred',
      };
    }
    return {
      message: (error.inner as YupError[])
        .reduce(
          (a: string[], validationError) => [...a, validationError.message],
          [] as string[],
        )
        .join(', '),
    };
  }
};

export const noErrors = <Value>(schema: SchemaOf<Value>, value: Value) =>
  validateSimple<Value>(schema)(value) === undefined;

export const validate = <Values extends object>(schema: SchemaOf<Values>) => (
  values: Values,
): Errors<Values> => {
  try {
    schema.validateSync(values, { abortEarly: false });
    return {};
  } catch (error) {
    if (!error || !error.inner) {
      return ({
        unknown: 'error has occurred',
      } as unknown) as Errors<Values>;
    }
    return (error.inner as YupError[]).reduce(
      (a: Errors<Values>, validationError) => {
        if (validationError.path !== undefined) {
          const [key, value] = validationError.path.split('].');
          if (!value) {
            const [simpleKey, simpleValue] = key!.split('[');
            if (!simpleValue) {
              a[key as keyof Values] = {
                ...(a[key as keyof Values] || {}),
                message: validationError.message,
              };
            } else {
              const [index] = simpleValue!.split(']');
              const existing: ValidationError = {
                message: '',
                items: {},
                ...(a[simpleKey! as keyof Values] || {}),
              };

              a[simpleKey! as keyof Values] = {
                ...existing,
                items: {
                  ...existing.items,
                  [index!]: {
                    ...((existing.items || {})[parseInt(index!, 10)] || {}),
                    _: {
                      message: validationError.message.replace(`${key} `, ''),
                    },
                  },
                },
              };
            }
          } else {
            const [actualKey, index] = key!.split('[');
            const existing: ValidationError = {
              message: '',
              items: {},
              ...(a[actualKey as keyof Values] || {}),
            };
            a[actualKey! as keyof Values] = {
              ...existing,
              items: {
                ...existing.items,
                [index!]: {
                  ...((existing.items || {})[parseInt(index!, 10)] || {}),
                  [value]: {
                    message: validationError.message.replace(
                      `${validationError.path} `,
                      '',
                    ),
                  },
                },
              },
            };
          }
        }
        return a;
      },
      {},
    );
  }
};
