import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import Selection from 'components/Form/Selection';
import StandardInput from 'components/Form/StandardInput';
import { PurposeOfAccount } from 'services/customer';

type Values = Pick<
  OnboardingContext['account'],
  'purposeOfNewAccount' | 'purposeOfNewAccountDetail'
>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  account: Values;
};

const purposeOptions: {
  [key in PurposeOfAccount]: string;
} = {
  [PurposeOfAccount.mainBankAccount]: 'Main bank account',
  [PurposeOfAccount.secondaryBankAccount]: 'Secondary bank account',
  [PurposeOfAccount.savings]: 'Savings',
  [PurposeOfAccount.tier1UkVisa]: 'Tier 1 UK visa',
  [PurposeOfAccount.other]: 'Other',
};

export default function PurposeOfNewAccountField({
  shownErrors,
  account,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_ACCOUNT',
    field: 'purposeOfNewAccount',
    value: PurposeOfAccount.mainBankAccount,
  });
  return (
    <>
      <Label
        error={shownErrors['purposeOfNewAccount']}
        ref={refFor('purposeOfNewAccount')}>
        Purpose of new account
        <Selection
          name="purposeOfNewAccount"
          placeholder="Please select..."
          options={purposeOptions}
          onChange={(value) =>
            send({
              type: 'UPDATE_ACCOUNT',
              field: 'purposeOfNewAccount',
              value: value as OnboardingContext['account']['purposeOfNewAccount'],
            })
          }
          disabled={disabled}
          value={account.purposeOfNewAccount}
          error={shownErrors['purposeOfNewAccount']}
        />
      </Label>
      {account.purposeOfNewAccount === PurposeOfAccount.other && (
        <Label error={shownErrors['purposeOfNewAccountDetail']}>
          What will you use the account for?
          <StandardInput
            id="purposeOfNewAccountDetail"
            placeholder="Enter the purpose of the new account"
            onChange={(value: string) =>
              send({
                type: 'UPDATE_ACCOUNT',
                field: 'purposeOfNewAccountDetail',
                value,
              })
            }
            disabled={disabled}
            value={account.purposeOfNewAccountDetail}
            error={shownErrors['purposeOfNewAccountDetail']}
          />
        </Label>
      )}
    </>
  );
}
