import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import PhoneInput from 'components/Form/PhoneInput';

type Values = Pick<OnboardingContext['contactDetails'], 'primaryTelNumber'>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  contactDetails: Values;
};

export default function MobileNumberField({
  shownErrors,
  contactDetails,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_PRIMARY_TEL_NUMBER',
    value: '',
  });
  return (
    <Label
      error={shownErrors['primaryTelNumber']}
      ref={refFor('primaryTelNumber')}>
      Mobile number
      <PhoneInput
        name="phone"
        onChange={(value) => send({ type: 'UPDATE_PRIMARY_TEL_NUMBER', value })}
        disabled={disabled}
        value={contactDetails.primaryTelNumber}
        error={shownErrors['primaryTelNumber']}
        placeholder="Enter mobile number"
        autoFocus={Object.keys(shownErrors).length === 0}
      />
    </Label>
  );
}
