import styled from 'styled-components';

type LoaderProps = {
  size: number;
  colour?: string;
};

const Loader = styled.div<LoaderProps>`
  position: relative;
  text-indent: -9999em;
  border-top: ${(props) => props.size * 0.11}rem solid rgba(255, 255, 255, 0.2);
  border-right: ${(props) => props.size * 0.11}rem solid
    rgba(255, 255, 255, 0.2);
  border-bottom: ${(props) => props.size * 0.11}rem solid
    rgba(255, 255, 255, 0.2);
  border-left: ${(props) => props.size * 0.11}rem solid
    ${(props) => props.colour || props.theme.color.primary};
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 0.7s infinite linear;
  animation: load8 0.7s infinite linear;

  &,
  &:after {
    border-radius: 50%;
    width: ${(props) => props.size}rem;
    height: ${(props) => props.size}rem;
  }
  @-webkit-keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
`;

export default function Spinner(props: Partial<LoaderProps>) {
  return <Loader size={5} {...props} data-testid="spinner" />;
}
