import BackArrow from 'components/BackArrow/BackArrow';
import PrimaryButton from 'components/Button/PrimaryButton';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import Envelope from 'components/Envelope/Envelope';
import Logo from 'components/Logo';
import StandardInput from 'components/Form/StandardInput';
import BodyText from 'components/Text/BodyText';
import Heading from 'components/Text/Heading';
import { useMagicLink } from 'lib/useMagicLink';
import { stateMatches } from 'statelib/stateMatches';
import { ReactComponent as Sent } from 'assets/sent.svg';
import { useState } from 'react';
import Page from 'components/Page/Page';
import Form from 'components/Form/Form';
import { useOnboarding } from 'lib/useOnboarding';
import Centred from 'components/Text/Centred';

export default function EnterEmail() {
  const [state, send, canTransition] = useMagicLink();
  const [, obSend, obCanTransition] = useOnboarding();
  const [resend] = useState(stateMatches(state, 'enterEmail', 'resendEmail'));
  return (
    <Page>
      <BackArrow send={obSend} canTransition={obCanTransition} />
      <Logo />
      {resend ? (
        <Centred>
          <CenterBlockSVG>
            <Sent />
          </CenterBlockSVG>
          <Heading>We'll resend an email to</Heading>
        </Centred>
      ) : (
        <Centred>
          <CenterBlockSVG>
            <Envelope />
          </CenterBlockSVG>
          <Heading>We'll need a valid email address to sign you up</Heading>
          <BodyText>We'll get your other details later</BodyText>
        </Centred>
      )}
      <Form>
        <StandardInput
          id="email"
          centred
          placeholder="Your email address"
          onChange={(emailAddress) =>
            send({ type: 'UPDATE_EMAIL', emailAddress })
          }
          disabled={!canTransition({ type: 'UPDATE_EMAIL', emailAddress: '' })}
          value={state.context.emailAddress}
          controlled
        />
        {resend && (
          <Centred>
            <BodyText>
              Please double check your email address before you resend it
            </BodyText>
          </Centred>
        )}
        <PrimaryButton
          type="submit"
          onClick={() => send({ type: 'CONTINUE' })}
          status={
            stateMatches(state, 'sendMagicLink')
              ? 'SUBMITTING'
              : !canTransition({ type: 'CONTINUE' })
              ? 'DISABLED'
              : 'ACTIVE'
          }>
          {resend ? 'Resend email' : 'Continue'}
        </PrimaryButton>
      </Form>
    </Page>
  );
}
