import {
  interpret,
  StateMachine,
  Actor,
  EventObject,
  StateSchema,
  Interpreter,
  SpawnedActorRef,
  State,
} from 'xstate';

export function startService<
  Context,
  Schema extends StateSchema,
  Event extends EventObject
>(
  machine: StateMachine<Context, Schema, Event>,
  initialState: State<Context, Event, Schema> | undefined = undefined,
) {
  return interpret(machine).start(initialState);
}

export function isService<Context, Schema, Event extends EventObject>(
  actor?: SpawnedActorRef<any>,
): actor is Interpreter<Context, Schema, Event> {
  return !!actor && 'children' in actor;
}

export function findService<Context, Schema, Event extends EventObject>(
  name: string,
  service: Interpreter<any, any, any>,
): Interpreter<Context, Schema, Event> | undefined {
  if (service.id === name) return service;
  const actor = service.children.has(name)
    ? service.children.get(name)
    : Array.from(service.children.values()).reduce((found, childService) => {
        if (found || !isService(childService)) {
          return found;
        }
        return findService(name, childService) as Actor | undefined;
      }, undefined as Actor | undefined);
  return isService<Context, Schema, Event>(actor) ? actor : undefined;
}
