import { useState } from 'react';
import styled from 'styled-components';
import { Event } from 'xstate';
import { OnboardingEvent } from 'sanstatecharts-onboarding';
import { useStatechart } from './hooks';

const H2 = styled.h2`
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: white;
  padding: 0;
  margin: 1rem 0 0.5rem;
  :first-child {
    margin-top: 0;
  }
`;

const Pre = styled.pre`
  margin: 0 0 0.25rem;
  word-break: break-word;
  overflow-wrap: anywhere;
`;

const StateRow = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`;

const StateRowInline = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex: 1;
  @media (min-width: 768px) {
    display: block;
  }
`;

const StyledCode = styled.code`
  color: #a0a0a0;
  word-break: break-word;
  overflow-wrap: anywhere;
`;

const StateHeading = H2;

const Buttons = styled.div`
  @media (min-width: 768px) {
    display: flex;
    flex-direction: column;
  }
`;

const StateDiv = styled.div`
  margin-right: 4rem;
  margin-bottom: 1rem;
`;

const StatePInline = styled.p`
  font-family: sans-serif;
  color: white;
`;

const Button = styled.button`
  font-family: sans-serif;
  background-color: Firebrick;
  color: white;
  padding: 5px;
  border-radius: 4px;
  margin-right: 10px;
  margin-bottom: 10px;
  font-weight: bold;
  border-color: Crimson;
  font-size: 14px;
  &:disabled {
    border-color: LightPink;
    background: LightPink;
  }
  @media (min-width: 768px) {
    margin-right: 0;
  }
  word-break: break-word;
  overflow-wrap: anywhere;
`;

const SecondaryButton = styled.button`
  font-family: 'PT Sans', sans-serif;
  background-color: white;
  color: Firebrick;
  padding: 5px;
  border-radius: 4px;
  margin-right: 10px;
  font-weight: bold;
  border-color: white;
  font-size: 16px;
  &:disabled {
    border-color: LightPink;
    background: LightPink;
  }
`;

const TextArea = styled.textarea`
  font-family: monospace;
  background-color: white;
  color: black;
  min-height: 8em;
  display: flex;
  padding: 5px;
  border-radius: 4px;
  border-color: Crimson;
  font-size: 15px;
  @media (min-width: 768px) {
    width: 100%;
  }
  &:disabled {
    border-color: LightPink;
    background: LightPink;
  }
`;

const Container = styled.div`
  @media (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
`;

const Code = ({ children }: { children: React.ReactNode }) => (
  <Pre>
    <StyledCode>{children}</StyledCode>
  </Pre>
);

const exists = (s: string) => !!s;

export default function StateMachineControl({
  machine,
  defaultEvents = {},
}: {
  machine: string;
  defaultEvents?: Record<string, any>;
}) {
  const [state, send, canTransition] = useStatechart(machine);

  const [eventObject, setEventObject] = useState('');
  const [shownEvent, setShownEvent] = useState<string | undefined>(undefined);
  function showEvent(event: string | undefined) {
    setShownEvent(event);
    setEventObject(
      JSON.stringify({ type: event, ...defaultEvents[event || ''] }, null, 2),
    );
  }
  function onChangeEvent(event: React.ChangeEvent<HTMLTextAreaElement>) {
    setEventObject(event.target.value);
  }

  function cancelSendEvent() {
    setShownEvent(undefined);
  }

  let eventToSend: Record<string, unknown> | undefined = undefined;
  try {
    eventToSend = JSON.parse(eventObject);
  } catch (err) {
    // empty
  }

  function sendEvent() {
    setShownEvent(undefined);
    send(({ data: {}, ...eventToSend } as unknown) as OnboardingEvent);
  }

  return (
    <Container>
      <StateRow>
        <StateDiv>
          <StateHeading>Current state</StateHeading>
          <Code>{JSON.stringify(state.value, null, 2)}</Code>
        </StateDiv>
        <StateDiv>
          <StateHeading>Current context</StateHeading>
          <Code>
            {JSON.stringify(
              {
                ...state.context,
                auth: {
                  ...state.context.auth,
                  initialUrl: state.context.auth.initialUrl ? '<present>' : '',
                },
              },
              null,
              2,
            )}
          </Code>
        </StateDiv>
      </StateRow>
      <StateRow>
        <StateRowInline>
          {shownEvent ? (
            <StatePInline>
              <TextArea onChange={onChangeEvent} value={eventObject} />
              <br />
              <Button
                type="button"
                onClick={sendEvent}
                disabled={eventToSend === undefined}>
                Send
              </Button>
              &nbsp;
              <SecondaryButton type="button" onClick={cancelSendEvent}>
                Cancel
              </SecondaryButton>
            </StatePInline>
          ) : (
            <StateDiv>
              <StateHeading>Next events</StateHeading>
              <Buttons>
                {state.nextEvents.filter(exists).map((nextEvent) => (
                  <Button
                    type="button"
                    key={nextEvent}
                    onClick={() => showEvent(nextEvent)}
                    disabled={
                      !!shownEvent ||
                      !canTransition({
                        type: nextEvent,
                        data: { message: `unknown ${nextEvent}` },
                        ...defaultEvents[nextEvent || ''],
                      } as Event<any>)
                    }>
                    {nextEvent}
                  </Button>
                ))}
              </Buttons>
            </StateDiv>
          )}
        </StateRowInline>
      </StateRow>
    </Container>
  );
}
