import { ComponentProps, useMemo, useState } from 'react';
import { Machine } from 'xstate';
import {
  employmentMachine,
  logToConsole,
  startService,
} from 'sanstatecharts-onboarding';
import { searchIndustrySectors } from 'services/customer';
import EmploymentInput from './EmploymentInput';

const industrySectorMachine = employmentMachine(
  'industrySector',
  (searchTerm: string) =>
    searchIndustrySectors({ searchTerm }).then(
      ({ data }) => data.industry_sectors,
    ),
);

type EmploymentInputProps = ComponentProps<typeof EmploymentInput>;

type Props = Omit<
  EmploymentInputProps,
  'service' | 'placeholder' | 'searching'
>;

export default function IndustrySectorInput(props: Props) {
  const [firstLine] = useState(props.value || '');
  const service: EmploymentInputProps['service'] = useMemo(() => {
    const industrySectorService = startService(
      Machine(industrySectorMachine).withContext({
        line: typeof firstLine === 'string' ? firstLine : firstLine.name,
        searchResults:
          typeof firstLine === 'string'
            ? {}
            : { [firstLine.name]: [firstLine] },
      }),
    );
    if (process.env.REACT_APP_XSTATE_LOG && process.env.NODE_ENV !== 'test') {
      logToConsole(industrySectorService);
    }
    return industrySectorService;
  }, [firstLine]);
  return (
    <EmploymentInput
      {...props}
      service={service}
      name="industrySector"
      placeholder="Start typing industry sector..."
      searching="Searching for your industry sector"
    />
  );
}
