import { Errors, ValidationError } from 'sanstatecharts-onboarding';

const ucFirst = (s: string) => s.charAt(0).toUpperCase() + s.substring(1);

// stackoverflow.com/a/15998146
function ordinalInWord(cardinal: number) {
  const ordinals = [
    'first',
    'second',
    'third',
    'fourth',
    'fifth',
    'sixth',
    'seventh',
    'eighth',
    'ninth',
    'tenth',
    'eleventh',
    'twelfth',
    'thirteenth',
    'fourteenth',
    'fifteenth',
    'sixteenth',
    'seventeenth',
    'eighteenth',
    'nineteenth',
    'twentieth',
  ];
  const tens = {
    20: 'twenty',
    30: 'thirty',
    40: 'forty',
    50: 'fifty',
    60: 'sixty',
    70: 'seventy',
    80: 'eighty',
    90: 'ninety',
  };
  const ordinalTens = {
    30: 'thirtieth',
    40: 'fortieth',
    50: 'fiftieth',
    60: 'sixtieth',
    70: 'seventieth',
    80: 'eightieth',
    90: 'nintieth',
  };

  if (cardinal <= 20) {
    return ordinals[cardinal];
  }

  if (cardinal % 10 === 0) {
    return ordinalTens[cardinal as keyof typeof ordinalTens];
  }

  return (
    tens[(cardinal - (cardinal % 10)) as keyof typeof tens] +
    ordinals[cardinal % 10]
  );
}

type ErrorItem = {
  message: string;
  field: string;
  index?: number;
  itemField?: string;
};

const errorToList = (field: string, error: ValidationError) => (
  a: ErrorItem[],
  key: string,
  _index: number,
  keys: string[],
): ErrorItem[] => {
  const keyNumber = parseInt(key, 10);
  const prefix =
    keyNumber === 0 && keys.length === 1 ? '' : `${ordinalInWord(keyNumber)} `;
  const items: Record<string, ValidationError> = error.items![keyNumber]!;
  return Object.keys(items).reduce(
    errorsToList(items, prefix, [keyNumber, field]),
    a,
  );
};

const errorsToList = (
  errors: Record<string, ValidationError>,
  prefix: string,
  useField?: [number, string],
) => (a: ErrorItem[], field: string) => {
  const error = errors[field]!;
  const keys = Object.keys(error.items || {});
  return keys.reduce(
    errorToList(useField ? useField[1] : field, error),
    error.message && keys.length === 0
      ? [
          ...a,
          useField
            ? {
                message: ucFirst(`${prefix}${error.message}`),
                field: useField[1],
                index: useField[0],
                itemField: useField && field !== '_' ? field : undefined,
              }
            : {
                message: ucFirst(`${prefix}${error.message}`),
                field,
              },
        ]
      : a,
  );
};

export function errorList<Values extends object>(
  errors: Errors<Values>,
): ErrorItem[] {
  const errorRecord = errors as Record<string, ValidationError>;
  return Object.keys(errorRecord).reduce(errorsToList(errorRecord, ''), []);
}
