import { pcaOnboardingMachine } from 'sanstatecharts-onboarding';
import { useStatechart } from 'state-machines/hooks';
import { MachineConfig } from 'xstate';

export function useOnboarding<
  Config extends MachineConfig<any, any, any> = typeof pcaOnboardingMachine
>() {
  return useStatechart<Config>('onboarding');
}
