import React from 'react';
import styled from 'styled-components';

const StyledBlock = styled.div`
  margin-left: -${({ theme: { spacing } }) => spacing.s4half};
  margin-right: -${({ theme: { spacing } }) => spacing.s4half};
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  padding: ${({ theme: { spacing } }) => spacing.s4}
    ${({ theme: { spacing } }) => spacing.s4half};
  background-color: ${({ theme: { color } }) => color.blueBG};
  p:last-child {
    margin-bottom: 0;
  }
`;

export default function InfoBlock({ children }: { children: React.ReactNode }) {
  return <StyledBlock>{children}</StyledBlock>;
}
