import queryString from 'query-string';
import { noErrors } from 'statelib/validate';
import {
  AddAddress,
  AddAnotherName,
  AddIncomeSource,
  AddInitialDeposit,
  AddNationality,
  AddTaxResidenceData,
  MaidenNameNo,
  MaidenNameYes,
  RemoveAddress,
  RemoveIncomeSource,
  RemoveInitialDeposit,
  RemoveNationality,
  RemoveOtherName,
  RemoveTaxResidenceData,
  UpdateAddress,
  UpdateIncomeSource,
  UpdateInitialDeposit,
  UpdateNationality,
  UpdateOtherName,
  UpdateTaxResidenceData,
} from './events';
import { UserAddress } from './address';
import { OnboardingContext } from './context';
import { OtherName, otherNameIsNotMaidenName } from './otherName';
import { Funds } from './funds';
import {
  aboutYouAdditionalValidation,
  aboutYouPersonalValidation,
  aboutYouValidation,
  otherNameValidationSchema,
  nationalitiesValidation,
  taxStatusValidation,
  taxResidenceEntryValidationSchema
} from './validation/aboutYouValidation';
import {
  accountValidation,
  initialDepositValidationSchema,
} from './validation/accountValidation';
import {
  aboutYouAddressesValidation,
  aboutYouContactValidation,
  addressValidationSchema,
  contactDetailsValidation,
  userAddressValidationSchema,
} from './validation/contactDetailsValidation';
import { financesValidation } from './validation/financesValidation';
import { overdraftValidation } from './validation/overdraftValidation';
import { TaxResidenceEntry } from 'services/customer';

const invalidOtherName = (o: OtherName) =>
  !noErrors(otherNameValidationSchema, o);

const invalidUserAddress = (address: UserAddress) =>
  !noErrors(userAddressValidationSchema, address);

const invalidInitialDeposit = (initialDeposit: Funds) =>
  !noErrors(initialDepositValidationSchema, initialDeposit);

const invalidTaxResidenceEntry = (taxResideceEntry: TaxResidenceEntry) =>
  !noErrors(taxResidenceEntryValidationSchema, taxResideceEntry);

const existsNotAtIndex = (value: string, index: number) => (
  s: string,
  i: number,
) => s === value && index !== i;

export const guards = {
  hasUserOrMagicLink: ({
    auth: { userId, sessionId, initialUrl },
  }: OnboardingContext) =>
    (!!userId && !!sessionId) || !!queryString.parse(initialUrl).token,
  hasMagicLink: ({ auth: { initialUrl } }: OnboardingContext) =>
    !!queryString.parse(initialUrl).token,
  hasProductCode: ({ productCode }: OnboardingContext) => !!productCode,
  canAddAnotherName: (
    { aboutYou: { otherNames } }: OnboardingContext,
    _event: AddAnotherName,
  ) =>
    otherNames === undefined ||
    (!otherNames.some(invalidOtherName) &&
      otherNames.filter(otherNameIsNotMaidenName).length < 5),
  canRemoveAnotherName: (
    { aboutYou: { otherNames } }: OnboardingContext,
    { index }: RemoveOtherName,
  ) => otherNames !== undefined && otherNames.length > index,
  otherNameExistsAtIndex: (
    { aboutYou: { otherNames } }: OnboardingContext,
    { index }: MaidenNameYes | MaidenNameNo | UpdateOtherName,
  ) => otherNames !== undefined && otherNames.length > index,
  nationalityExistsAtIndex: (
    { aboutYou: { nationalities } }: OnboardingContext,
    { value, index }: UpdateNationality,
  ) =>
    nationalities.length > index &&
    !nationalities.some(existsNotAtIndex(value, index)),
  canAddNationality: (
    { aboutYou }: OnboardingContext,
    _event: AddNationality,
  ) =>
    !aboutYouValidation({
      ...aboutYou,
      nationalities: [...aboutYou.nationalities, 'ANOTHER'],
    }).nationalities,
  canRemoveNationality: (
    { aboutYou: { nationalities } }: OnboardingContext,
    { index }: RemoveNationality,
  ) => index !== 0 && nationalities.length > index,
  taxResidenceDataExistsAtIndex: (
    { aboutYou: { taxResidenceData } }: OnboardingContext,
    { index }: UpdateTaxResidenceData,
  ) => !!taxResidenceData && taxResidenceData.length > index,
  canAddTaxResidenceData: ({ aboutYou: { taxResidenceData } }: OnboardingContext, _event: AddTaxResidenceData) =>
  !(taxResidenceData.length === 1 && taxResidenceData[0]?.country_of_tax_residence === 'GB') && (!taxResidenceData.some(invalidTaxResidenceEntry) && taxResidenceData.length < 5),
  canRemoveTaxResidenceData: (
    { aboutYou: { taxResidenceData } }: OnboardingContext,
    { index }: RemoveTaxResidenceData,
  ) => index !== 0 && taxResidenceData.length > index,
  aboutYouPersonalComplete: ({ aboutYou }: OnboardingContext) =>
    Object.keys(aboutYouPersonalValidation(aboutYou)).length === 0,
  aboutYouAdditionalComplete: ({ aboutYou }: OnboardingContext) =>
    Object.keys(aboutYouAdditionalValidation(aboutYou)).length === 0,
  aboutYouNationalityComplete: ({ aboutYou }: OnboardingContext) =>
    Object.keys(nationalitiesValidation(aboutYou)).length === 0,
  aboutYouTaxInformationComplete: ({ aboutYou }: OnboardingContext) =>
    Object.keys(taxStatusValidation(aboutYou)).length === 0,
  aboutYouComplete: ({ aboutYou }: OnboardingContext) =>
    Object.keys(aboutYouValidation(aboutYou)).length === 0,
  addressExistsAtIndex: (
    { contactDetails: { addresses } }: OnboardingContext,
    { index }: UpdateAddress,
  ) => addresses.length > index,
  canAddAddress: (
    { contactDetails: { addresses } }: OnboardingContext,
    _event: AddAddress,
  ) => !addresses.some(invalidUserAddress),
  canRemoveAddress: (
    { contactDetails: { addresses } }: OnboardingContext,
    { index }: RemoveAddress,
  ) =>
    addresses.length > index &&
    noErrors(addressValidationSchema, addresses[index]!.selectedAddress),
  aboutYouContactComplete: ({ contactDetails }: OnboardingContext) =>
    Object.keys(aboutYouContactValidation(contactDetails)).length === 0,
  aboutYouResidentialComplete: ({ contactDetails }: OnboardingContext) =>
    Object.keys(aboutYouAddressesValidation(contactDetails)).length === 0,
  contactDetailsComplete: ({ contactDetails }: OnboardingContext) =>
    Object.keys(contactDetailsValidation(contactDetails)).length === 0,
  incomeSourceExistsAtIndex: (
    { finances: { incomeSources } }: OnboardingContext,
    { index }: UpdateIncomeSource,
  ) => incomeSources.length > index,
  canAddIncomeSource: (
    { finances }: OnboardingContext,
    _event: AddIncomeSource,
  ) =>
    !financesValidation({
      ...finances,
      incomeSources: [
        ...finances.incomeSources,
        { type: 'SAVINGS', amount: 1000, origin: 'GB' },
      ],
    }).incomeSources,
  canRemoveIncomeSource: (
    { finances: { incomeSources } }: OnboardingContext,
    { index }: RemoveIncomeSource,
  ) => incomeSources.length > index && index !== 0,
  financesComplete: ({ finances }: OnboardingContext) =>
    Object.keys(financesValidation(finances)).length === 0,
  accountComplete: ({ account }: OnboardingContext) =>
    Object.keys(accountValidation(account)).length === 0,
  overdraftComplete: ({ overdraft }: OnboardingContext) =>
    Object.keys(overdraftValidation({ overdraft })).length === 0,
  initialDepositExistsAtIndex: (
    { account: { initialDeposits } }: OnboardingContext,
    { index }: UpdateInitialDeposit,
  ) => !!initialDeposits && initialDeposits.length > index,
  canAddInitialDeposit: (
    { account }: OnboardingContext,
    _event: AddInitialDeposit,
  ) =>
    !account.initialDeposits ||
    !accountValidation({
      ...account,
      initialDeposits: [
        ...account.initialDeposits,
        { type: 'SAVINGS', amount: 1000, origin: 'GB' },
      ],
    }).initialDeposits,
  canRemoveInitialDeposit: (
    { account: { initialDeposits } }: OnboardingContext,
    { index }: RemoveInitialDeposit,
  ) =>
    !!initialDeposits &&
    initialDeposits.length > index &&
    (index !== 0 || !invalidInitialDeposit(initialDeposits[0]!)),
};
