import { initialiseDependencies } from 'sanstatecharts-onboarding';
import { decrypt, encrypt } from 'lib/encrypt/encrypt';

const neverEncryptTheseNames: string[] = ['businessType', 'emailAddress'];
const encryptPersistence =
  process.env.REACT_APP_ENCRYPT_PERSISTENCE === undefined ||
  process.env.REACT_APP_ENCRYPT_PERSISTENCE !== 'false';

function doNotEncrypt(name: string) {
  return !encryptPersistence || neverEncryptTheseNames.includes(name);
}

const asIs = (s: any) => s;
const decryptOrNot = (name: string) => (doNotEncrypt(name) ? asIs : decrypt);
const encryptOrNot = (name: string) => (doNotEncrypt(name) ? asIs : encrypt);
const named = (name: string) => `${process.env.REACT_APP_WEBVIEW}.${name}`;

initialiseDependencies({
  getPersistedData: (name: string) =>
    decryptOrNot(name)(localStorage.getItem(named(name)) || undefined),
  setPersistedData: (name: string, value: string) =>
    localStorage.setItem(named(name), encryptOrNot(name)(value)),
  removePersistedData: (name: string) => localStorage.removeItem(named(name)),
});
