import React from 'react';
import styled from 'styled-components';

const StyledContainer = styled.div`
  position: relative;
`;

const StyledChild = styled.div`
  position: absolute;
  top: 0;
  right: ${({ theme: { spacing } }) => spacing.s4};
  line-height: 1.5;
  margin-top: ${({ theme: { spacing } }) => spacing.s3};
`;

const StyledSibling = styled.div`
  position: relative;
  top: -${({ theme: { spacing } }) => spacing.s4};
  margin-top: 0;
`;

export function InlineContainer({ children }: { children: React.ReactNode }) {
  return <StyledContainer>{children}</StyledContainer>;
}

export function InlineChild({ children }: { children: React.ReactNode }) {
  if (!children) return <></>;
  return <StyledChild>{children}</StyledChild>;
}

export function InlineSibling({ children }: { children: React.ReactNode }) {
  if (!children) return <></>;
  return <StyledSibling>{children}</StyledSibling>;
}
