export type OtherName = {
  name: string;
  isMaidenName: boolean | undefined;
};

export const maidenNameIsSet = (otherNames: OtherName[]) =>
  otherNames.some(otherNameIsMaidenName);

export const otherNameString = (otherName: OtherName) => otherName.name;

export const otherNameIsMaidenName = (otherName: OtherName) =>
  otherName.isMaidenName;

export const otherNameIsNotMaidenName = (otherName: OtherName) =>
  !otherName.isMaidenName;
