export default function NameFields({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_ABOUT_YOU',
    field: 'salutation',
    value: 'Mr',
  });
  const isOtherTitle =
    aboutYou.salutation !== undefined &&
    !standardSalutations.includes(aboutYou.salutation.toUpperCase());
  return (
    <>
      <Label error={shownErrors['salutation']} ref={refFor('salutation')}>
        Title
        <Selection
          name="title"
          placeholder="Please select..."
          options={[...standardSalutations, 'Other']}
          value={
            isOtherTitle ? 'Other' : (aboutYou.salutation || '').toUpperCase()
          }
          onChange={(value: string) =>
            send({
              type: 'UPDATE_ABOUT_YOU',
              field: 'salutation',
              value: value === 'Other' ? '' : value,
            })
          }
          disabled={disabled}
          error={shownErrors['salutation']}
          autoFocus={Object.keys(shownErrors).length === 0}
        />
      </Label>
      {isOtherTitle && (
        <Selection
          name="otherTitle"
          placeholder="Select other title"
          options={otherSalutations}
          value={aboutYou.salutation}
          onChange={(value: string) =>
            send({
              type: 'UPDATE_ABOUT_YOU',
              field: 'salutation',
              value,
            })
          }
          disabled={disabled}
          error={shownErrors['salutation']}
        />
      )}
      <Label error={shownErrors['firstName']} ref={refFor('firstName')}>
        First name
        <StandardInput
          id="firstName"
          placeholder="Enter first name"
          onChange={(value: string) =>
            send({ type: 'UPDATE_ABOUT_YOU', field: 'firstName', value })
          }
          disabled={disabled}
          value={aboutYou.firstName}
          error={shownErrors['firstName']}
          autoComplete="given-name"
          maxLength={50}
        />
      </Label>
      <Label error={shownErrors['middleNames']} ref={refFor('middleNames')}>
        Middle name(s) - if you have any?
        <StandardInput
          id="middleName"
          placeholder="Enter any middle names"
          onChange={(value) =>
            send({ type: 'UPDATE_ABOUT_YOU', field: 'middleNames', value })
          }
          disabled={disabled}
          value={aboutYou.middleNames}
          error={shownErrors['middleNames']}
          autoComplete="additional-name"
          maxLength={100}
        />
      </Label>
      <Label error={shownErrors['lastName']} ref={refFor('lastName')}>
        Last name
        <StandardInput
          id="lastName"
          placeholder="Enter last name"
          onChange={(value) =>
            send({ type: 'UPDATE_ABOUT_YOU', field: 'lastName', value })
          }
          disabled={disabled}
          value={aboutYou.lastName}
          error={shownErrors['lastName']}
          autoComplete="family-name"
          maxLength={50}
        />
      </Label>
    </>
  );
}
