import { Errors, ValidationError } from 'sanstatecharts-onboarding';

export function fieldError<Values extends object, Key extends string>(
  errors: Errors<Values>,
  field: keyof Values,
  index: number,
): (key: Key) => ValidationError | undefined {
  return (key: Key) => {
    const error = errors[field];
    if (!error || !error.items || !error.items[index]) return undefined;
    return error.items[index]![key];
  };
}

export function findError<Values extends object>(
  errors: Errors<Values>,
  field: keyof Values,
  index: number,
  key: string = '_',
): ValidationError | undefined {
  return fieldError(errors, field, index)(key);
}
