import { EmploymentTuple } from 'services/customer';
import {
  MachineConfig,
  assign,
  send,
  DoneInvokeEvent,
  actions as xstateActions,
} from 'xstate';

export type EmploymentContext = {
  line: string;
  searchResults: Record<string, EmploymentTuple[]>;
};

export const defaultEmploymentContext: Readonly<EmploymentContext> = {
  line: '',
  searchResults: {},
};

export type EmploymentSchema = {
  context: EmploymentContext;
  states: {
    pristine: {};
    searchAfterDelay: {};
    search: {};
    searchError: {};
    results: {};
  };
};

type PerformSearchFunction = (payload: string) => Promise<EmploymentTuple[]>;

export const employmentServices = {
  search: (performSearch: PerformSearchFunction) => async ({
    line,
  }: EmploymentContext) => performSearch(line.trim().toUpperCase()),
};

type UpdateLine = { type: 'UPDATE_LINE'; value: string };
type PerformSearch = { type: 'PERFORM_SEARCH' };
type DismissResults = { type: 'DISMISS_RESULTS' };
type SelectResult = {
  type: 'SELECT_RESULT';
  result: EmploymentTuple;
};
type Reset = { type: 'RESET' };

type Depromisified<T> = T extends Promise<infer R> ? R : never;

type DoneInvokeSearch = {
  type: 'done.invoke.SEARCH';
  data: Depromisified<ReturnType<PerformSearchFunction>>;
};

export type EmploymentEvent =
  | UpdateLine
  | PerformSearch
  | SelectResult
  | DoneInvokeSearch
  | DismissResults
  | Reset;

const DEBOUNCE = 450;

const actions = {
  assignLine: assign(
    (_context: EmploymentContext, { value: line }: UpdateLine) => ({
      line,
    }),
  ),
  searchAfterDelay: send<EmploymentContext, EmploymentEvent>(
    { type: 'PERFORM_SEARCH' },
    { delay: DEBOUNCE, id: 'searchAfterDelay' },
  ),
  cancelSearchAfterDelay: xstateActions.cancel('searchAfterDelay'),
  assignSearchResults: assign(
    (
      { line, searchResults }: EmploymentContext,
      { data: results }: DoneInvokeEvent<DoneInvokeSearch['data']>,
    ) => ({
      searchResults: {
        ...searchResults,
        [line.trim().toUpperCase()]: results,
      },
    }),
  ),
  selectResult: assign(
    ({ searchResults }: EmploymentContext, { result }: SelectResult) => ({
      line: result.name,
      searchResults: {
        ...searchResults,
        [result.name.trim().toUpperCase()]: [result],
      },
    }),
  ),
};

const guards = {
  lineIsShort: (_context: EmploymentContext, { value }: UpdateLine) =>
    value.trim().length <= 2,
  hasNoSearchResults: (
    { searchResults }: EmploymentContext,
    { value }: UpdateLine,
  ) => !searchResults[value.trim().toUpperCase()],
  hasSearchResults: (
    { searchResults }: EmploymentContext,
    { value }: UpdateLine,
  ) => !!searchResults[value.trim().toUpperCase()],
  resultIsDefined: (_context: EmploymentContext, { result }: SelectResult) =>
    !!result && !!result.name && !!result.code,
};

export const employmentMachine = (
  id: string,
  performSearch: PerformSearchFunction,
): MachineConfig<EmploymentContext, EmploymentSchema, EmploymentEvent> => ({
  id,
  context: defaultEmploymentContext,
  on: {
    UPDATE_LINE: [
      {
        target: '.pristine',
        cond: guards.lineIsShort,
        actions: actions.assignLine,
      },
      {
        target: '.searchAfterDelay',
        cond: guards.hasNoSearchResults,
        actions: actions.assignLine,
      },
      {
        target: '.results',
        cond: guards.hasSearchResults,
        actions: actions.assignLine,
      },
    ],
  },
  initial: 'pristine',
  states: {
    pristine: {},
    searchAfterDelay: {
      entry: actions.searchAfterDelay,
      exit: actions.cancelSearchAfterDelay,
      on: { PERFORM_SEARCH: 'search' },
    },
    search: {
      invoke: {
        id: 'SEARCH',
        src: employmentServices.search(performSearch),
        onDone: {
          target: 'results',
          actions: actions.assignSearchResults,
        },
        onError: 'searchError',
      },
    },
    searchError: {},
    results: {
      on: {
        DISMISS_RESULTS: 'pristine',
        SELECT_RESULT: [
          {
            actions: actions.selectResult,
            cond: guards.resultIsDefined,
          },
          { target: 'pristine' },
        ],
      },
    },
  },
});
