import Page from 'components/Page/Page';
import ProgressBar from 'components/ProgressBar/ProgressBar';
import Heading from 'components/Text/Heading';
import { useOnboarding } from 'lib/useOnboarding';
import {
  contactDetailsValidation,
  stateMatches,
} from 'sanstatecharts-onboarding';
import PersonUpdateError from 'components/PersonUpdateError/PersonUpdateError';
import { useValidation } from 'lib/useValidation';
import Form from 'components/Form/Form';
import MobileNumberField from 'components/Fields/MobileNumberField';
import AddressFields from 'components/Fields/AddressFields';
import ContinueButton from 'components/Form/ContinueButton';
import { PCAStepState } from 'lib/StepState';

export default function ContactDetails() {
  const [state, send, canTransition] = useOnboarding();
  const { contactDetails } = state.context;
  const validationErrors = contactDetailsValidation(contactDetails);
  const { shownErrors, refFor, refCreate, ...validation } = useValidation(
    validationErrors,
  );

  return (
    <Page>
      <ProgressBar step="contactDetails" title="Contact details" />
      <Heading>Contact details</Heading>
      <Form>
        <MobileNumberField
          shownErrors={shownErrors}
          contactDetails={contactDetails}
          send={send}
          canTransition={canTransition}
          refFor={refFor}
        />
        <AddressFields
          shownErrors={shownErrors}
          contactDetails={contactDetails}
          send={send}
          canTransition={canTransition}
          refCreate={refCreate}
        />
        <PersonUpdateError
          enabled={stateMatches(
            state,
            'steps',
            'contactDetails',
            'updateError',
          )}
        />
        <ContinueButton
          validationErrors={validationErrors}
          {...validation}
          status={(state.value as PCAStepState)?.steps?.contactDetails}
        />
      </Form>
    </Page>
  );
}
