import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import Selection from 'components/Form/Selection';
import Radios from 'components/Form/Radios';
import MoneyInput from 'components/Form/MoneyInput';

type Values = Pick<
  OnboardingContext['account'],
  'largeCash' | 'largeCashAmount'
>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  account: Values;
};

type LargeCashKey = NonNullable<OnboardingContext['account']['largeCash']>;

const largeCashOptions: {
  [key in LargeCashKey]?: string;
} = {
  TRANSACTION_1_1000: '£1 - £1,000',
  TRANSACTION_1001_2000: '£1,000 - £1,999',
  TRANSACTION_2000_PLUS: '£2,000+',
};

export default function LargeCashField({
  shownErrors,
  account,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_ACCOUNT',
    field: 'largeCash',
    value: 'NO',
  });
  return (
    <>
      <Label error={shownErrors['largeCash']} ref={refFor('largeCash')}>
        Will you pay in large amounts in cash?
        <Radios
          name="largeCash"
          options={['Yes', 'No']}
          onChange={(largeCash) =>
            send({
              type: 'UPDATE_ACCOUNT',
              field: 'largeCash',
              value: largeCash === 'Yes' ? 'YES' : 'NO',
            })
          }
          disabled={disabled}
          value={
            account.largeCash === undefined
              ? undefined
              : account.largeCash === 'YES' ||
                !!largeCashOptions[account.largeCash]
              ? 'Yes'
              : 'No'
          }
          error={shownErrors['largeCash']}
        />
      </Label>
      {account.largeCash !== undefined &&
        (account.largeCash === 'YES' ||
          !!largeCashOptions[account.largeCash]) && (
          <>
            <Label error={shownErrors['largeCash']}>
              Roughly how much do you think you will deposit each month?
              <Selection
                name="largeCashOption"
                placeholder="Please select..."
                options={largeCashOptions}
                onChange={(value) =>
                  send({
                    type: 'UPDATE_ACCOUNT',
                    field: 'largeCash',
                    value: value as LargeCashKey,
                  })
                }
                disabled={disabled}
                value={account.largeCash}
                error={shownErrors['largeCash']}
              />
            </Label>
            {account.largeCash === 'TRANSACTION_2000_PLUS' && (
              <Label error={shownErrors['largeCashAmount']}>
                As you've chosen more than £2,000, please give us a specific
                amount
                <MoneyInput
                  id="largeCashAmount"
                  value={account.largeCashAmount}
                  disabled={disabled}
                  onChange={(value) =>
                    send({
                      type: 'UPDATE_ACCOUNT',
                      field: 'largeCashAmount',
                      value,
                    })
                  }
                  error={shownErrors['largeCashAmount']}
                />
              </Label>
            )}
          </>
        )}
    </>
  );
}
