import React from 'react';
import styled from 'styled-components';

const StyledP = styled.p`
  margin-bottom: 0px;
  font-family: ${({ theme: { font } }) => font.bold.fontFamily};
`;

export default function CardTextBold({ children }: { children: React.ReactNode }) {
  return <StyledP>{children}</StyledP>;
}
