import { stateMatches } from 'statelib/stateMatches';
import EnterEmail from 'pages/EnterEmail';
import InvalidMagicLink from 'pages/InvalidMagicLink';
import RetrieveUser from 'pages/RetrieveUser';
import StyledAppRouter from 'components/StyledAppRouter';
import WeveSentAnEmail from 'pages/WeveSentAnEmail';
import { useMagicLink } from 'lib/useMagicLink';

export default function MagicLinkRouter() {
  const [state] = useMagicLink();
  return (
    <StyledAppRouter>
      {(stateMatches(state, 'enterEmail') ||
        stateMatches(state, 'sendMagicLink')) && <EnterEmail />}
      {stateMatches(state, 'weveSentAnEmail') && <WeveSentAnEmail />}
      {stateMatches(state, 'invalidMagicLink') && <InvalidMagicLink />}
      {stateMatches(state, 'validateMagicLink') && <RetrieveUser />}
    </StyledAppRouter>
  );
}
