import { ReactComponent as NiceWork } from 'assets/nice-work.svg';
import PrimaryButton from 'components/Button/PrimaryButton';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import Page from 'components/Page/Page';
import Subheading from 'components/Text/Subheading';
import Heading from 'components/Text/Heading';
import { useOnboarding } from 'lib/useOnboarding';
import Centred from 'components/Text/Centred';

export default function ValidMagicLink() {
  const [, send, canTransition] = useOnboarding();
  return (
    <Page>
      <CenterBlockSVG>
        <NiceWork />
      </CenterBlockSVG>
      <Centred>
        <Heading>Nice work!</Heading>
        <Subheading>You're logged in, secure and ready to start</Subheading>
      </Centred>
      <PrimaryButton
        onClick={() => send({ type: 'CONTINUE' })}
        status={!canTransition({ type: 'CONTINUE' }) ? 'DISABLED' : 'ACTIVE'}>
        Let's go
      </PrimaryButton>
    </Page>
  );
}
