import { useContext } from 'react';
import { Interpreter } from 'xstate';
import useXstate from 'lib/useXstate';
import { assertIsDefined } from 'sanstatecharts-onboarding';
import styled from 'styled-components';
import { StateMachineContext } from './StateMachineContext';

const rationalise = (s: string[]) =>
  s.reduce(
    (a, k) => [...a.filter((e) => k.indexOf(e) === -1), k],
    [] as string[],
  );

const View = styled.div`
  background-color: white;
  flex: 1;
`;

const Text = styled.p`
  color: black;
  flex: 1;
  padding: 1rem;
`;

export default function StateMachinesDebug() {
  const stateContext = useContext(StateMachineContext);
  assertIsDefined(
    stateContext,
    'StateMachinesDebug requires a StateMachineContext provider',
  );
  const [service] = stateContext;
  const [state] = useXstate(service);
  type MachineDebug = {
    [key: string]: {
      context: any;
      strings: string[];
    };
  };
  const stateMachineDebug = Array.from(service.children.values()).reduce(
    (a: MachineDebug, childService) => {
      if (!(childService instanceof Interpreter)) {
        return a;
      }
      return {
        ...a,
        [childService.machine.id]: {
          strings: rationalise(childService.state.toStrings()),
          context: childService.state.context,
        },
      };
    },
    {
      app: {
        strings: rationalise(state.toStrings()),
        context: state.context,
      },
    },
  );
  return (
    <View data-testid="StateMachinesDebug">
      {Object.keys(stateMachineDebug).map((debugKey) => (
        <View key={debugKey}>
          <Text>
            {`\n${debugKey}: ${stateMachineDebug[debugKey]!.strings.join(
              ', ',
            )} ${JSON.stringify(
              stateMachineDebug[debugKey]!.context,
              null,
              2,
            )}`}
          </Text>
        </View>
      ))}
    </View>
  );
}
