  firstName: string | null;
  middleNames: string | null;
  monthlyOutgoings: number | null;
  financialDependents: number | null;
  lastName: string | null;
  maidenName: string | null;
  DOB: string | null;
  otherNames: string[];
  countryOfBirth: string | null;
  nationalities: string[] | null;
  taxResidenceData: TaxResidenceEntry[];
  primaryTelNumber: string | null;
  accommodationCircumstances: AccommodationCircumstance | '';
  addressHistory: AddressHistoryEntryTransformed[];
