export type AccommodationCircumstance =
  | 'LIVING_WITH_PARENTS'
  | 'LIVING_WITH_FRIENDS'
  | 'TENANT'
  | 'OUTRIGHT_OWNER'
  | 'OWNER_WITH_MORTGAGE'
  | 'OWNER_WITH_SANTANDER_MORTGAGE';

// casting to Formik-safe values if falsy
// ' ' === value displayed in form, but not mandatory
// '' === value displayed in form, and mandatory
export const camelCasePersonDataForStore = (
  data: GetPersonResponse,
): GetPersonTransformed => ({
  employment: data.employment,
  salutation: data.salutation || '',
  incomeDetails: data.income_details || null,
  monthlyOutgoings: data.monthly_outgoings || null,
  financialDependents: data.financial_dependents,
  firstName: data.given_name || '',
  middleNames: data.middle_names || ' ',
  lastName: data.family_name || '',
  maidenName: data.maiden_name || ' ',
  DOB: data.date_of_birth || '',
  otherNames: data.other_names || [''],
  nationalities: data.nationalities || [''],
  taxResidenceData: data.tax_residence_data || [],
  primaryTelNumber: data.primary_contact_number || '',
  accommodationCircumstances: data.accommodation_circumstances || '',
  addressHistory: data.address_history.length
    ? data.address_history.map((item: AddressHistoryEntry) => ({
        addressId: item.address_id,
        addressHistoryType: item.address_history_type,
        moveOnMM: item.move_on?.split('-')[1] || '',
        moveOnYYYY: item.move_on?.split('-')[0] || '',
        moveOn: item.move_on || null,
        lookupEntries: [],
      }))
    : [],
  countryOfBirth: data.country_of_birth || '',
  applicationId: data.application_id || '',
});

export const getPerson = withRefresh(
  ({ userId }: { userId: string }): Promise<AxiosResponse<GetPersonResponse>> =>
    client.get(`customers/${userId}/persons`),
);

export interface UpdatePersonItem {
  op: 'replace';
  path: string;
  value: keyof GetPersonResponse;
}
export type UpdatePersonPayload = UpdatePersonItem[];

export type UpdatePersonResponse = {
  status: number;
};

export const updatePerson = withRefresh(
  ({
    userId,
    updatePayload,
  }: {
    userId: string;
    updatePayload: Partial<GetPersonResponse>;
  }): Promise<AxiosResponse<UpdatePersonResponse>> => {
    // https://tools.ietf.org/html/rfc6902#section-4.3
    const patchDataSet = Object.keys(updatePayload).reduce(
      (patchDataSetAcc, patchDataSetProp) => {
        patchDataSetAcc.push({
          op: 'replace',
          path: `/${[patchDataSetProp]}`,
          value: updatePayload[
            patchDataSetProp as keyof typeof updatePayload
          ] as keyof GetPersonResponse,
        });
        return patchDataSetAcc;
      },
      [] as UpdatePersonPayload,
    );

    return client.patch(`customers/${userId}/persons/patch`, patchDataSet);
  },
);

