import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';
import { useCallback } from 'react';
import { useUncontrolledInput } from 'lib/useUncontrolledInput';

type InputProps = Omit<
  React.InputHTMLAttributes<HTMLInputElement>,
  'onChange' | 'value' | 'id'
> & {
  onChange: (s: number) => void;
  value: number | undefined;
  error?: ValidationError;
  id: string;
};

const StyledContainer = styled.div`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  width: 11rem;
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const StyledContainerError = styled(StyledContainer)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
`;

const StyledPound = styled.span`
  font-size: ${({ theme: { type } }) => type.content.large};
  padding: ${({ theme: { spacing } }) => `${spacing.s3} ${spacing.s4}`};
  border-right: ${({ theme: { color } }) => `1px ${color.borderBlue} solid`};
`;

const StyledPoundError = styled(StyledPound)`
  border-color: ${({ theme: { color } }) => color.primary};
`;

const StyledInput = styled.input`
  color: ${({ theme: { color } }) => color.input};
  border: none;
  padding: ${({ theme: { spacing } }) =>
    `${spacing.s3} ${spacing.s2} ${spacing.s3} ${spacing.s4}`};
  width: 100%;
  background-color: transparent;
  &:disabled {
    border-bottom: 1px solid ${({ theme: { color } }) => color.borderBlue};
    color: ${({ theme: { color } }) => color.superLightGrey};
  }
`;

const StyledInputError = styled(StyledInput)`
  border-color: ${({ theme: { color } }) => color.primary};
`;

export default function MoneyInput({
  onChange,
  value,
  error,
  id,
  ...props
}: InputProps) {
  const moneyChange = useCallback(
    (value: string) => onChange(parseInt(value)),
    [onChange],
  );
  const inputProps = useUncontrolledInput(
    value ? value.toString() : '',
    moneyChange,
  );
  const Container = error ? StyledContainerError : StyledContainer;
  const Input = error ? StyledInputError : StyledInput;
  const Pound = error ? StyledPoundError : StyledPound;
  return (
    <Container>
      <Pound>£</Pound>
      <Input
        {...props}
        {...inputProps}
        id={id}
        data-testid={id}
        type="number"
        inputMode="numeric"
        pattern="[0-9]*"
        key={`money${value}`}
      />
    </Container>
  );
}
