import { State, StateSchema } from 'xstate';

export type StateSchemaOfPath<
  MatchesSchema extends StateSchema,
  Path extends keyof MatchesSchema['states']
> = MatchesSchema['states'][Path] extends StateSchema
  ? MatchesSchema['states'][Path]
  : any;

export function stateMatches<MatchesSchema extends StateSchema>(
  state: State<any, any, MatchesSchema>,
  path0: keyof MatchesSchema['states'],
): boolean;

export function stateMatches<
  MatchesSchema extends StateSchema,
  Path0 extends keyof MatchesSchema['states'],
  Path0Schema extends StateSchemaOfPath<MatchesSchema, Path0>,
  Path1 extends keyof Path0Schema['states']
>(state: State<any, any, MatchesSchema>, path0: Path0, path1: Path1): boolean;

export function stateMatches<
  MatchesSchema extends StateSchema,
  Path0 extends keyof MatchesSchema['states'],
  Path0Schema extends StateSchemaOfPath<MatchesSchema, Path0>,
  Path1 extends keyof Path0Schema['states'],
  Path1Schema extends StateSchemaOfPath<Path0Schema, Path1>,
  Path2 extends keyof Path1Schema['states']
>(
  state: State<any, any, MatchesSchema>,
  path0: Path0,
  path1: Path1,
  path2: Path2,
): boolean;

export function stateMatches<
  MatchesSchema extends StateSchema,
  Path0 extends keyof MatchesSchema['states'],
  Path0Schema extends StateSchemaOfPath<MatchesSchema, Path0>,
  Path1 extends keyof Path0Schema['states'],
  Path1Schema extends StateSchemaOfPath<Path0Schema, Path1>,
  Path2 extends keyof Path1Schema['states'],
  Path2Schema extends StateSchemaOfPath<Path1Schema, Path2>,
  Path3 extends keyof Path2Schema['states']
>(
  state: State<any, any, MatchesSchema>,
  path0: Path0,
  path1: Path1,
  path2: Path2,
  path3: Path3,
): boolean;

export function stateMatches<
  MatchesSchema extends StateSchema,
  Path0 extends keyof MatchesSchema['states'],
  Path0Schema extends StateSchemaOfPath<MatchesSchema, Path0>,
  Path1 extends keyof Path0Schema['states'],
  Path1Schema extends StateSchemaOfPath<Path0Schema, Path1>,
  Path2 extends keyof Path1Schema['states'],
  Path2Schema extends StateSchemaOfPath<Path1Schema, Path2>,
  Path3 extends keyof Path2Schema['states'],
  Path3Schema extends StateSchemaOfPath<Path2Schema, Path3>,
  Path4 extends keyof Path3Schema['states']
>(
  state: State<any, any, MatchesSchema>,
  path0: Path0,
  path1: Path1,
  path2: Path2,
  path3: Path3,
  path4: Path4,
): boolean;

export function stateMatches<
  MatchesSchema extends StateSchema,
  Path0 extends keyof MatchesSchema['states'],
  Path0Schema extends StateSchemaOfPath<MatchesSchema, Path0>,
  Path1 extends keyof Path0Schema['states'],
  Path1Schema extends StateSchemaOfPath<Path0Schema, Path1>,
  Path2 extends keyof Path1Schema['states'],
  Path2Schema extends StateSchemaOfPath<Path1Schema, Path2>,
  Path3 extends keyof Path2Schema['states'],
  Path3Schema extends StateSchemaOfPath<Path2Schema, Path3>,
  Path4 extends keyof Path3Schema['states'],
  Path4Schema extends StateSchemaOfPath<Path3Schema, Path4>,
  Path5 extends keyof Path4Schema['states']
>(
  state: State<any, any, MatchesSchema>,
  path0: Path0,
  path1: Path1,
  path2: Path2,
  path3: Path3,
  path4: Path4,
  path5: Path5,
): boolean;

export function stateMatches(...args: any) {
  const [state, ...paths] = args as [State<any, any, any>, ...string[]];
  return state.matches(paths.join('.'));
}
