/* eslint-disable camelcase */
import { AxiosResponse } from 'axios';
import { withRefresh } from 'services/auth';
import { client } from '../client';

type ResidentialTypes = 'RESIDENTIAL';
type AddressableTypes = 'CUSTOMER';

export interface SelectedAddress {
  id: string;
  addressLineOne: string;
  addressLineTwo: string;
  addressLineThree: string;
  city: string;
  postcode: string;
}

export type AddressResponse = {
  address_line_1: string;
  address_line_2: string | null;
  address_line_3: string | null;
  addressable_id: string;
  addressable_type: AddressableTypes;
  city: string | null;
  country_code: string;
  created_at: string;
  district_or_county: string;
  id: string;
  postal_code: string;
  state: string;
  state_or_region: string;
  type: ResidentialTypes | null;
  version: number;
};

export type PostcodeLookupAddresses = {
  address_line_1: string;
  address_line_2: string | null;
  address_line_3: string | null;
  city?: string | null;
  country_code: string;
  district_or_county: string;
  postal_code: string;
  state_or_region: string;
};

export interface SearchAddressResponse {
  addresses: PostcodeLookupAddresses[];
}

export const searchAddress = withRefresh(
  (postcode: string): Promise<AxiosResponse<SearchAddressResponse>> =>
    client.get(`/lookup-addresses?postal_code=${postcode}&source=external`),
);

export const createAddress = withRefresh(
  ({
    address,
    addressableType,
    addressableId,
    type,
    userId,
  }: {
    address: PostcodeLookupAddresses;
    addressableType: AddressResponse['addressable_type'];
    addressableId: string;
    type: ResidentialTypes;
    userId: string;
  }): Promise<AxiosResponse<AddressResponse>> =>
    client.post('addresses', {
      address,
      addressable_id: addressableId,
      addressable_type: addressableType?.toUpperCase(),
      type: type?.toUpperCase(),
      user_id: userId,
    }),
);

export const getAddress = withRefresh(
  ({
    userId,
    addressableType,
  }: {
    userId: string;
    addressableType: AddressResponse['addressable_type'];
  }): Promise<AxiosResponse<AddressResponse[]>> =>
    client.get(
      `addresses?addressable_type=${addressableType.toUpperCase()}&addressable_id=${userId}`,
    ),
);
