import React from 'react';
import { Errors, maidenNameIsSet } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import StandardInput from 'components/Form/StandardInput';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import Radios from 'components/Form/Radios';
import { findError } from 'components/ValidationErrors/findError';
import FormLink from 'components/Form/FormLink';

type Values = Pick<OnboardingContext['aboutYou'], 'otherNames'>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  aboutYou: Values;
};

export default function OtherNameFields({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'CLEAR_OTHER_NAMES',
  });
  const maidenNameSet =
    aboutYou.otherNames !== undefined && maidenNameIsSet(aboutYou.otherNames);
  return (
    <>
      <Label error={shownErrors['otherNames']} ref={refFor('otherNames')}>
        Have you been known by another name?
        <Radios
          name="otherNameLast"
          options={['Yes', 'No']}
          onChange={(value) =>
            send({
              type: value === 'Yes' ? 'ADD_OTHER_NAME' : 'CLEAR_OTHER_NAMES',
            })
          }
          disabled={disabled}
          value={
            aboutYou.otherNames === undefined
              ? undefined
              : aboutYou.otherNames.length === 0
              ? 'No'
              : 'Yes'
          }
          error={
            aboutYou.otherNames === undefined
              ? shownErrors['otherNames']
              : undefined
          }
        />
      </Label>
      {aboutYou.otherNames?.map((otherName, index) => (
        <React.Fragment key={`otherName${index}`}>
          {(!maidenNameSet || otherName.isMaidenName) && (
            <Label
              ref={
                otherName.isMaidenName === undefined
                  ? refFor('otherNames', index, 'name')
                  : undefined
              }>
              Is this your maiden name?
              <Radios
                name={`maidenName${index}`}
                options={['Yes', 'No']}
                onChange={(value) =>
                  send({
                    type: value === 'No' ? 'MAIDEN_NAME_NO' : 'MAIDEN_NAME_YES',
                    index,
                  })
                }
                disabled={disabled}
                value={
                  otherName.isMaidenName
                    ? 'Yes'
                    : otherName.isMaidenName === false
                    ? 'No'
                    : undefined
                }
              />
            </Label>
          )}
          {otherName.isMaidenName !== undefined && (
            <StandardInput
              id={`otherName${index}`}
              onChange={(value) =>
                send({ type: 'UPDATE_OTHER_NAME', index, value })
              }
              disabled={disabled}
              value={otherName.name}
              error={findError(shownErrors, 'otherNames', index, 'name')}
              ref={refFor('otherNames', index, 'name')}
              placeholder={
                otherName.isMaidenName
                  ? 'Enter your maiden name'
                  : 'Enter your other name'
              }>
              {canTransition({ type: 'REMOVE_OTHER_NAME', index }) && (
                <FormLink
                  id={`removeOtherName${index}`}
                  inline
                  onClick={() => send({ type: 'REMOVE_OTHER_NAME', index })}>
                  Remove
                </FormLink>
              )}
            </StandardInput>
          )}
        </React.Fragment>
      ))}
      {canTransition({ type: 'ADD_OTHER_NAME' }) &&
        !!aboutYou.otherNames &&
        aboutYou.otherNames.length !== 0 && (
          <FormLink
            id="addOtherName"
            onClick={() => send({ type: 'ADD_OTHER_NAME' })}>
            Add another name
          </FormLink>
        )}
    </>
  );
}
