import PrimaryButton from 'components/Button/PrimaryButton';
import { useStatechart } from 'state-machines/hooks';

export default function ModalClose({
  children = 'Close',
  statechart = 'onboarding',
}: {
  children?: string;
  statechart?: 'onboarding' | 'magicLink';
}) {
  const [, send, canTransition] = useStatechart(statechart);
  return (
    <PrimaryButton
      onClick={() => send({ type: 'CLOSE' })}
      status={!canTransition({ type: 'CLOSE' }) ? 'DISABLED' : 'ACTIVE'}
      autoFocus>
      {children}
    </PrimaryButton>
  );
}
