/* eslint-disable no-bitwise */

// Seeded Multiply-with-carry pseudorandom number generator
// https://en.wikipedia.org/wiki/Multiply-with-carry_pseudorandom_number_generator
// https://stackoverflow.com/a/19301306/5225716

const mask = 0xffffffff;
let w = 123456789;
let z = 987654321;

function seedRNG(i: number) {
  w = (123456789 + i) & mask;
  z = (987654321 - i) & mask;
}

function random() {
  z = (36969 * (z & 65535) + (z >> 16)) & mask;
  w = (18000 * (w & 65535) + (w >> 16)) & mask;
  return (((z << 16) + (w & 65535)) >>> 0) / 4294967296;
}

export default function randomBytes(seed: number, length: number) {
  seedRNG(seed);
  return Array.from({ length }).map((_) => Math.floor(random() * 256));
}
