import BackArrow from 'components/BackArrow/BackArrow';
import PrimaryButton from 'components/Button/PrimaryButton';
import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import { ReactComponent as Sent } from 'assets/sent.svg';
import Logo from 'components/Logo';
import BodyText from 'components/Text/BodyText';
import Heading from 'components/Text/Heading';
import { useMagicLink } from 'lib/useMagicLink';
import SecondaryButton from 'components/Button/SecondaryButton';
import styled from 'styled-components';
import Page from 'components/Page/Page';
import Centred from 'components/Text/Centred';

const Coloured = styled.span`
  color: ${({ theme: { color } }) => color.teal};
`;

export default function WeveSentAnEmail() {
  const [state, send, canTransition] = useMagicLink();
  return (
    <Page>
      <BackArrow send={send} canTransition={canTransition} />
      <Logo />
      <CenterBlockSVG>
        <Sent />
      </CenterBlockSVG>
      <Centred>
        <Heading>
          We've just sent an email to{' '}
          <Coloured>{state.context.emailAddress}</Coloured>
        </Heading>
        <BodyText>
          Register your phone by clicking on the link in the email
        </BodyText>
      </Centred>
      <PrimaryButton onClick={() => {}}>Open my email</PrimaryButton>
      <SecondaryButton
        onClick={() => send({ type: 'RESEND_EMAIL' })}
        status={
          !canTransition({ type: 'RESEND_EMAIL' }) ? 'DISABLED' : 'ACTIVE'
        }>
        Resend registration email
      </SecondaryButton>
    </Page>
  );
}
