import { Errors } from 'sanstatecharts-onboarding';
import Label from 'components/Form/Label';
import { OnboardingContext } from 'machines/context';
import { OnboardingEvent } from 'machines/events';
import { RefFor } from 'lib/useValidation';
import Selection from 'components/Form/Selection';

type Values = Pick<OnboardingContext['aboutYou'], 'gender'>;

type Props = {
  shownErrors: Errors<Values>;
  refFor: RefFor<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  aboutYou: Values;
};

export default function GenderField({
  shownErrors,
  aboutYou,
  send,
  canTransition,
  refFor,
}: Props) {
  const disabled = !canTransition({
    type: 'UPDATE_ABOUT_YOU',
    field: 'DOB',
    value: '2000-01-01',
  });
  return (
    <Label error={shownErrors['gender']} ref={refFor('gender')}>
      Gender
      <Selection
        name="gender"
        placeholder="Please select..."
        options={{ MALE: 'MALE', FEMALE: 'FEMALE' }}
        onChange={(value) =>
          send({
            type: 'UPDATE_ABOUT_YOU',
            field: 'gender',
            value:
              (value as OnboardingContext['aboutYou']['gender']) || undefined,
          })
        }
        disabled={disabled}
        value={aboutYou.gender}
        error={shownErrors['gender']}
      />
    </Label>
  );
}
