// eslint-disable-next-line import/prefer-default-export

import { AxiosResponse } from 'axios';
import { idandvClient } from 'services/client';
import { defaultEnvironment } from '../../environment';

export type AcceptedDocuments =
  | 'PASSPORT'
  | 'DRIVING_LICENSE'
  | 'PROVISIONAL_DRIVING_LICENSE';
export type GetAccessTokenResponse = {
  token_type: string;
  access_token: string;
  expires_in: number;
  consented_on: number;
  scope: string;
};

export type GetAccessToken = {
  grant_type: string;
  scope: string;
  client_id: string;
  client_secret: string;
};

export type GetSessionIdResponseConfiguration = {
  id: number;
  documentScan: boolean;
  impersonation: boolean;
  acceptedDocuments: string[];
};
export type Ddv = {
  id: number;
  docScanProvider: string;
  docScanStatus: string;
  images: string[];
  createdDate: string;
  modifiedDate: string;
};

export type Liveness = {
  id: number;
  numberOfAttempts: number;
  livenessProvider: string;
  livenessStatus: string;
  images: string[];
  createdDate: string;
  modifiedDate: string;
};
export type GetSessionIdResponse = {
  sessionStatus: string;
  configuration: GetSessionIdResponseConfiguration;
  ddv: Ddv;
  liveness: Liveness;
  images: string[];
  createdDate: string;
  modifiedDate: string;
  createdBy: string;
  consumingApp: string;
  modifiedBy: string;
  sessionId: string;
};

export type SessionConfiguration = {
  documentScan: boolean;
  impersonation: boolean;
  acceptedDocuments: string[];
};
export type GetSessionId = {
  sessionConfiguration: SessionConfiguration;
};

export const startIdAndV = (
  tokens: GetAccessTokenResponse,
): Promise<AxiosResponse<GetSessionIdResponse>> => {
  const options = {
    headers: {
      Authorization: `Bearer ${tokens.access_token}`,
      'x-ibm-client-id': defaultEnvironment.idandv.x_ibm_client_id,
    },
  };
  return idandvClient.post(
    '/session',
    {
      sessionConfiguration: {
        documentScan: true,
        impersonation: true,
        acceptedDocuments: [
          'PASSPORT',
          'DRIVING_LICENSE',
          'PROVISIONAL_DRIVING_LICENSE',
        ],
      },
    },
    options,
  );
};

export const getAccessToken = (): Promise<
  AxiosResponse<GetAccessTokenResponse>
> => {
  const options = {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
    },
  };
  const params: GetAccessToken = {
    grant_type: 'client_credentials',
    scope: 'xddv',
    client_id: defaultEnvironment.idandv.x_ibm_client_id,
    client_secret: defaultEnvironment.idandv.x_ibm_client_secret,
  };

  const data = Object.keys(params)
    .map(
      (key) =>
        `${key}=${encodeURIComponent(params[key as keyof typeof params])}`,
    )
    .join('&');

  return idandvClient.post('/oauth/oauth2/token', data, options);
};
