import PrimaryButton from 'components/Button/PrimaryButton';
import StandardInput from 'components/Form/StandardInput';
import Logo from 'components/Logo';
import Page from 'components/Page/Page';
import BodyText from 'components/Text/BodyText';
import Centred from 'components/Text/Centred';
import Heading from 'components/Text/Heading';
import React, { useEffect, useState } from 'react';

const password = process.env.REACT_APP_PASSWORD_PROTECTED || '';

export default function PasswordProtect({
  children,
}: {
  children: React.ReactNode;
}) {
  const [storagePassword, setStoragePassword] = useState('');
  const [localPassword, setLocalPassword] = useState(storagePassword);
  useEffect(() => {
    const fromStorage = localStorage.getItem('pwd');
    if (fromStorage) setStoragePassword(fromStorage);
  }, []);
  if (storagePassword !== password) {
    return (
      <Page>
        <Logo />
        <Centred>
          <Heading>Password protected</Heading>
          <StandardInput
            id="password"
            onChange={(p) => setLocalPassword(p)}
            value={storagePassword}
            placeholder="Enter access password"
          />
          <PrimaryButton
            onClick={() => {
              localStorage.setItem('pwd', localPassword);
              setStoragePassword(localPassword);
            }}>
            Enter
          </PrimaryButton>
          <BodyText>
            <br />
            No unauthorised access
          </BodyText>
        </Centred>
      </Page>
    );
  }
  return <>{children}</>;
}
