import React from 'react';
import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';

const Container = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
`;
const StyledLabel = styled.label`
  flex: 1;
  &:first-child span {
    margin-left: 0;
  }
`;
const StyledInput = styled.input`
  position: absolute;
  opacity: 0;
  &:focus + span {
    border-color: ${({ theme: { color } }) => color.content};
  }
  &:checked + span {
    box-shadow: none;
    background-color: ${({ theme: { color } }) => color.lightBlue};
    color: ${({ theme: { color } }) => color.white};
  }
`;
const StyledSpan = styled.span`
  color: ${({ theme: { color } }) => color.content};
  border-radius: ${({ theme: { spacing } }) => spacing.s2};
  border: ${({ theme: { color } }) => `1px ${color.lightBlue} solid`};
  display: block;
  background-color: ${({ theme: { color } }) => color.superLightBlue};
  border-color: ${({ theme: { color } }) => color.teal};
  text-align: center;
  cursor: pointer;
  padding: ${({ theme: { spacing } }) => spacing.s3};
  margin-left: ${({ theme: { spacing } }) => spacing.s3};
`;

const StyledSpanError = styled(StyledSpan)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
  color: ${({ theme: { color } }) => color.primary};
`;

export default function Radios({
  options,
  value: selected,
  onChange,
  disabled = false,
  name,
  error,
}: {
  options: string[] | Record<string, string>;
  value: string | undefined;
  onChange: (value: string) => void;
  disabled?: boolean;
  name: string;
  error?: ValidationError;
}) {
  const values = Array.isArray(options) ? options : Object.keys(options);
  const Span = error ? StyledSpanError : StyledSpan;
  return (
    <Container>
      {values.map((value) => (
        <StyledLabel key={`${name}${value}`}>
          <StyledInput
            key={value}
            data-testid={`${name}${value}`}
            type="radio"
            value="1"
            onChange={(event) => event.target.value && onChange(value)}
            checked={value === selected}
            disabled={disabled}></StyledInput>
          <Span>{Array.isArray(options) ? value : options[value]}</Span>
        </StyledLabel>
      ))}
    </Container>
  );
}
