export function assertIsDefined<I>(
  item: I | undefined,
  error: string,
): asserts item is I {
  if (typeof item === 'undefined') {
    throw new Error(error);
  }
}

export function assertIsDefinedInHook<T>(
  item: T | undefined,
  hook: string,
  container: string,
): asserts item is T {
  assertIsDefined(item, `${hook} can only be used within <${container} />`);
}
