import { useUncontrolledInput } from 'lib/useUncontrolledInput';
import React, { forwardRef } from 'react';
import { ValidationError } from 'sanstatecharts-onboarding';
import styled from 'styled-components';
import { InlineChild, InlineContainer } from './Inline';

type InputProps = Omit<
  React.InputHTMLAttributes<HTMLInputElement>,
  'onChange' | 'value' | 'id'
> & {
  id: string;
  onChange: (s: string) => void;
  error?: ValidationError;
  value: string | undefined;
  controlled?: boolean;
  centred?: boolean;
};

const StyledInput = styled.input`
  margin-bottom: ${({ theme: { spacing } }) => spacing.s4};
  border-radius: 4px 4px 0px 0px;
  border: 1px solid ${({ theme: { color } }) => color.borderBlue};
  border-bottom: 1px solid ${({ theme: { color } }) => color.lightBlue};
  color: ${({ theme: { color } }) => color.input};
  width: 100%;
  line-height: 1.5;
  &:disabled {
    background-color: transparent;
    border-bottom: 1px solid ${({ theme: { color } }) => color.borderBlue};
    color: ${({ theme: { color } }) => color.superLightGrey};
  }
  &:focus {
    background-color: ${({ theme: { color } }) => color.superLightBlue};
    box-shadow: ${({ theme: { color } }) =>
      `0 0 0 1px ${color.superLightBlue}`};
  }
`;

const StyledInputCentred = styled(StyledInput)`
  text-align: center;
`;
const StyledInputError = styled(StyledInput)`
  border-color: ${({ theme: { color } }) => color.primary};
  background-color: ${({ theme: { color } }) => color.superLightRed};
`;

const StyledInputErrorCentred = styled(StyledInputError)`
  text-align: center;
`;

function StandardInput(
  {
    onChange,
    children,
    error,
    value,
    id,
    centred = false,
    controlled = false,
    ...props
  }: InputProps,
  ref?: React.Ref<HTMLInputElement>,
) {
  const inputProps = useUncontrolledInput(value || '', onChange, controlled);
  const Input = error
    ? centred
      ? StyledInputErrorCentred
      : StyledInputError
    : centred
    ? StyledInputCentred
    : StyledInput;
  return (
    <InlineContainer>
      <Input {...props} data-testid={id} id={id} ref={ref} {...inputProps} />
      <InlineChild>{children}</InlineChild>
    </InlineContainer>
  );
}

export default forwardRef(StandardInput);
