import React, { createRef, MutableRefObject, useCallback, useRef } from 'react';
import { useState } from 'react';
import { Errors } from 'sanstatecharts-onboarding';

const key = <Values extends object>(
  field: keyof Values,
  index?: number,
  itemField?: string,
) => `${field}${index}${itemField}`;

export type RefFor<Values extends object> = (
  field: keyof Values,
  index?: number,
  itemField?: string,
) => React.RefObject<any>;

export type RefCreate<Values extends object> = (
  field: keyof Values,
  index: number,
) => (itemField?: string) => React.RefObject<any>;

export function useValidation<Values extends object>(
  validationErrors: Errors<Values>,
) {
  const refs = useRef<Record<string, MutableRefObject<HTMLElement | null>>>({});
  const [visible, setVisible] = useState(false);
  const setErrorsVisible = useCallback(() => setVisible(true), []);
  const onScrollToError = useCallback(
    (field: keyof Values, index?: number, itemField?: string) => {
      const ref = refs.current[key(field, index, itemField)];
      setVisible(true);
      ref?.current && ref.current.scrollIntoView({ behavior: 'smooth' });
    },
    [refs],
  );
  const refFor = useCallback(
    (field: keyof Values, index?: number, itemField?: string) => {
      const ref =
        refs.current[key(field, index, itemField)] || createRef<any>();
      refs.current[key(field, index, itemField)] = ref;
      return ref;
    },
    [],
  );
  const refCreate = useCallback((field: keyof Values, index: number) => {
    return (itemField?: string) => {
      const ref =
        refs.current[key(field, index, itemField)] || createRef<any>();
      refs.current[key(field, index, itemField)] = ref;
      return ref;
    };
  }, []);
  const shownErrors: typeof validationErrors = visible ? validationErrors : {};
  return {
    setErrorsVisible,
    shownErrors,
    refFor,
    refCreate,
    onScrollToError,
  };
}
