import { searchAddress, PostcodeLookupAddresses } from 'services/address';
import {
  MachineConfig,
  assign,
  send,
  DoneInvokeEvent,
  actions as xstateActions,
} from 'xstate';

export type AddressContext = {
  line: string;
  searchResults: Record<string, PostcodeLookupAddresses[]>;
};

export const defaultAddressContext: Readonly<AddressContext> = {
  line: '',
  searchResults: {},
};

export type AddressSchema = {
  context: AddressContext;
  states: {
    singleLine: {
      states: {
        pristine: {};
        searchAfterDelay: {};
        search: {};
        searchError: {};
        results: {};
      };
    };
    manualEntry: {
      states: {
        editing: {};
        fixed: {};
      };
    };
  };
};

export const addressServices = {
  search: async ({ line }: AddressContext) =>
    searchAddress(line.trim().toUpperCase()),
};

type UpdateLine = { type: 'UPDATE_LINE'; value: string };
type PerformSearch = { type: 'PERFORM_SEARCH' };
type SelectAddress = {
  type: 'SELECT_ADDRESS';
  address: PostcodeLookupAddresses;
};
type EnterManually = { type: 'ENTER_MANUALLY' };
type SetFixed = { type: 'SET_FIXED' };
type DismissResults = { type: 'DISMISS_RESULTS' };
type Reset = { type: 'RESET' };

type Depromisified<T> = T extends Promise<infer R> ? R : never;

type DoneInvokeSearch = {
  type: 'done.invoke.SEARCH';
  data: Depromisified<ReturnType<typeof addressServices.search>>;
};

export type AddressEvent =
  | UpdateLine
  | PerformSearch
  | SelectAddress
  | EnterManually
  | DismissResults
  | DoneInvokeSearch
  | Reset
  | SetFixed;

const DEBOUNCE = 450;

const actions = {
  assignLine: assign(
    (_context: AddressContext, { value: line }: UpdateLine) => ({
      line,
    }),
  ),
  searchAfterDelay: send<AddressContext, AddressEvent>(
    { type: 'PERFORM_SEARCH' },
    { delay: DEBOUNCE, id: 'searchAfterDelay' },
  ),
  cancelSearchAfterDelay: xstateActions.cancel('searchAfterDelay'),
  assignSearchResults: assign(
    (
      { line, searchResults }: AddressContext,
      { data }: DoneInvokeEvent<DoneInvokeSearch['data']>,
    ) => ({
      searchResults: {
        ...searchResults,
        [line.trim().toUpperCase()]: [...(data?.data?.addresses || [])],
      },
    }),
  ),
};

const guards = {
  lineIsShort: (_context: AddressContext, { value }: UpdateLine) =>
    value.trim().length <= 2,
  updatelineIsShort: ({ line }: AddressContext, _event: Reset) =>
    line.trim().length <= 2,
  hasNoSearchResults: (
    { searchResults }: AddressContext,
    { value }: UpdateLine,
  ) => !searchResults[value.trim().toUpperCase()],
  hasSearchResults: (
    { searchResults }: AddressContext,
    { value }: UpdateLine,
  ) => !!searchResults[value.trim().toUpperCase()],
  hasSearchResultsForLine: (
    { searchResults, line }: AddressContext,
    _event: Reset,
  ) => !!searchResults[line.trim().toUpperCase()],
  addressIsDefined: (_context: AddressContext, { address }: SelectAddress) =>
    !!address,
};

export const addressMachine: MachineConfig<
  AddressContext,
  AddressSchema,
  AddressEvent
> = {
  id: 'address',
  context: defaultAddressContext,
  initial: 'singleLine',
  states: {
    singleLine: {
      on: {
        UPDATE_LINE: [
          {
            target: '.pristine',
            cond: guards.lineIsShort,
            actions: actions.assignLine,
          },
          {
            target: '.searchAfterDelay',
            cond: guards.hasNoSearchResults,
            actions: actions.assignLine,
          },
          {
            target: '.results',
            cond: guards.hasSearchResults,
            actions: actions.assignLine,
          },
        ],
        ENTER_MANUALLY: 'manualEntry.editing',
        SET_FIXED: 'manualEntry.fixed',
      },
      initial: 'pristine',
      states: {
        pristine: {},
        searchAfterDelay: {
          entry: actions.searchAfterDelay,
          exit: actions.cancelSearchAfterDelay,
          on: { PERFORM_SEARCH: 'search' },
        },
        search: {
          invoke: {
            id: 'SEARCH',
            src: addressServices.search,
            onDone: {
              target: 'results',
              actions: actions.assignSearchResults,
            },
            onError: 'searchError',
          },
        },
        searchError: {},
        results: {
          on: {
            DISMISS_RESULTS: 'pristine',
            SELECT_ADDRESS: [
              {
                target: '#address.manualEntry.fixed',
                cond: guards.addressIsDefined,
              },
              { target: 'pristine' },
            ],
          },
        },
      },
    },
    manualEntry: {
      initial: 'editing',
      on: {
        RESET: [
          {
            target: '#address.singleLine.pristine',
            cond: guards.updatelineIsShort,
          },
          {
            target: '#address.singleLine.results',
            cond: guards.hasSearchResultsForLine,
          },
          {
            target: '#address.singleLine.search',
          },
        ],
      },
      states: {
        editing: { on: { SET_FIXED: 'fixed' } },
        fixed: { on: { ENTER_MANUALLY: 'editing' } },
      },
    },
  },
};
