import { object, string, number, array, mixed } from 'yup';
import { noErrors, validate } from 'statelib/validate';
import { Funds } from 'machines/funds';
import { PurposeOfAccount } from 'services/customer';
import { OnboardingContext } from 'machines/context';
import { alphanumericDashHyphenRegex } from './validationHelpers';

export const initialDepositValidationSchema = object({
  type: mixed()
    .defined()
    .test(
      'type',
      'initial deposit must be specified',
      (fundType: NonNullable<Funds['type']>) =>
        typeof fundType !== 'object' ||
        noErrors(string().required().min(3).max(100), fundType.otherName),
    )
    .label('initial deposit'),
  amount: number().defined().min(1).label('initial deposit amount'),
  origin: string()
    .trim()
    .required('initial deposit origin must be specified')
    .label('initial deposit origin'),
}).label('initial deposit');

export const purposesRequiringIncomingsOutgoings = [
  PurposeOfAccount.secondaryBankAccount,
  PurposeOfAccount.savings,
  PurposeOfAccount.tier1UkVisa,
  PurposeOfAccount.other,
];

export const accountValidation = validate(
  object({
    purposeOfNewAccount: string()
      .trim()
      .required('purpose of new account must be specified')
      .label('purpose of new account'),
    purposeOfNewAccountDetail: string().when('purposeOfNewAccount', {
      is: PurposeOfAccount.other,
      then: string()
        .trim()
        .required('purpose of account must be specified')
        .matches(
          alphanumericDashHyphenRegex,
          "purpose of new account can only contain the special characters ' and -",
        )
        .min(3)
        .max(100)
        .label('purpose of new account'),
    }),
    incoming: mixed().when('purposeOfNewAccount', {
      is: (purpose: OnboardingContext['account']['purposeOfNewAccount']) =>
        !!purpose && purposesRequiringIncomingsOutgoings.includes(purpose),
      then: number().defined().label('monthly amount paid in'),
      otherwise: number().nullable().label('monthly amount paid in'),
    }),
    outgoings: mixed().when('purposeOfNewAccount', {
      is: (purpose: OnboardingContext['account']['purposeOfNewAccount']) =>
        !!purpose && purposesRequiringIncomingsOutgoings.includes(purpose),
      then: number().defined().label('monthly outgoings'),
      otherwise: number().nullable().label('monthly outgoings'),
    }),
    largeCash: string()
      .defined()
      .notOneOf(['YES'], 'large cash deposit amount must be specified')
      .label('large cash deposits'),
    largeCashAmount: number()
      .when('largeCash', {
        is: 'TRANSACTION_2000_PLUS',
        then: number().required().min(2000),
        otherwise: number().nullable(),
      })
      .label('large cash deposit amount'),
    initialDeposits: array()
      .defined()
      .max(5)
      .of(initialDepositValidationSchema)
      .label('initial deposits'),
  }),
);
