import React, { forwardRef } from 'react';
import { ValidationError } from 'sanstatecharts-onboarding';
import { CountryInfo } from 'lib/countries';
import Selection from './Selection';

type InputProps = {
  onChange: (s: string) => void;
  value: string | undefined;
  name: string;
  placeholder?: string;
  disabled?: boolean;
  children?: React.ReactNode;
  field?: keyof CountryInfo;
  showFlags?: boolean;
  dataSource: CountryInfo[];
  error?: ValidationError;
  filterOut?: string[];
};

const countriesToOptions = (field: keyof CountryInfo, showFlags: boolean) => (
  a: Record<string, string>,
  info: CountryInfo,
) => ({
  ...a,
  [info.ISOcode]: showFlags ? `${info.flag} ${info[field]}` : `${info[field]}`,
});

const filterOutCountries = (
  filterOut: string[] | undefined,
  value: string | undefined,
) => (c: CountryInfo) =>
  !filterOut || c.ISOcode === value || !filterOut.includes(c.ISOcode);

function CountryInput(
  {
    onChange,
    value,
    name,
    placeholder = 'Please select...',
    field = 'countryName',
    disabled = false,
    showFlags = false,
    dataSource,
    error,
    children,
    filterOut,
  }: InputProps,
  ref?: React.Ref<HTMLSelectElement>,
) {
  return (
    <Selection
      ref={ref}
      name={name}
      placeholder={placeholder}
      options={dataSource
        .filter(filterOutCountries(filterOut, value))
        .reduce(countriesToOptions(field, showFlags), {})}
      onChange={onChange}
      disabled={disabled}
      error={error}
      value={value}>
      {children}
    </Selection>
  );
}

export default forwardRef(CountryInput);
