import React, { forwardRef } from 'react';
import styled from 'styled-components';
import { ValidationError } from 'sanstatecharts-onboarding';

const StyledLabel = styled.label`
  font-family: ${({ theme: { font } }) => font.light.fontFamily};
  font-size: ${({ theme: { type } }) => type.content.medium};
  > * {
    margin-top: ${({ theme: { spacing } }) => spacing.s3};
  }
`;

const StyledLabelError = styled(StyledLabel)`
  color: ${({ theme: { color } }) => color.primary};
`;

type Props = {
  children: React.ReactNode;
  error?: ValidationError;
};

function Label({ children, error }: Props, ref?: React.Ref<HTMLLabelElement>) {
  const Label = error ? StyledLabelError : StyledLabel;
  return <Label ref={ref}>{children}</Label>;
}

export default forwardRef(Label);
