import { stateMatches } from 'statelib/stateMatches';
import { useBCAOnboarding } from 'lib/useBCAOnboarding';
import Error from 'pages/Error';
import RetrievalError from 'pages/RetrievalError';
import StyledAppRouter from 'components/StyledAppRouter';
import ValidMagicLink from 'pages/ValidMagicLink';
import MagicLinkRouter from './MagicLinkRouter';
import RetrieveUser from 'pages/RetrieveUser';
import SelectBusinessType from 'pages/SelectBusinessType';
import AboutYouIntro from 'pages/AboutYouIntro';
import AboutYouPersonalInformation from 'pages/AboutYouPersonalInformation';
import AboutYouAdditionalInformation from 'pages/AboutYouAdditionalInformation';
import AboutYouContactInformation from 'pages/AboutYouContactInformation';
import AboutYouNationality from 'pages/AboutYouNationality';
import AboutYouTaxInformation from 'pages/AboutYouTaxInformation';
import AboutYouResidentialInformation from 'pages/AboutYouResidentialInformation';
import BusinessIntro from 'pages/BusinessIntro';
import BusinessInformation from 'pages/BusinessInformation';
import BusinessIndustry from 'pages/BusinessIndustry';
import BusinessDetails from 'pages/BusinessDetails';
import BusinessAddress from 'pages/BusinessAddress';
import BusinessPresence from 'pages/BusinessPresence';
import BusinessInternationalTrade from 'pages/BusinessInternationalTrade';
import BusinessInternationalTradeDetails from 'pages/BusinessInternationalTradeDetails';
import FinancesIntro from 'pages/FinancesIntro';
import FinancesTurnover from 'pages/FinancesTurnover';
import FinancesFirstDeposit from 'pages/FinancesFirstDeposit';
import FinancesFirstDepositCountry from 'pages/FinancesFirstDepositCountry';
import FinancesOngoingDeposits from 'pages/FinancesOngoingDeposits';
import FinancesOngoingDepositsCountry from 'pages/FinancesOngoingDepositsCountry';
import FinancesOutgoings from 'pages/FinancesOutgoings';
import AccountIntro from 'pages/AccountIntro';
import AccountOptions from 'pages/AccountOptions';
import AccountSwitcher from 'pages/AccountSwitcher';
import AccountSelection from 'pages/AccountSelection';
import AccountLegalBit from 'pages/AccountLegalBit';
import AccountMarketingPreferences from 'pages/AccountMarketingPreferences';
import { useMagicLinkIfExists } from 'lib/useMagicLink';
import Congratulations from 'pages/Congratulations';
import IdandV from 'pages/IdandV';
import IdentityFailure from 'pages/IdentityFailure';

export default function BCARouter() {
  const [state] = useBCAOnboarding();
  const [mlState] = useMagicLinkIfExists();
  const validating =
    !!mlState &&
    (stateMatches(mlState, 'validateMagicLink') ||
      stateMatches(mlState, 'userRetrieved'));
  return (
    <StyledAppRouter>
      {stateMatches(state, 'retrievalError') && <RetrievalError />}
      {stateMatches(state, 'magicLinkIsValid') && <ValidMagicLink />}
      {stateMatches(state, 'magicLink') && !validating && <MagicLinkRouter />}
      {(stateMatches(state, 'retrieveUser') || validating) && <RetrieveUser />}
      {stateMatches(state, 'selectBusinessType') && <SelectBusinessType />}
      {stateMatches(state, 'error') && <Error />}
      {stateMatches(state, 'steps', 'aboutYou', 'intro') && <AboutYouIntro />}
      {stateMatches(state, 'steps', 'aboutYou', 'personalInformation') && (
        <AboutYouPersonalInformation />
      )}
      {stateMatches(state, 'steps', 'aboutYou', 'additionalInformation') && (
        <AboutYouAdditionalInformation />
      )}
      {stateMatches(state, 'steps', 'aboutYou', 'contactInformation') && (
        <AboutYouContactInformation />
      )}
      {stateMatches(state, 'steps', 'aboutYou', 'nationality') && (
        <AboutYouNationality />
      )}
      {stateMatches(state, 'steps', 'aboutYou', 'taxInformation') && (
        <AboutYouTaxInformation />
      )}
      {stateMatches(state, 'steps', 'aboutYou', 'residentialInformation') && (
        <AboutYouResidentialInformation />
      )}
      {stateMatches(state, 'steps', 'business', 'intro') && <BusinessIntro />}
      {stateMatches(state, 'steps', 'business', 'information') && (
        <BusinessInformation />
      )}
      {stateMatches(state, 'steps', 'business', 'industry') && (
        <BusinessIndustry />
      )}
      {stateMatches(state, 'steps', 'business', 'details') && (
        <BusinessDetails />
      )}
      {stateMatches(state, 'steps', 'business', 'address') && (
        <BusinessAddress />
      )}
      {stateMatches(state, 'steps', 'business', 'presence') && (
        <BusinessPresence />
      )}
      {stateMatches(state, 'steps', 'business', 'internationalTrade') && (
        <BusinessInternationalTrade />
      )}
      {stateMatches(
        state,
        'steps',
        'business',
        'internationalTradeDetails',
      ) && <BusinessInternationalTradeDetails />}
      {stateMatches(state, 'steps', 'finances', 'intro') && <FinancesIntro />}
      {stateMatches(state, 'steps', 'finances', 'turnover') && (
        <FinancesTurnover />
      )}
      {stateMatches(state, 'steps', 'finances', 'firstDeposit') && (
        <FinancesFirstDeposit />
      )}
      {stateMatches(state, 'steps', 'finances', 'firstDepositCountry') && (
        <FinancesFirstDepositCountry />
      )}
      {stateMatches(state, 'steps', 'finances', 'ongoingDeposits') && (
        <FinancesOngoingDeposits />
      )}
      {stateMatches(state, 'steps', 'finances', 'ongoingDepositsCountry') && (
        <FinancesOngoingDepositsCountry />
      )}
      {stateMatches(state, 'steps', 'finances', 'outgoings') && (
        <FinancesOutgoings />
      )}
      {stateMatches(state, 'steps', 'account', 'intro') && <AccountIntro />}
      {stateMatches(state, 'steps', 'account', 'options') && <AccountOptions />}
      {stateMatches(state, 'steps', 'account', 'switcher') && (
        <AccountSwitcher />
      )}
      {stateMatches(state, 'steps', 'account', 'selection') && (
        <AccountSelection />
      )}
      {stateMatches(state, 'steps', 'account', 'legalBit') && (
        <AccountLegalBit />
      )}
      {stateMatches(state, 'steps', 'account', 'marketingPreferences') && (
        <AccountMarketingPreferences />
      )}
      {stateMatches(state, 'congratulations') && <Congratulations />}
      {stateMatches(state, 'idandV') && <IdandV />}
      {stateMatches(state, 'identityFailure') && <IdentityFailure />}
    </StyledAppRouter>
  );
}
