import { InitialDepositItem } from 'services/application';
import { IncomeDetails, IncomeDetailsSourceCodes } from 'services/customer';

export type Funds = {
  type:
    | undefined
    | 'EMPLOYMENT'
    | 'RETIREMENT'
    | 'PROPERTY'
    | 'INHERITENCE'
    | 'STOCKS_ETC'
    | 'SALE_DIVESTMENT_ASSETS'
    | 'LEGAL_SETTLEMENT'
    | 'VIRTUAL_CURRENCIES'
    | 'GIFT'
    | 'ANOTHER_PERSON_ENTITY'
    | 'SAVINGS'
    | 'STUDENT'
    | { otherName: string };
  amount: undefined | number;
  origin: string | undefined;
};

export const emptyFunds: Readonly<Funds> = {
  type: undefined,
  amount: undefined,
  origin: undefined,
};

const incomeDetailsSourceCodeToFundType: {
  [key in IncomeDetailsSourceCodes]: Funds['type'];
} = {
  [IncomeDetailsSourceCodes.employmentOrBenefits]: 'EMPLOYMENT',
  [IncomeDetailsSourceCodes.retirement]: 'RETIREMENT',
  [IncomeDetailsSourceCodes.property]: 'PROPERTY',
  [IncomeDetailsSourceCodes.inheritence]: 'INHERITENCE',
  [IncomeDetailsSourceCodes.stocksEtc]: 'STOCKS_ETC',
  [IncomeDetailsSourceCodes.saleDivestmentAssets]: 'SALE_DIVESTMENT_ASSETS',
  [IncomeDetailsSourceCodes.legalSettlement]: 'LEGAL_SETTLEMENT',
  [IncomeDetailsSourceCodes.virtualCurrencies]: 'VIRTUAL_CURRENCIES',
  [IncomeDetailsSourceCodes.gift]: 'GIFT',
  [IncomeDetailsSourceCodes.anotherPersonEntity]: 'ANOTHER_PERSON_ENTITY',
  [IncomeDetailsSourceCodes.savings]: 'SAVINGS',
  [IncomeDetailsSourceCodes.student]: 'STUDENT',
  [IncomeDetailsSourceCodes.other]: { otherName: '' },
};

const incomeDetailsSourceCodeToContext = (
  code: IncomeDetailsSourceCodes | undefined,
): Funds['type'] =>
  code === undefined
    ? undefined
    : incomeDetailsSourceCodeToFundType[code] || undefined;

const contextToIncomeDetailsSourceCode = (
  type: Funds['type'],
): IncomeDetailsSourceCodes | undefined => {
  if (type === undefined) return undefined;
  if (typeof type === 'object') return IncomeDetailsSourceCodes.other;
  return (
    (Object.keys(incomeDetailsSourceCodeToFundType).find(
      (key) =>
        incomeDetailsSourceCodeToFundType[
          key as keyof typeof incomeDetailsSourceCodeToFundType
        ] === type,
    ) as IncomeDetailsSourceCodes) || undefined
  );
};

export const incomeDetailsToFunds = (details: IncomeDetails): Funds => ({
  type: incomeDetailsSourceCodeToContext(details.source_code),
  amount: details.monthly_income,
  origin: details.country_code || undefined,
}); // @TODO - handle other name

export const fundsToIncomeDetails = (details: Funds): IncomeDetails => ({
  source_code: contextToIncomeDetailsSourceCode(details.type),
  monthly_income: details.amount,
  country_code: details.origin || undefined,
}); // @TODO - handle other name

export const initialDepositToFunds = (details: InitialDepositItem): Funds => ({
  type: incomeDetailsSourceCodeToContext(details.source || undefined),
  amount: details.amount || undefined,
  origin: details.origin || undefined,
}); // @TODO - handle other name

export const fundsToInitialDeposit = (details: Funds): InitialDepositItem => ({
  source: contextToIncomeDetailsSourceCode(details.type) || null,
  amount: details.amount || null,
  origin: details.origin || null,
}); // @TODO - handle other name
