import CenterBlockSVG from 'components/CenterBlockSVG/CenterBlockSVG';
import { useOnboarding } from 'lib/useOnboarding';
import PrimaryButton from 'components/Button/PrimaryButton';
import ErroredMobile from 'components/ErroredMobile/ErroredMobile';
import BodyText from 'components/Text/BodyText';
import Heading from 'components/Text/Heading';
import Page from 'components/Page/Page';
import Centred from 'components/Text/Centred';
import SecondaryButton from 'components/Button/SecondaryButton';

export default function RetrievalError() {
  const [, send, canTransition] = useOnboarding();
  return (
    <Page>
      <CenterBlockSVG>
        <ErroredMobile />
      </CenterBlockSVG>
      <Centred>
        <Heading>Oops!</Heading>
        <BodyText>Something went wrong while retrieving your details.</BodyText>
      </Centred>
      <PrimaryButton
        onClick={() => send({ type: 'RETRY' })}
        status={!canTransition({ type: 'RETRY' }) ? 'DISABLED' : 'ACTIVE'}>
        Retry
      </PrimaryButton>
      <SecondaryButton
        onClick={() => send({ type: 'RESTART' })}
        status={!canTransition({ type: 'RESTART' }) ? 'DISABLED' : 'ACTIVE'}>
        Resend registration email
      </SecondaryButton>
    </Page>
  );
}
