import React from 'react';
import {
  Errors,
  noErrors,
  PCAOnboardingContext,
  UserAddress,
  userAddressValidationSchema,
} from 'sanstatecharts-onboarding';
import { OnboardingEvent } from 'machines/events';
import { RefCreate } from 'lib/useValidation';
import FormLink from 'components/Form/FormLink';
import { Address } from 'sanstatecharts-onboarding';
import InfoBlockWithIcon from 'components/InfoBlock/InfoBlockWithIcon';
import { fieldError } from 'components/ValidationErrors/findError';
import Label from 'components/Form/Label';
import Selection from 'components/Form/Selection';
import AddressInput from 'components/Form/AddressInput';
import MonthInput from 'components/Form/MonthInput';
import { AccommodationCircumstance } from 'services/customer';

const addressName = (a: Address | string) =>
  typeof a === 'string' ? a : a.addressLineOne || a.postcode;

type Values = Pick<
  PCAOnboardingContext['contactDetails'],
  'addresses' | 'accommodationCircumstances'
>;

type Props = {
  shownErrors: Errors<Values>;
  refCreate: RefCreate<Values>;
  send: (e: OnboardingEvent) => void;
  canTransition: (e: OnboardingEvent) => boolean;
  contactDetails: Values;
};

const prevIsComplete = (
  _userAddress: UserAddress,
  index: number,
  userAddresses: UserAddress[],
) =>
  index === 0 ||
  noErrors(userAddressValidationSchema, userAddresses[index - 1]);

const accommodationCircumstancesOptions: {
  [key in AccommodationCircumstance]: string;
} = {
  LIVING_WITH_PARENTS: 'Living with parents',
  LIVING_WITH_FRIENDS: 'Living with friends/relatives',
  TENANT: 'Tenant',
  OUTRIGHT_OWNER: 'Outright owner',
  OWNER_WITH_MORTGAGE: 'Owner with mortgage',
  OWNER_WITH_SANTANDER_MORTGAGE: 'Owner with Santander mortgage',
};

export default function AddressFields({
  shownErrors,
  contactDetails,
  send,
  canTransition,
  refCreate,
}: Props) {
  return (
    <>
      {contactDetails.addresses.map(
        (userAddress, index) =>
          prevIsComplete(userAddress, index, contactDetails.addresses) && (
            <React.Fragment key={`address${index}`}>
              {index === 1 && (
                <InfoBlockWithIcon>
                  We'll need your address history for the last 3 years.
                </InfoBlockWithIcon>
              )}
              <AddressInput
                name={`address${index}`}
                title={
                  index === 0
                    ? 'Address'
                    : `Where did you live before ${addressName(
                        contactDetails.addresses[index - 1]!.selectedAddress,
                      )}?`
                }
                value={userAddress.selectedAddress}
                onChange={(selectedAddress) =>
                  send({
                    type: 'UPDATE_ADDRESS',
                    index,
                    value: { ...userAddress, selectedAddress },
                  })
                }
                error={(key?: keyof Address) =>
                  fieldError(
                    shownErrors,
                    'addresses',
                    index,
                  )(key || 'selectedAddress')
                }
                refFor={(key?: keyof Address) =>
                  refCreate('addresses', index)(key || 'selectedAddress')
                }>
                {canTransition({ type: 'REMOVE_ADDRESS', index }) && (
                  <FormLink
                    id={`removeAddress${index}`}
                    inline
                    onClick={() => send({ type: 'REMOVE_ADDRESS', index })}>
                    Remove
                  </FormLink>
                )}
              </AddressInput>
              {index === 0 && (
                <Label error={shownErrors['accommodationCircumstances']}>
                  Accommodation circumstances
                  <Selection
                    options={accommodationCircumstancesOptions}
                    placeholder="Please select..."
                    name={`address${index}accommodationCircumstances`}
                    onChange={(value) =>
                      send({
                        type: 'UPDATE_ACCOMMODATION_CIRCUMSTANCES',
                        value,
                      })
                    }
                    disabled={false}
                    value={contactDetails.accommodationCircumstances}
                    error={shownErrors['accommodationCircumstances']}
                  />
                </Label>
              )}
              {(index === 0 ||
                typeof userAddress.movingInDate === 'string') && (
                <Label
                  error={fieldError(
                    shownErrors,
                    'addresses',
                    index,
                  )('movingInDate')}
                  ref={refCreate('addresses', index)('movingInDate')}>
                  When did you move in?
                  <Selection
                    options={['Less than 3 years ago', 'More than 3 years ago']}
                    placeholder="Please select..."
                    name={`address${index}moveInSelection`}
                    onChange={(selection) =>
                      send({
                        type: 'UPDATE_ADDRESS',
                        index,
                        value: {
                          ...userAddress,
                          movingInDate:
                            selection === ''
                              ? undefined
                              : selection === 'More than 3 years ago'
                              ? 'moreThan3Years'
                              : { month: 0, year: 0 },
                        },
                      })
                    }
                    disabled={false}
                    value={
                      userAddress.movingInDate === 'moreThan3Years'
                        ? 'More than 3 years ago'
                        : typeof userAddress.movingInDate === 'object'
                        ? 'Less than 3 years ago'
                        : undefined
                    }
                    error={fieldError(
                      shownErrors,
                      'addresses',
                      index,
                    )('movingInDate')}
                  />
                </Label>
              )}
              {(typeof userAddress.movingInDate === 'object' ||
                (index !== 0 &&
                  typeof userAddress.movingInDate !== 'string')) && (
                <Label
                  error={fieldError(
                    shownErrors,
                    'addresses',
                    index,
                  )('movingInDate')}>
                  Tell us the date you moved in
                  <MonthInput
                    id={`address${index}movingInDate`}
                    value={
                      typeof userAddress.movingInDate === 'object'
                        ? userAddress.movingInDate
                        : { month: 0, year: 0 }
                    }
                    onChange={(movingInDate) =>
                      send({
                        type: 'UPDATE_ADDRESS',
                        index,
                        value: { ...userAddress, movingInDate },
                      })
                    }
                    error={fieldError(
                      shownErrors,
                      'addresses',
                      index,
                    )('movingInDate')}
                  />
                </Label>
              )}
            </React.Fragment>
          ),
      )}
    </>
  );
}
