import PrimaryButton from 'components/Button/PrimaryButton';
import SecondaryButton from 'components/Button/SecondaryButton';
import Modal from 'components/Modals/Modal';
import ModalContainerWhite from 'components/Modals/ModalContainerWhite';
import BodyText from 'components/Text/BodyText';
import Centred from 'components/Text/Centred';
import { useOnboarding } from 'lib/useOnboarding';

export default function PersonUpdateError({ enabled }: { enabled: boolean }) {
  const [, send, canTransition] = useOnboarding();
  return (
    <Modal enabled={enabled}>
      <ModalContainerWhite>
        <Centred>
          <BodyText>An error has occurred updating your details</BodyText>
        </Centred>
        <PrimaryButton
          status={!canTransition({ type: 'RETRY' }) ? 'DISABLED' : 'ACTIVE'}
          onClick={() => send({ type: 'RETRY' })}>
          Retry
        </PrimaryButton>
        <SecondaryButton
          status={!canTransition({ type: 'CLOSE' }) ? 'DISABLED' : 'ACTIVE'}
          onClick={() => send({ type: 'CLOSE' })}>
          Close
        </SecondaryButton>
      </ModalContainerWhite>
    </Modal>
  );
}
